import {Enum} from '../utils';

/**
 * Enum containing the possible status of a conference. Possible values:
 * - DESTROYED
 * - DISCONNECTED
 * - CONNECTING
 * - CONNECTED
 * - DISCONNECTING
 * @type {Enum}
 */
export const ConferenceStatus = new Enum([
	'DESTROYED',
	'DISCONNECTED',
	'CONNECTING',
	'CONNECTED',
	'DISCONNECTING',
]);
