import { JanusEvents } from '../Constants';
export class MediaAPI {
    constructor(client) {
        this.client = client;
    }
    publish(medium, sdp) {
        const payload = {
            medium: medium,
            jsep: {
                type: 'offer',
                sdp: sdp,
            },
        };
        if (this.parallelSipExtension) {
            payload.parallelSipExtension = this.parallelSipExtension;
        }
        return this.client.send('open', payload);
    }
    waitPublishComplete(medium) {
        const { emitter } = this.client;
        const { OPEN_COMPLETED } = JanusEvents;
        return new Promise((resolve) => {
            const onOpenCompleted = (stream) => {
                if (stream.medium === medium) {
                    emitter.off(OPEN_COMPLETED, onOpenCompleted);
                    resolve();
                }
            };
            emitter.on(OPEN_COMPLETED, onOpenCompleted);
        });
    }
    unpublish(medium) {
        return this.client.send('close', {
            medium: medium,
        });
    }
    subscribe(medium, feed) {
        const payload = {
            medium: medium,
            feed: feed,
        };
        return this.client.send('open', payload);
    }
    unsubscribe(medium, feed) {
        return this.client.send('close', {
            feed: feed,
            medium: medium,
        });
    }
    answer(medium, feed, sdp) {
        const payload = {
            feed: feed,
            medium: medium,
            jsep: {
                type: 'answer',
                sdp: sdp,
            },
        };
        return this.client.send('answer', payload);
    }
    /**
     * Send a tricke message to Janus wrapper
     * @param {string} medium media type
     * @param {RTCIceCandidate} candidate RTC ice candidate to send. null means 'no more candidates'
     * @param {string} feed subscribed username. Optional
     * @return {Promise} resolved on success
     */
    trickle(medium, candidate, feed) {
        const payload = {
            medium: medium,
        };
        if (candidate) {
            payload.candidate = {
                sdpMid: candidate.sdpMid,
                sdpMLineIndex: candidate.sdpMLineIndex,
                candidate: candidate.candidate,
            };
        }
        else {
            payload.candidate = {
                completed: true,
            };
        }
        if (feed) {
            payload.feed = feed;
        }
        return this.client.send('trickle', payload);
    }
    bitrate(bitrate) {
        return this.client.send('bitrate', { bitrate: bitrate });
    }
    setParallelSipExtension(extension) {
        this.parallelSipExtension = extension;
    }
}
