
import {ServerAPI} from './ServerAPI';

/**
 * Request invitation methods.
 */
export class InviteRequestAPI extends ServerAPI {

	/**
	 * Request an invitation to user with given username.
	 * @throws {ErrorResponse} when server returns an error.
	 * @param {string} id invitation request ID.
	 * @param {string} from the user name that requested the invitation.
	 * @param {string} username user name to request invitation.
	 * @param {object} mediaConstraints media constraints to request in the invitation, defaults to {audio: true, video: true}.
	 * @return {Promise} resolved with server response on success.
	 */
	requestInvitation(id, from, username, mediaConstraints = {audio: true, video: true}) {
		return this.request('invitationRequest', id, {
			from: from,
			to: username,
			mediatypes: mediaConstraints,
		}).then(response => response.payload);
	}

	/**
	 * Cancel a previous request.
	 * @throws {ErrorResponse} when server returns an error.
	 * @param {string} id received invitation request Id.
	 * @param {string} from user name that requested the invitation
	 */
	cancelInvitation(id, from) {
		this.cancel('invitationRequest', id, {from: from});
	}

	/**
	 * Accept an invitation request received from given username.
	 * @throws {ErrorResponse} when server returns an error.
	 * @param {string} id received invitation request Id.
	 * @param {string} from user name that requested the invitation
	 */
	accept(id, from) {
		return this.answerWaitingResponse('invitationRequest', id, {from});
	}

	/**
	 * Decline a previous invitation request sent by given username.
	 * @throws {ErrorResponse} when server retuns an error.
	 * @param {string} id received invitation request Id.
	 * @param {string} from user name that requested the invitation
	 * @param {number} [error=603] rejection code to send
	 */
	decline(id, from, error = 603) {
		this.answer('invitationRequest', id, {
			from: from,
			error: error,
		});
	}

}
