import { ConferenceMediaList } from '../conference-media/ConferenceMediaList';
import { Subscriber } from './Subscriber';
const _room = Symbol('room');
/**
 * Class that manage multiple remote media subscriptions.
 * The main responsability is create and subscribe required Subscriber.
 *
 * @extends ConferenceMediaList
 */
export class Subscribers extends ConferenceMediaList {
    /**
     * Creates a new Subscribers instance
     */
    constructor(room, deps) {
        super();
        /**
         * Janus room.
         */
        this[_room] = room;
        this.deps = deps;
    }
    unsubscribeAll() {
        return Promise.all(this.items.map(subscriber => this._removeItem(subscriber)));
    }
    /**
     * Get an existing Subscriber with provided feed and medium.
     * If Subscriber does not exist null is returned instead.
     * @param {string} feed subscribed user name.
     * @param {string} medium remote medium type.
     * @return {Subscriber} managed Subscriber.
     */
    get(feed, medium) {
        return this.items.find((item) => item.provides(feed, medium));
    }
    /**
     * Subscribe to given feed.
     * @throws {Error} if already subscribed or an error during subscription.
     * @param {string} feed subscribed user name.
     * @param {string} medium remote medium type.
     * @return {Promise} resolved on success.
     */
    subscribe(feed, medium) {
        if (this.get(feed, medium)) {
            return Promise.reject(new Error('already subscribed'));
        }
        return this._addItem(this.createSubscriber(feed, medium));
    }
    /**
     * Subscribe to multiple feeds
     * @throws {Error} if already subscribed or an error during subscription.
     * @param {Object[]} feeds
     * @param {string} feeds.feed subscribed user name.
     * @param {string} feeds.medium remote medium type.
     * @return {Promise} resolved on success.
     */
    subscribeMany(feeds) {
        const subscribe = feed => this.subscribe(feed.feed, feed.medium);
        return this.isSubscribedToSome(feeds)
            ? Promise.reject(new Error('already subscribed'))
            : Promise.all(feeds.map(subscribe));
    }
    isSubscribedToSome(feeds) {
        return (feeds.some(feed => !!this.get(feed.feed, feed.medium)));
    }
    createSubscriber(feed, medium) {
        const SubscriberIF = this.deps.Subscriber || Subscriber;
        return new SubscriberIF(feed, this[_room], medium, this.deps);
    }
    /**
     * Unsubscribe from given feed.
     * @throws {Error} if not subscribed or an error during unsubscription.
     * @param {string} feed subscribed user name.
     * @param {string} medium remote medium type.
     * @return {Promise} resolved on success.
     */
    unsubscribe(feed, medium) {
        const subscriber = this.get(feed, medium);
        if (!subscriber) {
            return Promise.reject(new Error('not subscribed'));
        }
        return this._removeItem(subscriber);
    }
    /**
     * @override
     */
    _addItem(subscriber) {
        super._addItem(subscriber);
        return Promise.resolve().then(() => {
            return subscriber.subscribe();
        }).catch((error) => {
            return this._removeItem(subscriber).then(() => {
                throw error;
            }).catch(() => {
                throw error;
            });
        });
    }
    /**
     * @override
     */
    _removeItem(subscriber) {
        return Promise.resolve().then(() => {
            return subscriber.unsubscribe();
        }).then(() => {
            super._removeItem(subscriber);
        }).catch((error) => {
            super._removeItem(subscriber);
            throw error;
        });
    }
}
