import nanoid from 'nanoid';

import {bindMethods} from '../../../utils';
import {Conference, UPDATED_CONFERENCE_EVENT, DESTROYED_CONFERENCE_EVENT} from './Conference';
import {EventEmitter2} from 'eventemitter2';
import {ErrorResponse} from './signaling/errors';
import {default as Logs} from '../../../Logs';

const log = Logs.instance.getLogger('SippoJS/Janus/Conferences');

/**
 * Incoming conference event.
 * Fired when an invitation to a conference is received.
 * @event INCOMING_CONFERENCE_EVENT
 * @type {Conference}
 */
export const INCOMING_CONFERENCE_EVENT = 'incomingConference';

/**
 * Invitation request event.
 * Fired when an invitation request to a conference is received.
 * @event INVITATION_REQUEST_EVENT
 * @type {object}
 * @property {string} id request identifier.
 * @property {string} user user that requested the invitation.
 * @property {boolean} video boolean value that indicates if remote user is sharing video.
 * @property {function} accept function that should be called when invitation is accepted.
 * @property {function} reject function that should be called when invitation is rejected.
 */
export const INVITATION_REQUEST_EVENT = 'invitationRequest';

/**
 * Invitation declined event.
 * Fired when an remote peer canceled an invitation request.
 * @event INVITATION_CANCEL_EVENT
 * @type {object}
 * @property {string} id request identifier.
 */
export const INVITATION_CANCEL_EVENT = 'cancelInvitationRequest';

const _conferences = Symbol('conferences');
const _pendingRequests = Symbol('pendingRequest');
const _receivedRequests = Symbol('receivedRequests');
const _session = Symbol('session');

/**
 * This class implements a Conferences module that provides access
 * to conference management methods.
 */
export class Conferences extends EventEmitter2 {
	/**
	 * Createsa a new Conferences module.
	 */
	constructor(dependencies = {}) {
		super();

		/**
		 * Server client
		 * @private
		 * @type {Session}
		 */
		this[_session] = null;

		/**
		 * Conference collection, indexed by id
		 * @private
		 * @type {object}
		 */
		this[_conferences] = {};

		/**
		 * Contains a list of pending invitations requests (still not accepted/rejected
		 * by remote peer) transaction identifiers. Used to cancel a previous invitation
		 * and determine if an user has been requested.
		 * @private
		 * @type {object}
		 */
		this[_pendingRequests] = {};

		/**
		 * Contains a list of received and pending invitations requests (still not
		 * accepted/rejected by us) transaction identifiers. Used to cancel a
		 * them when session is closed.
		 * @private
		 * @type {object}
		 */
		this[_receivedRequests] = {};

		bindMethods(this, [
			'onRoomUpdated',
			'onIncomingInvite',
			'onIncomingInviteRequest',
			'onInviteCancelRequest',
		]);

		this.dependencies = dependencies;
	}

	/**
	 * Initialize conferences module.
	 * Get any room where user participates from server and create Conference
	 * elements with them.
	 * It also listens for new rooms notified by server.
	 * @param {Session} session associated signaling session.
	 * @return {Promise} resolved on success
	 */
	init(session) {
		this[_session] = session;
		this[_session].signaling.addContentListener({request: 'room-update'}, this.onRoomUpdated);
		this[_session].signaling.addContentListener({request: 'invitation'}, this.onIncomingInvite);
		this[_session].signaling.addContentListener(
			{request: 'invitationRequest'},
			this.onIncomingInviteRequest,
		);
		this[_session].signaling.addContentListener(
			{cancel: 'invitationRequest'},
			this.onInviteCancelRequest,
		);
		return this[_session].signaling.rooms.getRooms().then((response) => {
			response.forEach((data) => {
				let conference = new Conference();
				conference._updateWithServerData(data);
				this._addConferenceToCollection(conference);
			});
		});
	}

	/**
	 * Deinitialize conferences module.
	 * Stops listening for new rooms messages from server.
	 * @return {void}
	 */
	uninit() {
		Object.values(this[_conferences]).forEach((conference) => {
			this._removeConferenceFromCollection(conference.id);
		});
		if (this[_session] && this[_session].signaling) {
			this[_session].signaling.removeContentListener(this.onRoomUpdated);
			this[_session].signaling.removeContentListener(this.onIncomingInvite);
			this[_session].signaling.removeContentListener(this.onIncomingInviteRequest);
			this[_session].signaling.removeContentListener(this.onInviteCancelRequest);
		}
		this[_session] = null;
	}

	/**
	 * Suspend conferences:
	 *  - cancel any pending invitation request sent.
	 *  - update internally any received invitation request to canceled.
	 *  - suspend any managed conference.
	 * @return {Promise} resolved on success.
	 */
	suspend() {
		return Promise.resolve().then(() => {
			Object.keys(this[_pendingRequests]).forEach(participant =>
				this.cancelInvitationRequest(participant, 603),
			);
			Object.keys(this[_receivedRequests]).forEach((id) => {
				this.onInviteCancelRequest({
					requestID: id,
				});
			});
			return Promise.all(
				Object.keys(this[_conferences]).map(id => this[_conferences][id].suspend()),
			);
		});
	}

	/**
	 * Resume conferences:
	 *  - resume any managed conference.
	 * @return {Promise} resolved on success.
	 */
	resume() {
		return Promise.all(Object.keys(this[_conferences]).map(id => this[_conferences][id].resume()));
	}

	/**
	 * Get any conference where user is included as participant.
	 * @return {Array<Conference>}
	 */
	fetch() {
		return Object.values(this[_conferences]);
	}

	/**
	 * Get an existing conference by conference ID.
	 * @param {string} conferenceId conference ID.
	 * @return {Conference}
	 */
	get(conferenceId) {
		return this[_conferences][conferenceId];
	}

	/**
	 * Creates a new conference with given identifier.
	 * @return {Promise<Conference>} created conference on success.
	 */
	create() {
		return this[_session].signaling.rooms.createRoom().then((response) => {
			let conference = new Conference();
			conference._updateWithServerData(response);
			this._addConferenceToCollection(conference);
			return conference;
		});
	}

	/**
	 * Request an invitation to given user.
	 * @param {string} participant requested user.
	 * @param {object} mediaConstraints media constraints to request in the invitation, defaults to {audio: true, video: true}.
	 * @return {Promise<Integer>} Returns a SIP code indicating the state of the call.
	 * that indicates if invitation has been accepted.
	 */
	requestInvitation(participant, mediaConstraints = {audio: true, video: true}) {
		if (this[_pendingRequests][participant]) {
			return Promise.reject(new Error(`participant ${participant} has a pending request`));
		}
		return new Promise((resolve, reject) => {
			let id = nanoid();
			this[_pendingRequests][participant] = {
				id: id,
				resolve: resolve,
				reject: reject,
			};
			let from = `${this[_session].username}/${this[_session].peerId}`;
			this[_session].signaling.inviteRequest
				.requestInvitation(id, from, participant, mediaConstraints)
				.then(() => {
					delete this[_pendingRequests][participant];
					resolve({code: 200});
				})
				.catch((error) => {
					delete this[_pendingRequests][participant];
					if (error instanceof ErrorResponse) {
						resolve({code: error.message});
					} else {
						reject(error);
					}
				});
		});
	}

	/**
	 * Cancel a pending requestInvitation.
	 * @throws {Error} if no pending request exists.
	 * @param {string} participant user name to decline.
	 * @param {string} [error=undefined] resolution error code.
	 */
	cancelInvitationRequest(participant, error) {
		let pending = this[_pendingRequests][participant];
		if (!pending) {
			throw new Error(`participant ${participant} has no pending invitation`);
		}
		delete this[_pendingRequests][participant];
		let from = `${this[_session].username}/${this[_session].peerId}`;
		this[_session].signaling.inviteRequest.cancelInvitation(pending.id, from);
		pending.resolve(error);
	}

	/**
	 * Retrieves current invitations
	 *
	 * @return {Array<Conference>} A resolved {Promise} with an array of {Conference}s if succeeds
	 */
	getCurrentInvitations() {
		let id = nanoid();
		let from = `${this[_session].username}/${this[_session].peerId}`;
		log.debug('getCurrentInvitations');
		return this[_session].signaling.invites.getCurrentInvitations(id, from).then((response) => {
			log.debug('getCurrentInvitations/response', response);
			if (response.payload && response.payload.currentInvitations) {
				let rooms = response.payload.currentInvitations.map((invite) => {
					if (invite.request === 'invitationRequest') {
						return this.createInviteRequestFromMessage(invite);
					} else {
						let conf = new Conference();
						log.debug('getCurrentInvitations/invite', invite);
						this.updateWithServerData(conf, invite);
						this._addConferenceToCollection(conf);
						log.debug('getCurrentInvitations/response/conf', conf);
						return conf;
					}
				});
				return rooms;
			} else {
				throw new Error('no valid response');
			}
		});
	}

	async getRoomByUri(uri) {
		const room = await this[_session].signaling.rooms.joinRoom(uri);
		const conference = new Conference();
		conference.session = this[_session];
		conference._updateWithServerData(room);
		this._addConferenceToCollection(conference);
		return conference;
	}

	/**
	 * Add a conference to collection.
	 * @private
	 * @param {Conference} conference conference to add.
	 */
	_addConferenceToCollection(conference) {
		log.info('added conference', conference);
		this[_conferences][conference.id] = conference;
		let onUpdatedConference = () => {
			log.debug('updated conference', conference);
		};
		let onDestroyedConference = () => {
			log.debug('destroyed conference', conference);
			conference.off(UPDATED_CONFERENCE_EVENT, onUpdatedConference);
			conference.off(DESTROYED_CONFERENCE_EVENT, onDestroyedConference);
			this._removeConferenceFromCollection(conference.id);
		};
		conference.session = this[_session];
		conference.on(UPDATED_CONFERENCE_EVENT, onUpdatedConference);
		conference.on(DESTROYED_CONFERENCE_EVENT, onDestroyedConference);
	}

	/**
	 * Removes a conference from collection
	 * @private
	 * @param {string} id conference identifier
	 */
	_removeConferenceFromCollection(id) {
		log.info('removed conference', id);
		if (this[_conferences][id]) {
			this[_conferences][id].session = null;
			delete this[_conferences][id];
		}
	}

	/**
	 * Event listener called when a room update message is received
	 * @private
	 * @param {object} data
	 */
	onRoomUpdated(data) {
		if (this[_conferences][data.payload.room]) {
			return;
		}
		let conference = new Conference();
		conference._updateWithServerData(data.payload);
		this._addConferenceToCollection(conference);
	}

	/**
	 * Event listener called when an invite is received
	 * @private
	 * @param {object} data
	 */
	onIncomingInvite(data) {
		const conference = this.handleIncomingInvite(data);
		this.emit(INCOMING_CONFERENCE_EVENT, conference, data.mediatypes);
	}

	updateWithServerData(conference, data) {
		conference._updateWithServerData(
			Object.assign({}, data.payload, {
				invite: {
					id: data.requestID,
					from: data.from,
					mediatypes: data.mediatypes,
				},
				parallelSipExtension: data.parallelSipExtension,
				left: false,
			}),
		);
	}

	handleIncomingInvite(data) {
		let conference = this.get(data.payload.room) || new Conference(this.dependencies);
		this.updateWithServerData(conference, data);
		if (!this.get(data.payload.room)) {
			this._addConferenceToCollection(conference);
		}
		return conference;
	}

	_buildInvitationRequestEvent(id, from, mediatypes) {
		return {
			id: id,
			user: from,
			video: mediatypes && mediatypes.video,
			isRequest: true,
			accept: () => {
				delete this[_receivedRequests][id];
				return this[_session].signaling.inviteRequest.accept(id, from);
			},
			reject: (code) => {
				delete this[_receivedRequests][id];
				this[_session].signaling.inviteRequest.decline(id, from, code);
			},
		};
	}

	/**
	 * Event listener called when an invite request is received.
	 * @private
	 * @param {object} data.
	 */

	createInviteRequestFromMessage(data) {
		let id = data.requestID;
		let from = data.from;
		this[_receivedRequests][id] = from;
		return this._buildInvitationRequestEvent(id, from, data.mediatypes);
	}

	onIncomingInviteRequest(data) {
		this.emit(INVITATION_REQUEST_EVENT, this.createInviteRequestFromMessage(data));
	}

	/**
	 * Event listener called when an invite is canceled.
	 * @private
	 * @param {object} data.
	 */
	onInviteCancelRequest(data) {
		delete this[_receivedRequests][data.requestID];
		this.emit(INVITATION_CANCEL_EVENT, {
			id: data.requestID,
		});
	}
}
