import { EventEmitter2 } from 'eventemitter2';
import * as log from 'js-logger';
import { HoldAPI } from './api/HoldAPI';
import { LoginAPI } from './api/LoginAPI';
import { MediaAPI } from './api/MediaAPI';
import { UserAPI } from './api/UserAPI';
import { ConferenceMediaList } from './conference-media';
import { Client as ClientIO } from './connection/Client';
import { ConnectionEvents, ParticipantEvents } from './Constants';
import { BaseJanusFeedEventHandler } from './janus-feeds/BaseJanusFeedEventHandler';
import { Publishers } from './publishers/Publishers';
import { Subscribers } from './subscribers/Subscribers';
import { bindMethods } from './utils';
const _ice = Symbol('ice');
const _loggedIn = Symbol('loggedIn');
const _media = Symbol('media');
const _publishers = Symbol('publishers');
const _room = Symbol('room');
const _server = Symbol('server');
const _subscribers = Symbol('subscribers');
const _token = Symbol('token');
const _username = Symbol('username');
/**
 * Janus room.
 *
 * @fires CONNECTED
 * @fires CONNECTION_CLOSED
 * @fires PARTICIPANT_ADDED
 * @fires PARTICIPANT_REMOVED
 *
 */
export class Room {
    /**
     * Creates a new Janus Wrapper client.
     * @param {Object} params
     * @param {string} params.server Wrapper URL.
     * @param {string} params.username user name to register.
     * @param {string} params.room room identifier to enter.
     * @param {string} params.token room's access token.
     * @param {Array<object>} params.ice ice server list.
     * @param {object} params.deps object with dependencies
     */
    constructor({ server, username, room, token, ice, deps = {} } = {}) {
        /**
         * Server address.
         * @private
         * @type {string}
         */
        this[_server] = server;
        /**
         * Registered username.
         * @private
         * @type {string}
         */
        this[_username] = username;
        /**
         * Room identifier.
         * @private
         * @type {string}
         */
        this[_room] = room;
        /**
         * Room's access token.
         * @private
         * @type {string}
         */
        this[_token] = token;
        /**
         * Conference ice servers
         * @private
         * @type {Array<object>}
         */
        this[_ice] = ice;
        /**
         * A login attempt succeded.
         * @private
         * @type {boolean}
         */
        this[_loggedIn] = false;
        const createClient = deps.Client || ClientIO;
        this.client = createClient(deps);
        /**
         * Server methods.
         * @private
         * @type {object}
         * @property {LoginAPI} session login/logout methods.
         * @property {MediaAPI} media media publish/subscription methods.
         * @property {UserAPI} user participants-related methods.
         * @property {HoldAPI} hold hold-related methods
         */
        this.api = {
            session: new LoginAPI(this.client),
            media: new MediaAPI(this.client),
            user: new UserAPI(this.client),
            hold: new HoldAPI(this.client),
        };
        /**
         * Local media publishers.
         * @private
         * @type {Publishers}
         */
        const createPublishers = () => {
            if (deps.Publishers) {
                return new (deps.Publishers)(this, deps);
            }
            else {
                return new Publishers(this, deps);
            }
        };
        this[_publishers] = createPublishers();
        /**
         * Remote media subscribers.
         * @private
         * @type {Subscribers}
         */
        const createSubscribers = () => {
            if (deps.Subscribers) {
                return new (deps.Subscribers)(this, deps);
            }
            else {
                return new Subscribers(this, deps);
            }
        };
        this[_subscribers] = createSubscribers(this, deps);
        const createConferenceMediaList = () => {
            if (deps.conferenceMediaList) {
                return deps.conferenceMediaList;
            }
            return new ConferenceMediaList();
        };
        this[_media] = createConferenceMediaList();
        this[_media]._addItem(this[_publishers]);
        this[_media]._addItem(this[_subscribers]);
        const createJanusFeedHandler = () => {
            if (deps.janusFeedHandler) {
                return deps.janusFeedHandler;
            }
            return BaseJanusFeedEventHandler(this[_subscribers]);
        };
        this.janusFeedHandler = createJanusFeedHandler();
        this.emitter = new EventEmitter2();
        bindMethods(this, [
            'onDisconnect',
            'onJanusFeed',
            'onJanusWebRTC',
            'onJanusDisconnected',
            'onJanusDestroyed',
            'onParticipantAdded',
            'onParticipantRemoved',
        ]);
    }
    getPublishers() {
        return this[_publishers];
    }
    getSubscribers() {
        return this[_subscribers];
    }
    /**
     * Configured ICE server list
     * @type {Array<object>}
     */
    get ice() {
        return this[_ice];
    }
    /**
     * Initialize Janus connection and login.
     * @throws {Error} when already connected or connection fails.
     * @return {Promise} resolved on success.
     */
    init() {
        return Promise.resolve().then(() => {
            return this.client.init(this[_server]);
        }).then(() => {
            this.onConnect();
            return this.api.session.login(this[_username], this[_room], this[_token]);
        }).then(() => {
            this[_loggedIn] = true;
            return this;
        }).catch((error) => {
            return this.uninit().then(() => {
                throw error;
            });
        });
    }
    /**
     * Logout, stop any publisher and close Janus connection.
     * @return {Promise} resolved on success
     */
    async uninit() {
        if (!this[_loggedIn]) {
            return;
        }
        this[_loggedIn] = false;
        this.client.emitter.removeAllListeners();
        await this[_publishers].unpublishAll();
        await this[_subscribers].unsubscribeAll();
        try {
            await this.api.session.logout();
        }
        catch (error) {
            log.error(`Wrapper stack unable to logout`);
        }
        await this.client.uninit();
        this.emitter.emit(ConnectionEvents.CONNECTION_CLOSED);
    }
    /**
     * Conference media
     * @type {ConferenceMediaList}
     */
    get media() {
        return this[_media];
    }
    /**
     * Current mediatypes.
     * @type {object}
     */
    get mediatypes() {
        return this[_publishers].mediatypes;
    }
    /**
     * Update local media types.
     * @param {object} mediatypes required mediatypes.
     * @return {Promise} resolved on success.
     */
    update(mediatypes = {}) {
        return this[_publishers].update(mediatypes);
    }
    /**
     * Retrieve the participants in the room
     */
    getParticipants() {
        log.debug('getParticipants');
        return this.api.user.getAll();
    }
    /**
     * Callback called when connection with Janus server is established.
     * @private
     */
    onConnect() {
        log.debug('connection created');
        this.client.emitter.on('disconnect', this.onDisconnect);
        this.client.emitter.on('feed', this.onJanusFeed);
        this.client.emitter.on('webrtc', this.onJanusWebRTC);
        this.client.emitter.on('destroyed', this.onJanusDestroyed);
        this.client.emitter.on('disconnected', this.onJanusDisconnected);
        this.client.emitter.on('participantAdded', this.onParticipantAdded);
        this.client.emitter.on('participantRemoved', this.onParticipantRemoved);
        this.emitter.emit(ConnectionEvents.CONNECTED);
    }
    /**
     * Callback called when connection with Janus server is closed.
     * @private
     */
    onDisconnect() {
        log.debug('connection closed');
        if (this.client.isConnected()) {
            this.client.emitter.removeAllListeners();
            return this.client.uninit();
        }
    }
    onJanusFeed(feed) {
        this.janusFeedHandler.onJanusFeed(this, feed);
    }
    /**
     * Wrapper notifies this event when a PeerConnection from this user
     * just became active or was just closed from the Janus perspective.
     * @private
     * @param {object} data event payload.
     */
    onJanusWebRTC(data) {
        log.info('janus webrtc >> ', data);
    }
    /**
     * Wrapper notifies this event when the room has been destroyed.
     * @private
     * @param {object} data event payload.
     */
    onJanusDestroyed(data) {
        log.info('janus destroyed >> ', data);
        this.uninit();
    }
    /**
     * Wrapper notifies this event when the janus instane was lost.
     * @private
     * @param {object} data event payload.
     */
    onJanusDisconnected(data) {
        log.info('janus disconnected >> ', data);
        this.uninit();
    }
    onParticipantAdded(data) {
        log.info('janus participant added >> ', data);
        this.emitter.emit(ParticipantEvents.PARTICIPANT_ADDED, data);
    }
    onParticipantRemoved(data) {
        log.info('janus participant removed >> ', data);
        this.emitter.emit(ParticipantEvents.PARTICIPANT_REMOVED, data.user);
    }
}
