import { defer } from 'rxjs';
import { filter, map, take } from 'rxjs/operators';
import { WebSocketConnection } from './WebSocketConnection';
import { asLegacyEvent, asLegacyResponse, isValidEvent, isValidResponse, } from './WebSocketWrapperApi';
export class WebSocketClient {
    constructor(connection) {
        this.connection = connection;
    }
    static async connect(server, dependencies = {}) {
        const createWebSocket = (address) => new (dependencies.webSocketClient || WebSocket)(address, WebSocketConnection.PROTOCOL);
        const connection = await WebSocketConnection.connect(server.getAddress(), createWebSocket);
        return new WebSocketClient(connection);
    }
    request(request) {
        return defer(() => {
            const response = this.connection
                .getMessages()
                .pipe(filter(isValidResponse(request)), map(asLegacyResponse), take(1));
            this.connection.send({ type: 'request', payload: request });
            return response;
        });
    }
    getEvents() {
        return this.connection.getMessages().pipe(filter(isValidEvent), map(asLegacyEvent));
    }
    disconnect() {
        this.connection.disconnect();
    }
}
