import * as log from 'js-logger';
import { pick, size } from 'lodash-es';
import { ConferenceMediaFeed } from '../conference-media/ConferenceMediaFeed';
import { RTCPeerConnectionAdapter } from '../RTCPeerConnectionAdapter';
import { checkRTCDependency } from '../utils';
const _room = Symbol('room');
const _constraints = Symbol('constraints');
const _pc = Symbol('pc');
/**
 * Media publisher.
 * Used to manage local media sent to Janus server.
 * Due 3rd party restrictions only one constraint is supported
 * for a Publisher.
 *
 * @extends ConferenceMediaFeed
 */
export class Publisher extends ConferenceMediaFeed {
    /**
     * Create a new Publisher.
     * Constraints format: constraints parameter is an object with next format
     *  - audio: {Boolean} or {MediaTrackConstraints} defines if audio track should be enabled
     *  - video: {Boolean} or {MediaTrackConstraints} defines if camera track should be enabled
     *  - screen: {Boolean} or {MediaTrackConstraints} defines if screen track should be enabled
     * @protected
     * @param {string} feed associated user name.
     * @param {object} constraints local constraints.
     */
    constructor(feed, room, constraints = {}, deps) {
        super(feed);
        constraints = pick(constraints, 'audio', 'video', 'screen');
        if (size(constraints) > 1) {
            throw new Error('only one constraint (audio,video,screen) is supported');
        }
        /**
         * Janus wrapper room
         * @private
         */
        this[_room] = room;
        /**
         * Published media types
         * @private
         * @type {object}
         */
        this[_constraints] = constraints;
        /**
         * Peer connection
         * @private
         * @type {RTCPeerConnection}
         */
        this[_pc] = null;
        checkRTCDependency(deps, 'RTCPeerConnection');
        checkRTCDependency(deps, 'RTCSessionDescription');
        checkRTCDependency(deps, 'getUserMedia');
        this.deps = deps;
    }
    /**
     * Get configured constraints
     * @type {object}
     */
    get constraints() {
        return this[_constraints];
    }
    /**
     * Indicate if publisher enables audio sharing
     * @private
     * @type {boolean}
     */
    get hasAudio() {
        return !!this[_constraints].audio;
    }
    /**
     * Indicate if publisher enables video sharing
     * @private
     * @type {boolean}
     */
    get hasVideo() {
        return !!this[_constraints].video || !!this[_constraints].screen;
    }
    /**
     * Medium used by Publisher
     * @private
     * @type {string}
     */
    get medium() {
        return this.hasVideo ? 'sfuvideo' : 'sipcall';
    }
    /**
     * Indicate if {Publisher} has been published.
     * @type {boolean}
     */
    get published() {
        return !!this[_pc] && this[_pc].signalingState === 'connected';
    }
    /**
     * Update publisher bitrate.
     * @param {number} bitrate in kbps.
     * @return {Promise} resolved on success.
     */
    setBitrate(bitrate) {
        return this[_room].api.media.bitrate(bitrate);
    }
    /**
     * Mute published audio (local or remote).
     * @param {boolean} muted should mute or unmute audio.
     */
    muteAudio(muted) {
        if (!this.hasAudio) {
            return;
        }
        if (this.localStream && this.localStream.stream) {
            this.localStream.stream.getAudioTracks().forEach(track => track.enabled = !muted);
        }
        if (this.remoteStream && this.remoteStream.stream) {
            this.remoteStream.stream.getAudioTracks().forEach(track => track.enabled = !muted);
        }
    }
    /**
     * Start publishing.
     * This method requires configured media, creates RTCPeerConnection and interchanges
     * messages with Janus wrapper server to establish multimedia conectivity.
     * @return {Promise} resolved on success
     */
    publish() {
        const constraints = this[_constraints];
        if (!size(constraints)) {
            return Promise.resolve();
        }
        return this.deps.getUserMedia(constraints).then((stream) => {
            this.setLocalStream(stream, this.medium);
            const track = stream.getTracks()[0];
            const pcConfig = {
                iceServers: this[_room].ice,
            };
            const pcConstraints = {
                'optional': [
                    { 'DtlsSrtpKeyAgreement': true },
                ],
            };
            this[_pc] = new RTCPeerConnectionAdapter(pcConfig, pcConstraints, this.deps.RTCPeerConnection, this.deps);
            this[_pc].onicecandidate = (evt) => {
                this[_room].api.media.trickle(this.medium, evt.candidate);
            };
            this[_pc].remoteStreamHandlers((remoteStream) => this.setRemoteStream(remoteStream, this.medium));
            this[_pc].onconnectionstatechange = () => {
                if (this[_pc] && this[_pc].connectionState === 'closed') {
                    this[_pc] = null;
                }
            };
            this[_pc].addStreamToPeerConnection(stream, track);
            return Promise.all([
                this[_room].api.media.waitPublishComplete(this.medium),
                this.createWebRTCConnection(),
            ]);
        }).catch((error) => {
            log.error(error);
            this.unpublish();
            throw error;
        });
    }
    async createWebRTCConnection() {
        const { media: mediaApi } = this[_room].api;
        const offer = await this[_pc].createOffer({
            offerToReceiveAudio: this.hasAudio,
            offerToReceiveVideo: this.hasVideo,
        });
        await this[_pc].setLocalDescription(offer);
        const answer = await mediaApi.publish(this.medium, offer.sdp);
        const remoteSdp = new this.deps.RTCSessionDescription(answer.jsep);
        return this[_pc].setRemoteDescription(remoteSdp);
    }
    /**
     * Stop publishing
     * @return {Promise} resolved on success
     */
    unpublish() {
        if (this.localStream) {
            this.localStream.stream.getTracks().forEach(function (track) {
                track.stop();
            });
            this.setLocalStream(null, this.medium);
        }
        if (this[_pc]) {
            this[_pc].close();
        }
        return this[_room].api.media.unpublish(this.medium);
    }
}
