export class User {

	constructor(wacRequest) {
		this.wacRequest = wacRequest;
		this.pathname = '/users';

		this.updateUser = this.updateUser.bind(this);
		this.validatePhone = this.validatePhone.bind(this);
		this.getUser = this.getUser.bind(this);
	}

	updateUser(id, objToUpdate) {
		const fullPath = `${this.pathname}/${id}`;
		return this.wacRequest.put(fullPath, {}, objToUpdate);
	}

	getUser(id) {
		const fullPath = `${this.pathname}/${id}`;
		return this.wacRequest.get(fullPath);
	}

	validatePhone(id, code, phone) {
		const fullPath = `${this.pathname}/${id}`;
		const body = {
			code,
			phone,
		};
		return this.wacRequest.post(fullPath, {}, body);
	}
}
