import { ServerAddress } from './ServerAddress';
import { SocketIoClient } from './SocketIoClient';
import { WebSocketClient } from './WebSocketClient';
export class ClientFactory {
    constructor(dependencies) {
        this.dependencies = dependencies;
    }
    static from(dependencies) {
        return new ClientFactory(dependencies);
    }
    async createClient(serverAddress) {
        const parsedAddress = ServerAddress.fromUnknownAddress(serverAddress);
        if (parsedAddress.getTransport() === ServerAddress.Transport.WEBSOCKET) {
            return WebSocketClient.connect(parsedAddress, this.dependencies);
        }
        return SocketIoClient.connect(parsedAddress, this.dependencies);
    }
}
