import {EventEmitter2} from 'eventemitter2';
import {createClient, JID, Utils} from 'stanza';
import Semaphore from 'semaphore-async-await';
import {bindMethods} from '../../utils';
import Logs from '../../Logs';
const log = Logs.instance.getLogger('SippoJS/WacStack/XmppClient');

export class XmppClient {
	constructor(chatUri, token, jid) {
		this.lock = new Semaphore(1);
		this.throttleLock = new Semaphore(100);
		this.mucJoinPromise = {};
		this.messagePromise = {};
		this.options = {
			jid,
			password: token,
			timeout: 50,
			transports: {
				websocket: chatUri,
			},
		};
		this.emitter = new EventEmitter2();
		bindMethods(this, [
			'onEvent',
			'onStanzaAcked',
			'onMucError',
			'onMucJoin',
			'onDisconnected',
		]);
	}

	async start() {
		if (this.started) {
			throw Error('already started');
		}
		this.started = true;
		await this.lock.acquire();
		this.client = createClient(this.options);
		const cachedSM = sessionStorage.getItem('cachedSM');
		if (cachedSM) {
			this.client.sm.load(JSON.parse(cachedSM, Utils.reviveData));
		}
		try {
			await this.connectClient();
		} catch (error) {
			if (error.message === 'SESSION_ENDED') {
				sessionStorage.removeItem('cachedSM');
				await this.disconnectClient();
				this.client = createClient(this.options);
				await this.connectClient();
			} else {
				throw error;
			}
		}
		this.bindEventListeners();
		this.client.sm.cache((state) => {
			sessionStorage.setItem('cachedSM', JSON.stringify(state));
		});
		await this.client.getRoster();
		this.client.updateCaps();
		this.client.sendPresence({caps: this.client.disco.caps});
		this.client.enableKeepAlive({interval: 20, timeout: 10});
		await this.client.enableCarbons();
		this.lock.release();
	}

	async stop() {
		if (!this.started) {
			throw Error('not started');
		}
		this.started = false;
		await this.lock.acquire();
		this.unbindEventListeners();
		sessionStorage.removeItem('cachedSM');
		await this.disconnectClient();
		delete this.client;
		this.lock.release();
	}

	async suspend() {
		this.started = false;
		await this.lock.acquire();
		this.unbindEventListeners();
		new Promise((resolve, reject) => {
			this.client.transport.conn.addEventListener('close', resolve);
			this.client.transport.conn.addEventListener('error', reject);
			this.client.transport.conn.close();
		}).catch(log.error); // Don't await here to avoid unnecessary delays if socket is not instantly closed
		delete this.client;
		this.lock.release();
	}

	async resume() {
		await this.start();
	}

	async connectClient() {
		return new Promise((resolve, reject) => {
			let onSuccess, onError, onSessionEnd;
			const unbind = () => {
				this.client.off('session:started', onSuccess);
				this.client.off('session:end', onSessionEnd);
				this.client.off('stream:management:resumed', onSuccess);
				this.client.off('auth:failed', onError);
			};
			onSuccess = () => {
				unbind();
				resolve();
			};
			onError = () => {
				unbind();
				reject(Error('XMPP authentication error'));
			};
			onSessionEnd = () => {
				unbind();
				reject(Error('SESSION_ENDED'));
			};
			this.client.on('auth:failed', onError);
			this.client.on('session:end', onSessionEnd);
			this.client.on('session:started', onSuccess);
			this.client.on('stream:management:resumed', onSuccess);
			this.client.connect();
		});
	}

	disconnectClient() {
		return new Promise((resolve) => {
			this.client.once('disconnected', resolve);
			this.client.disconnect();
		});
	}

	onDisconnected() {
		if (!this.started || this.reconnectingInterval) {
			return;
		}
		this.client.once('connected', () => {
			clearInterval(this.reconnectingInterval);
			this.reconnectingInterval = null;
		});
		this.reconnectingInterval = setInterval(() => {
			this.client.connect();
		}, 10000);
	}

	bindEventListeners() {
		this.client.on('*', this.onEvent);
		this.client.on('muc:join', this.onMucJoin);
		this.client.on('muc:error', this.onMucError);
		this.client.on('stanza:acked', this.onStanzaAcked);
		this.client.on('disconnected', this.onDisconnected);
	}

	unbindEventListeners() {
		this.client.off('*', this.onEvent);
		this.client.off('muc:join', this.onMucJoin);
		this.client.off('muc:error', this.onMucError);
		this.client.off('stanza:acked', this.onStanzaAcked);
		this.client.off('disconnected', this.onDisconnected);
	}

	sendMessage(data) {
		const id = this.client.sendMessage(data);
		return new Promise((resolve, reject) => {
			this.messagePromise[id] = {resolve, reject};
		});
	}

	markReceived(to, id, type) {
		const msg = {from: to, type, id, marker: {type: 'markable'}};
		return this.client.markReceived(msg);
	}

	async markDisplayed(to, id, type) {
		const msg = {from: to, type, id, marker: {type: 'markable'}};
		await this.client.markDisplayed(msg);
	}

	async searchHistory(data) {
		await this.throttleLock.acquire();
		const res = await this.client.searchHistory(data);
		this.throttleLock.release();
		return res;
	}

	getBookmarks() {
		return this.client.getBookmarks();
	}

	async addBookmark(data) {
		await this.lock.acquire();
		try {
			await this.client.addBookmark(data);
		} finally {
			this.lock.release();
		}
	}

	async removeBookmark(jid) {
		await this.lock.acquire();
		try {
			await this.client.removeBookmark(jid);
		} finally {
			this.lock.release();
		}
	}

	async getDiscoInfo(jid) {
		await this.throttleLock.acquire();
		const res = await this.client.getDiscoInfo(jid);
		this.throttleLock.release();
		return res;
	}

	getRoomConfig(jid) {
		return this.client.getRoomConfig(jid);
	}

	configureRoom(room, form) {
		return this.client.configureRoom(room, form);
	}

	destroyRoom(room, options) {
		return this.client.destroyRoom(room, options);
	}

	async getRoomMembers(jid, affiliation) {
		await this.throttleLock.acquire();
		const res = await this.client.getRoomMembers(jid, {affiliation});
		this.throttleLock.release();
		return res;
	}

	async joinRoom(jid, nick, options) {
		await this.throttleLock.acquire();
		this.client.joinRoom(jid, nick, options);
		const res = await new Promise((resolve, reject) => {
			this.mucJoinPromise[jid] = {resolve, reject};
		});
		this.throttleLock.release();
		return res;
	}

	invite(room, options) {
		return this.client.invite(room, options);
	}

	setRoomAffiliation(room, jid, affiliation, reason) {
		return this.client.setRoomAffiliation(room, jid, affiliation, reason);
	}

	setSubject(room, subject) {
		return this.client.setSubject(room, subject);
	}

	async deleteAccount(jid) {
		try {
			await this.client.deleteAccount(jid);
		} catch (error) {
			if (error.type !== 'error') {
				throw error;
			}
			if (error.error.condition !== 'gone') {
				throw Error(error.error.text);
			}
		}
	}

	enableNotifications(jid, node, fieldList) {
		return this.client.enableNotifications(jid, node, fieldList);
	}

	disableNotifications(jid, node) {
		return this.client.disableNotifications(jid, node);
	}

	onEvent(eventName, eventData) {
		log.debug(eventName, eventData);
		this.emitter.emit(eventName, eventData);
	}

	onStanzaAcked(event) {
		if (event.kind === 'message') {
			const obj = this.messagePromise[event.stanza.id];
			if (obj) {
				obj.resolve(event.stanza.id);
				delete this.messagePromise[event.stanza.id];
			}
		}
	}

	onMucError(event) {
		const id = JID.toBare(event.from);
		if (this.mucJoinPromise[id]) {
			this.mucJoinPromise[id].reject(new Error(event.error.text));
			delete this.mucJoinPromise[id];
		}
	}

	onMucJoin(event) {
		const id = JID.toBare(event.from);
		if (this.mucJoinPromise[id]) {
			this.mucJoinPromise[id].resolve();
			delete this.mucJoinPromise[id];
		}
	}
}
