import EventEmitter from 'eventemitter3';

import {Stack} from './stacks/Stack';
import {WacStack} from './wac-stack/WacStack';
import {CapabilitiesManager} from './CapabilitiesManager';
import Logs from '../Logs';
import {StackRegisterError} from './errors';
import {bindMethods} from '../utils/bindMethods';


const log = Logs.instance.getLogger('SippoJS/WacProxy');
const isPromise = p => p && typeof p.then === 'function';

const ROUTES = [
	{method: 'getSessions', capability: 'sessions'},
	{method: 'getCurrentSession', capability: 'sessions'},
	{method: 'retainSession', capability: 'sessions'},
	{method: 'getCredentials', capability: 'credentials'},
	{method: 'getCurrentCredential', capability: 'credentials'},
	{method: 'retainCredential', capability: 'credentials'},
	{method: 'suspendSession', capability: null},
	{method: 'resumeSession', capability: null},
	{method: 'call', capability: null},
	{method: 'answer', capability: null},
	{method: 'hangup', capability: null},
	{method: 'hold', capability: 'hold'},
	{method: 'resume', capability: 'hold'},
	{method: 'transfer', capability: 'blind-transfer'},
	{method: 'move', capability: 'move'},
	{method: 'enableScreenSharing', capability: 'screen-sharing'},
	{method: 'getCurrentMediaTypes', capability: 'media-update'},
	{method: 'updateCallMedia', capability: 'media-update'},
	{method: 'sendDataChannel', capability: 'datachannel'},
	{method: 'insertDTMF', capability: null},
	{method: 'getSubscribedPresences', capability: 'presence'},
	{method: 'getPresence', capability: 'presence'},
	{method: 'updatePresence', capability: 'presence'},
	{method: 'subscribeToPresence', capability: 'presence'},
	{method: 'subscribeToPresences', capability: 'presence'},
	{method: 'unsubscribeFromPresence', capability: 'presence'},
	{method: 'sendIm', capability: 'im'},
	{method: 'updateMediaState', capability: null},
	{method: 'getSettings', capability: 'settings'},
	{method: 'setSetting', capability: 'settings'},
	{method: 'getContacts', capability: 'contacts'},
	{method: 'addContact', capability: 'contacts'},
	{method: 'updateContact', capability: 'contacts'},
	{method: 'removeContact', capability: 'contacts'},
	{method: 'invalidateContacts', capability: 'contacts'},
	{method: 'searchContact', capability: 'ldap'},
	{method: 'getLogger', capability: 'log'},
	{method: 'sendStats', capability: 'stats'},
	{method: 'getCallAlarms', capability: 'call-alarms'},
	{method: 'triggerAlarm', capability: 'call-alarms'},
	{method: 'startListeningAudioLevel', capability: 'audio-level-reporting'},
	{method: 'stopListeningAudioLevel', capability: 'audio-level-reporting'},
	{method: 'reportStats', capability: 'stats-reporting'},
	{method: 'detectNetworkErrors', capability: 'networkerror-reporting'},
	{method: 'startRecording', capability: 'recording'},
	{method: 'stopRecording', capability: 'recording'},
	{method: 'getRecordings', capability: 'recording'},
	{method: 'getUser', capability: null},
	{method: 'updateUser', capability: null},
	{method: 'validatePhone', capability: null},
	{method: 'insertMany', capability: null},
	{method: 'getContactsWacUser', capability: null},
	{method: 'checkConnection', capability: 'network-reporting'},
	{method: 'getRemoteSessions', capability: 'list-remote'},
	{method: 'pushCallToSession', capability: 'call-push'},
	{method: 'pullCallFromSession', capability: 'call-pull'},
];

const NEW_ROUTES = {
	conference: {
		capability: 'conferences',
		methods: [
			'on',
			'off',
			'create',
			'fetch',
			'requestInvitation',
			'cancelInvitationRequest',
			'getCurrentInvitations',
			'getRoomByUri',
		],
	},
};

export class WacProxy {
	constructor() {
		this.emitter = new EventEmitter();
		for (const route of ROUTES) {
			this[route.method] = function(...args) {
				return this._routeCall(route.method, route.capability, args);
			}.bind(this);
		}

		for (const group in NEW_ROUTES) {
			this[group] = {};
			const {capability} = NEW_ROUTES[group];
			for (const route of NEW_ROUTES[group].methods) {
				this[group][route] = function(...args) {
					return this._newRouteCall(group, route, capability, args);
				}.bind(this);
			}
		}
		/** @private */
		this.stackReconnecting = false;

		bindMethods(this, [
			'onWacStackDisconnected',
			'onStackDisconnected',
			'onWacStackReconnecting',
			'onStackReconnecting',
			'onWacStackReconnected',
			'onStackReconnected',
		]);
	}

	initialize(wacUri) {
		this.cStack = new Stack();
		this.wStack = new WacStack(wacUri);
		this.capabilitiesManager = new CapabilitiesManager();
	}

	start() {
		this.wStack.on('disconnected', this.onWacStackDisconnected);
		this.cStack.on('qs-unregistered', this.onStackDisconnected);
		this.wStack.on('reconnecting', this.onWacStackReconnecting);
		this.cStack.on('qs-reconnecting', this.onStackReconnecting);
		this.wStack.on('reconnecting', this.onWacStackReconnected);
		this.cStack.on('qs-reconnected', this.onStackReconnected);
		async function start(stack) {
			await new Promise((resolve) => {
				stack.on('qs-ready', resolve);
				stack.start();
			});
			stack.off('qs-ready');
		}
		return Promise.all([start(this.cStack), this.wStack.start()]);
	}

	async loginToken(issuer, token, deviceId) {
		const wapiToken = await this.wStack.loginToken(issuer, token, deviceId);
		await this.capabilitiesManager.start(this.wStack.capabilitiesService);
		return wapiToken;
	}

	async recoverSession(token, deviceId) {
		const wapiToken = await this.wStack.recoverSession(token, deviceId);
		await this.capabilitiesManager.start(this.wStack.capabilitiesService);
		return wapiToken;
	}

	async register(credential, {deviceId}) {
		this.capabilitiesManager.setGatewayCapabilities(this.cStack.capabilities());
		await new Promise((resolve, reject) => {
			this.cStack.on('qs-register-success', () => {
				this.cStack.off('qs-register-success qs-register-error');
				resolve();
			});
			this.cStack.on('qs-register-error', () => {
				this.cStack.off('qs-register-success qs-register-error');
				reject(new StackRegisterError());
			});
			this.cStack.register(credential, {deviceId});
		});
		this.registeredCredential = credential;
	}

	async stop() {
		this.wStack.off('disconnected', this.onWacStackDisconnected);
		this.cStack.off('qs-unregistered', this.onStackDisconnected);
		this.wStack.off('reconnecting', this.onWacStackReconnecting);
		this.cStack.off('qs-reconnecting', this.onStackReconnecting);
		this.wStack.off('reconnecting', this.onWacStackReconnected);
		this.cStack.off('qs-reconnected', this.onStackReconnected);
		if (this.registeredCredential) {
			await new Promise((resolve, reject) => {
				this.cStack.on('qs-unregister-success', () => {
					this.cStack.off('qs-unregister-success qs-unregister-error');
					resolve();
				});
				this.cStack.on('qs-unregister-error', (error) => {
					this.cStack.off('qs-unregister-success qs-unregister-error');
					reject(error);
				});
				this.cStack.unregister();
			});
		}
		await this.wStack.stop();
	}

	getCapabilities() {
		return this.capabilitiesManager.getCapabilities();
	}

	hasCapability(capability) {
		return this.getCapabilities().includes(capability);
	}

	getVersion() {
		this.wStack.getVersion().concat(this.cStack.version());
	}

	on(eventName, callback) {
		this.cStack.on(eventName, callback);
		this.wStack.on(eventName, callback);
	}

	once(eventName, callback) {
		this.cStack.once(eventName, callback);
		this.wStack.once(eventName, callback);
	}

	off(eventName, callback) {
		this.cStack.off(eventName, callback);
		this.wStack.off(eventName, callback);
	}

	_joinResults(a, b) {
		if (a == null && b == null) {
			return;
		}
		if (a != null && b == null) {
			return a;
		}
		if (b != null && a == null) {
			return b;
		}
		if (typeof a !== typeof b) {
			return Error(`result type mismatch: ${typeof a} != ${typeof b}`);
		}
		if (typeof a === 'boolean') {
			return a && b;
		}
		if (typeof a === 'number') {
			return a + b;
		}
		if (typeof a === 'string') {
			return a + b;
		}
		if (a instanceof Array && b instanceof Array) {
			return a.concat(b);
		}
		if (isPromise(a) && isPromise(b)) {
			return Promise.all([a, b]);
		}
	}

	_routeCall(method, capability, args) {
		log.info(`route ${method}, capability ${capability}`);
		args = Array.prototype.slice.call(args);
		this.wStack.trace(method, args);
		if (this.capabilitiesManager.hasWacCapability(capability)) {
			return this.wStack[method](...args);
		} else if (this.capabilitiesManager.hasGatewayCapability(capability)) {
			return this.cStack[method](...args);
		} else if (capability == null) {
			let a, b;
			if (typeof this.wStack[method] === 'function') {
				a = this.wStack[method](...args);
			}
			if (typeof this.cStack[method] === 'function') {
				b = this.cStack[method](...args);
			}
			return this._joinResults(a, b);
		} else {
			throw new Error(`stack does not support ${capability}`);
		}
	}

	_newRouteCall(group, method, capability, args) {
		args = Array.prototype.slice.call(args);
		if (this.capabilitiesManager.hasWacCapability(capability)) {
			return this.wStack[group] != null ? this.wStack[group][method](...args) : undefined;
		}
		if (this.capabilitiesManager.hasGatewayCapability(capability)) {
			return this.cStack[group] != null ? this.cStack[group][method](...args) : undefined;
		} else if (!capability) {
			return this.wStack[group] != null ? this.wStack[group][method](...args) : undefined;
		}
	}

	getChatService() {
		return this.wStack.chatService;
	}

	getCallAttemptService() {
		return this.wStack.callAttemptService;
	}

	getCallService() {
		return this.wStack.callService;
	}

	getConferenceLogService() {
		return this.wStack.conferenceLogService;
	}

	getDatapipeService() {
		if (this.capabilitiesManager.hasWacCapability('datapipe')) {
			return this.wStack.datapipeService;
		} else if (this.capabilitiesManager.hasGatewayCapability('datapipe')) {
			return this.cStack;
		}
	}

	getFileSharingService() {
		return this.wStack.fileSharingService;
	}

	getGroupService() {
		return this.wStack.groupService;
	}

	getContactService() {
		return this.wStack.contactService;
	}

	getMeetingService() {
		return this.wStack.meetingService;
	}

	getPhonebookService() {
		return this.wStack.phonebookService;
	}

	getPresenceService() {
		return this.wStack.presenceService;
	}

	getPushNotificationsService() {
		return this.wStack.pushNotificationsService;
	}

	getResolver() {
		return this.wStack.resolver;
	}

	/** @private */
	async onWacStackDisconnected() {
		try {
			await this.stop();
		} catch (error) {
			log.error(error);
		} finally {
			this.emitter.emit('disconnected');
		}
	}

	/** @private */
	async onStackDisconnected() {
		try {
			await this.stop();
		} catch (error) {
			log.error(error);
		} finally {
			this.emitter.emit('disconnected');
		}
	}

	/** @private */
	async onWacStackReconnecting() {
		if (!this.stackReconnecting) {
			this.emitter.emit('reconnecting');
		}
	}

	/** @private */
	async onStackReconnecting() {
		this.stackReconnecting = true;
		if (!this.wStack.isReconnecting()) {
			this.emitter.emit('reconnecting');
		}
	}

	/** @private */
	async onWacStackReconnected() {
		if (!this.stackReconnecting) {
			this.emitter.emit('reconnected');
		}
	}

	/** @private */
	async onStackReconnected() {
		this.stackReconnecting = false;
		if (!this.wStack.isReconnecting()) {
			this.emitter.emit('reconnected');
		}
	}
}
