
import {ServerAPI} from './ServerAPI';

export class InvitesAPI extends ServerAPI {

	/**
	 * Send an invitation to one room to the user with given username.
	 * @throws {ErrorResponse} when server returns an error.
	 * @param {string} id invitation request ID.
	 * @param {string} from the user name that requested the invitation.
	 * @param {string} username user name to request invitation.
	 * @param {string} roomId room identifier where invited user should join.
	 * @param {object} mediatypes default mediatypes that invited peer should use.
	 * @param {string} [requestId] the ID of the request that generated this invitation.
	 * @param {object} context context information for agent assignment purposes
	 * @return {Promise} resolved with server response on success.
	 */
	invite(id, from, username, roomId, mediatypes, requestId, context) {
		return this.request('invitation', id, {
			from: from,
			to: username,
			replyOf: requestId,
			payload: {
				room: roomId,
			},
			mediatypes: mediatypes,
			context: context,
		}).then(response => response.payload);
	}

	/**
	 * Cancel a previous request.
	 * @throws {ErrorResponse} when server returns an error.
	 * @param {string} id received invitation request Id.
	 * @param {string} from user name that requested the invitation
	 * @param {number} [error=487] rejection code to send
	 */
	cancelInvitation(id, from, error = 487) {
		this.cancel('invitation', id, {
			from: from,
			error: error,
		});
	}

	/**
	 * Accept a received invitation to join a room.
	 * @throws {ErrorResponse} when server returns an error.
	 * @param {string} id invitation request ID.
	 * @param {string} from user name who send the invitation.
	 * @return {Promise} resolved with server resoponse on success.
	 */
	accept(id, from) {
		return this.answer('invitation', id, {from});
	}

	/**
	 * Decline a received/sent invitation to join a room.
	 * @throws {ErrorResponse} when server returns an error.
	 * @param {string} id invitation request ID.
	 * @param {string} from user name who send the invitation.
	 * @param {number} [error=603] rejection code to send
	 * @return {Promise} resoponse with server response on success.
	 */
	decline(id, from, error = 603) {
		return this.answer('invitation', id, {
			from: from,
			error: error,
		});
	}

	/**
	 * Retrieves current invitations
	 * @throws {ErrorResponse} when server returns an error.
	 * @param {string} id request Id.
	 * @param {string} from user name that requests the invitations
	 */
	getCurrentInvitations(id, from) {
		return this.request('getCurrentInvitations', id, {
			from,
		});
	}
}
