import {default as Logs} from '../../../Logs';

export class RemoteLog {

	constructor(wacRequest) {
		this.wacRequest = wacRequest;
		this.pathname = '/remotelog';
		this._enabled = false;

		this._logHandler = this._logHandler.bind(this);
	}

	enable() {
		if (this._enabled) {
			return;
		}
		Logs.instance.addHandler(this._logHandler);
		this._enabled = true;
	}

	disable() {
		if (!this._enabled) {
			return;
		}
		Logs.instance.removeHandler(this._logHandler);
		this._enabled = false;
	}

	create(level, file, text) {
		const body = {
			level,
			file,
			text,
		};
		return this.wacRequest.post(this.pathname, {}, body);
	}

	_logHandler(messages, context) {
		return this.create(
			context.level.name.toUpperCase(),
			context.name, JSON.stringify(messages, null, 2),
		);
	}
}
