/**
 * Conference Media stream.
 * Class used to encapsulate a MediaStream and provide additional info
 * about it's procedence.
 */
export class ConferenceMediaStream {
    /**
     * Create a new ConferenceMediaStream instance with
     * @param {string} feed associated user name.
     * @param {boolean} local indicates if stream is local.
     * @param {MediaStream} stream media stream.
     */
    constructor(feed, local, stream, medium) {
        /**
         * Associated user name.
         * @type {string}
         */
        this.feed = feed;
        /**
         * Indicate if stream is local.
         * @type {boolean}
         */
        this.local = local;
        /**
         * Media stream
         * @type {MediaStream}
         */
        this.stream = stream;
        this.medium = medium;
    }
}
