import {EventEmitter2} from 'eventemitter2';
import {bindMethods} from '../../../utils';

const PATH = '/presence';

import Logs from '../../../Logs';
const log = Logs.instance.getLogger('SippoJS/WacStack/Presence');

/**
 * Provides access to WAC presence service.
 *
 * Presences are accessed with an address identifier that determines the user
 * associated with {Presence} object.
 *
 * WAC provices two ways to access user's presence:
 * - Querying current state
 * - Subscribing to presence updates
 *
 * When we are subscribed to presence updates, if user associated with such
 * address updates it's presence we will receive a message that notifies the
 * update
 */
export class Presence {

	constructor(wacRequest) {
		this.wacRequest = wacRequest;
		this.emitter = new EventEmitter2();
		this.emitter.setMaxListeners(0);
		bindMethods(this, ['onPresencesReceived', 'onContactPresenceUpdated']);
		this.bindEventHandlers();
		log.warn('PresenceService started');
	}


	/** @private */
	bindEventHandlers() {
		this.wacRequest.emitter.on('presence/*', this.onContactPresenceUpdated);
		this.wacRequest.emitter.on('batch/presence', this.onPresencesReceived);
	}

	onPresencesReceived(event) {
		log.debug(`subscriptions response ${event.body}`);
	}

	/** @private */
	onContactPresenceUpdated(event) {
		const {path, method} = event;
		log.debug(`presence:onContactPresenceUpdated:${method} ${path}`);
		this.emitter.emit('presence-updated', event.body);
	}

	/**
	 * Return a list of presences that we are subscribed to
	 *
	 * @return {Promise<Array<Presence>>}
	 */
	getAll(withoutAvatar = false) {
		try {
			return this.wacRequest.get(PATH, {withoutAvatar});
		} catch (err) {
			if (err !== 404) {
				throw err;
			}
			log.warn('presence not found (404)');
			return [];
		}
	}

	/**
	 * Get a presence associated with given address
	 *
	 * @param {String} address user address to retrieve presence
	 *
	 * @return {Promise<Presence>} resolved with user's presence
	 */
	get(address) {
		return this.wacRequest.get(`${PATH}/${address}`);
	}

	/**
	 * Updates a Presence associated with given address
	 *
	 * @param {String} address user address to update presence
	 * @param {Presence} presence presence to update
	 *
	 * @return {Promise<Presence>} resolved with full presence object
	 */
	update(address, presence) {
		return this.wacRequest.put(`${PATH}/${address}`, {}, presence);
	}

	/**
	 * Subscribe to user's presence updates
	 *
	 * @param {String} address user associated address
	 *
	 * @return {Promise<Presence>} resolved with current user presence
	 */
	create(address) {
		return this.wacRequest.post(PATH, {}, {address});
	}

	/**
	 * Unsubscribe from user's presence updates
	 *
	 * @param {String} address user associated address
	 *
	 * @return {Promise}
	 */
	destroy(address) {
		return this.wacRequest.delete(`${PATH}/${address}`);
	}

	unsubscribeFromPresence(address) {
		return this.destroy(address);
	}

	subscribeToPresences(addresses) {
		return this.wacRequest.post(`/batch${PATH}`, {}, addresses);
	}
}
