import nanoid from 'nanoid';

import {bindMethods} from '../../../../utils';
import {default as Logs} from '../../../../Logs';
import {Emitter} from './Emitter';
import {InvitesAPI} from './InvitesAPI';
import {InviteRequestAPI} from './InviteRequestAPI';
import ReconnectingWebSocket from 'reconnecting-websocket';
import {RegisterAPI} from './RegisterAPI';
import {CallTransferAPI} from './CallTransferAPI';
import {RoomsAPI} from './RoomsAPI';
import {ErrorResponse} from './errors';

const log = Logs.instance.getLogger('SippoJS/Janus/SignalingClient');

export const MESSAGE_EVENT = 'message';

export const CONNECTED = 'connected';

export const RECONNECTED = 'reconnected';

export const RECONNECTING = 'reconnecting';

export const CONNECTION_CLOSED = 'closed';

const HEARTBEAT_INTERVAL = 35000;

export class SignalingClient extends Emitter {

	constructor(address, dependencies = {}) {
		super();
		this.dependencies = dependencies;

		this.address = address;

		this.socket = null;

		this.pending = {};

		this.invites = new InvitesAPI(this);
		this.inviteRequest = new InviteRequestAPI(this);
		this.register = new RegisterAPI(this);
		this.callTransfer = new CallTransferAPI(this);
		this.rooms = new RoomsAPI(this);

		this.running = false;

		bindMethods(this, ['onMessage', 'onClose']);

		this.pingTimer = 0;
	}

	get ready() {
		return !!this.socket;
	}

	init() {
		return new Promise((resolve, reject) => {
			log.debug(`init with address ${this.address}`);
			if (this.ready) {
				reject(new Error('already initialized'));
			}
			this.running = true;
			const RWS = this.dependencies.ReconnectingWebSocket || ReconnectingWebSocket;
			let socket = new RWS(this.address, 'jt', {
				minReconnectionDelay: 10000,
				connectionTimeout: 4000,
				debug: true,
			});
			socket.onopen = () => {
				log.debug(`socket created with ${this.address}`);
				this.socket = socket;
				this.socket.onclose = this.onClose.bind(this);
				this.socket.onerror = this.onClose.bind(this);
				this.socket.onmessage = this.onMessage.bind(this);
				this.emit(CONNECTED);
				this.heartbeat();
				resolve(this);
			};
			socket.onerror = this.handlerErrorSocket.bind(this);
			socket.onclose = this.handlerErrorSocket.bind(this);
		});
	}

	handlerErrorSocket(error) {
		log.error(`socket error with ${this.address}`, JSON.stringify(error));
		clearTimeout(this.pingTimer);
	}

	uninit() {
		return new Promise((resolve, reject) => {
			log.debug('uninit');
			if (!this.ready) {
				reject(new Error('not initialized'));
			}
			this.running = false;
			clearTimeout(this.pingTimer);
			this.closePendingMessages();
			this.socket.onclose = this.socket.onerror = () => {
				this.unbindSocketEvents();
				this.socket = null;
				this.emit(CONNECTION_CLOSED);
				resolve(this);
			};
			this.socket.close(undefined, undefined, {keepClosed: true});
		});
	}

	closePendingMessages() {
		Object.keys(this.pending).forEach((id) => {
			log.warn('pending request exist on uninit with id', id);
			this.pending[id].reject(new Error('uninit'));
			delete this.pending[id];
		});
	}

	send(data, id, retry) {
		return new Promise((resolve, reject) => {
			log.debug('send', data);
			if (!this.ready) {
				return reject(new Error('not initialized'));
			}
			if (id) {
				let pending = {
					resolve: resolve,
					reject: reject,
				};
				if (typeof retry === 'number') {
					pending.timer = setInterval(() => {
						retry--;
						if (retry <= 0) {
							clearInterval(pending.timer);
							pending.reject(new Error('timeout'));
							delete this.pending[id];
						} else {
							try {
								this.socket.send(JSON.stringify(data));
							} catch (err) {
								throw Error('Session closed');
							}
						}
					}, 2000);
				}
				this.pending[id] = pending;
			} else {
				resolve();
			}
			data.requestID = data.requestID || nanoid();
			this.socket.send(JSON.stringify(data));
		});
	}

	onMessage(msg) {
		log.debug(`received ${msg.data}`);
		var data = JSON.parse(msg.data);
		if (data.request === 'ping') {
			this.heartbeat();
			this.send({answer: 'ping'});
		}
		let pending = this.pending[data.requestID];
		if (pending) {
			clearInterval(pending.timer);
			delete this.pending[data.requestID];
			if (data.error) {
				return pending.reject(new ErrorResponse(data.error));
			}
			return pending.resolve(data);
		}
		this.emit(MESSAGE_EVENT, data);
	}

	heartbeat() {
		clearTimeout(this.pingTimer);
		this.pingTimer = setTimeout(() => {
			this.stopCurrentSocketAndReconnect();
		}, HEARTBEAT_INTERVAL);
	}

	stopCurrentSocketAndReconnect() {
		this.closeCurrentSocket();
		this.cleanState();
		this.reconnect();
	}

	closeCurrentSocket() {
		this.unbindSocketEvents();
		this.socket.close(undefined, undefined, {keepClosed: true});
	}

	async reconnect() {
		try {
			this.emit(RECONNECTING);
			await this.init();
			this.emit(RECONNECTED);
		} catch (error) {
			this.unbindSocketEvents();
			this.cleanState();
			this.closePendingMessages();
			this.emit(CONNECTION_CLOSED);
		}
	}

	cleanState() {
		clearTimeout(this.pingTimer);
		this.running = false;
		this.socket = null;
	}

	unbindSocketEvents() {
		if (this.socket) {
			this.socket.onmessage = this.socket.onclose = this.socket.onerror = () => {};
		}
	}

	async onClose() {
		if (this.ready) {
			this.stopCurrentSocketAndReconnect();
		}
	}
}
