
import nanoid from 'nanoid';
import {ServerAPI} from './ServerAPI';

export class RegisterAPI extends ServerAPI {

	/**
	 * Create a registry on server.
	 * @throws {ErrorResponse} if server returns an error
	 * @param {string} username user name to register.
	 * @param {string} token validation token.
	 * @param {object} options Options to pass in the login
	 * @param {string} options.deviceId The id that identifies to a device
	 * @return {Promise<object>} resolved with created registry.
	 */
	login(username, token, {deviceId} = {}) {
		return this.request('login', nanoid(), {
			payload: {
				username: username,
				token: token,
				deviceId,
			},
		}).then(response => response.payload);
	}

	/**
	 * Remove registration on server
	 * @throws {ErrorResponse} if server returns an error
	 * @return {Promise} resolved on success.
	 */
	logout() {
		return this.request('logout', nanoid(), {})
			.then(response => response.payload)
			.catch(() => {});
	}
}
