import {EventEmitter2} from 'eventemitter2';
export class Contact {

	constructor(wacRequest) {
		this.pathname = '/contacts';
		this.wacRequest = wacRequest;
		this.wacRequest.emitter.on('contacts/', this.onContact.bind(this));
		this.wacRequest.emitter.on('contacts/insert_many', this.onContacts.bind(this));
		this.wacRequest.emitter.on('contacts/:id', this.onContactChanged.bind(this));

		this.emitter = new EventEmitter2();
		this.emitter.setMaxListeners(0);

		this.getContacts = this.getContacts.bind(this);
		this.addContact = this.addContact.bind(this);
		this.invalidateContacts = this.invalidateContacts.bind(this);
		this.updateContact = this.updateContact.bind(this);
		this.removeContact = this.removeContact.bind(this);
		this.searchContact = this.searchContact.bind(this);
		this.insertMany = this.insertMany.bind(this);
		this.getContactsWacUser = this.getContactsWacUser.bind(this);
	}

	onContact(event) {
		this.emitter.emit('qs-contact-add', event.body);
	}

	onContacts(event) {
		this.emitter.emit('qs-contact-add-many', event.body);
	}

	onContactChanged(event) {
		if (event.method === 'put') {
			this.emitter.emit('qs-contact-update', event.body);
		} else if (event.method === 'del') {
			this.emitter.emit('qs-contact-remove', event.body);
		}
	}

	getContacts(query = {}) {
		return this.wacRequest.get(this.pathname, query);
	}

	addContact(data) {
		return this.wacRequest.post(this.pathname, {}, data);
	}

	updateContact(data) {
		const fullPath = `${this.pathname}/${data.id}`;
		return this.wacRequest.put(fullPath, {}, data);
	}

	/**
	 * Delete all contacts from a source
	 *
	 * @param  {String} deviceId Identificator of a device
	 * @return {Promise} Fulfilled promise if success
	 */
	invalidateContacts(deviceId) {
		return this.wacRequest.delete(this.pathname, {deviceId});
	}

	removeContact(data) {
		const fullPath = `${this.pathname}/${data.id}`;
		return this.wacRequest.delete(fullPath);
	}

	searchContact(data) {
		return this.getAll(true, data);
	}

	getAll(ldap = false, data = {}) {
		if (ldap) {
			return this.wacRequest.get('/ldap/', {query: data});
		} else {
			return this.wacRequest.get(this.pathname, {});
		}
	}

	insertMany(data) {
		return this.wacRequest.post('/contacts/insert_many', {}, data);
	}

	getContactsWacUser(query = {}) {
		return this.wacRequest.get('/contacts/iswacuser/', query);
	}
}
