import { EventEmitter2 } from 'eventemitter2';
import * as log from 'js-logger';
import nanoid from 'nanoid';
import { ClientFactory } from './ClientFactory';
class ClientFacade {
    constructor(dependencies) {
        this.dependencies = dependencies;
        this.clientEmitter = new EventEmitter2();
    }
    static of(dependencies = {}) {
        return new ClientFacade(dependencies);
    }
    async init(server) {
        if (this.client !== undefined) {
            throw Error('already connected');
        }
        const clientFactory = ClientFactory.from(this.dependencies);
        this.client = await clientFactory.createClient(server);
        this.client
            .getEvents()
            .subscribe((e) => this.clientEmitter.emit(e.eventName, e.payload));
    }
    async uninit() {
        if (this.client === undefined) {
            throw Error('already disconnected');
        }
        this.client.disconnect();
        this.client = undefined;
    }
    async send(endpoint, payload) {
        if (this.client === undefined) {
            throw new Error('not connected');
        }
        const request = { endpoint, payload, id: nanoid() };
        log.debug('send message', request);
        try {
            const response = await this.client.request(request).toPromise();
            log.debug(`received ${request.id} response`, response);
            return response;
        }
        catch (e) {
            log.error(`received ${request.id} error response: `, e.message);
            throw e;
        }
    }
    get emitter() {
        return this.clientEmitter;
    }
    isConnected() {
        return this.client !== undefined;
    }
}
export const Client = ClientFacade.of;
