import {EventEmitter2} from 'eventemitter2';
import {bindMethods} from '../../../utils';

export class CallStats {
	constructor(wacRequest) {
		this.pathname = '/stats/call';
		this.wacRequest = wacRequest;
		this.emitter = new EventEmitter2();
		this.emitter.setMaxListeners(0);
		bindMethods(this, ['onStatsEnable', 'sendStats']);
		this.bindEventHandlers();
	}

	bindEventHandlers() {
		this.wacRequest.emitter.on('/stats/call', this.onStatsEnable);
	}

	onStatsEnable(event) {
		this.emitter.emit('qs-enable-stats', event.body);
	}

	sendStats(service, ts, message, payload) {
		const body = {
			service,
			ts,
			message,
			payload,
		};
		return this.wacRequest.post(this.pathname, {}, body);
	}
}
