import {LoginError} from '../errors';

export class SapiClient {
	/**
     * @param {Object} urls
	 * @param {string} urls.urn:quobis:sippo:https The URL for the SAPI endpoint
     */
	set urls(urls) {
		if (!urls['urn:quobis:sippo:https']) {
			throw new Error('No valid URL found for SAPI');
		}
		this._url = urls['urn:quobis:sippo:https'];
	}

	async login(provider, token, deviceId) {
		return this._request(`${this._url}/login`, {
			'grant_type': 'oauth2',
			provider,
			token,
			context: this._getContext(deviceId),
		}).catch((err) => {
			throw new LoginError('Failed to login', err.message);
		});
	}

	async recover(token) {
		return this._request(`${this._url}/login/sessionToken`, {token}).catch((err) => {
			throw new LoginError('Failed to recover session', err.message);
		});
	}

	async _request(url, body) {
		if (!this._url) {
			throw new Error('No URL set');
		}
		const options = {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
			},
			redirect: 'follow',
			body: JSON.stringify(body),
		};
		const response = await fetch(url, options);
		if (response.status >= 200 && response.status < 300) {
			const json = await response.json();
			return json;
		} else {
			throw new Error('Non 200 OK response');
		}
	}

	/**
	 * getContext foo
	 *
	 * @param {string} deviceId The Device's ID
	 */
	_getContext(deviceId) {
		let language = navigator.language || 'en';
		if (navigator.languages && navigator.languages.length) {
			language = navigator.languages[0];
		}
		let context = {
			useragent: navigator.userAgent,
			language: language,
			location: this._getLocationHref(),
			platform: navigator.platform,
			vendor: navigator.vendor,
			deviceId,
		};
		return context;
	}

	_getLocationHref() {
		return location.href;
	}
}
