
const _client = Symbol('client');

export class ServerAPI {

	/**
	 * Create a new Server API client
	 * @param {Client} client server client
	 */
	constructor(client) {

		/**
		 * Server client
		 * @private
		 * @type {Client}
		 */
		this[_client] = client;
	}

	/**
	 * Send a request to server with given data.
	 * @protected
	 * @throws {ErrorResponse} if server returns an error
	 * @param {string} type request type, i.e: 'createRoom', 'invitation',...
	 * @param {string} id request id
	 * @param {object} body requested body.
	 * @param {number} [retry=0] if specified and an id is provided, when an answer
	 * is not received for this requested id, message will be send again periodically.
	 * @return {Promise<object>} resolved with server response.
	 */
	request(type, id, body, retry) {
		let request = Object.assign({
			request: type,
			requestID: id,
		}, body);
		return this[_client].send(request, id, retry);
	}

	/**
	 * Cancel a previous request.
	 * @protected
	 * @throws {ErrorResponse} if server returns an error
	 * @param {string} type response type, i.e: 'invitation',...
	 * @param {string} id request id
	 */
	cancel(type, id, payload) {
		let response = Object.assign({
			cancel: type,
			requestID: id,
		}, payload);
		this[_client].send(response);
	}

	/**
	 * Send an answer to server with given data.
	 * @protected
	 * @throws {ErrorResponse} if server returns an error
	 * @param {string} type response type, i.e: 'invitation',...
	 * @param {string} id request id
	 * @param {object} payload response data.
	 */
	answer(type, id, payload) {
		let response = Object.assign({
			answer: type,
			requestID: id,
		}, payload);
		return this[_client].send(response);
	}

	answerWaitingResponse(type, id, payload) {
		const response = Object.assign({
			answer: type,
			requestID: id,
		}, payload);
		return this[_client].send(response, id);
	}
}
