export function HeldSubscriptionsState() {
    let savedSubscriptions = [];
    const isDifferentFeed = feed1 => feed2 => (feed1.feed !== feed2.feed) ||
        (feed1.medium !== feed2.medium);
    return {
        saveSubscription({ feed, medium }) {
            savedSubscriptions.push({ feed, medium });
        },
        getSavedSubscriptions() {
            return savedSubscriptions;
        },
        removeSubscription(feed) {
            savedSubscriptions = savedSubscriptions.filter(isDifferentFeed(feed));
        },
        isStored({ feed, medium }) {
            return savedSubscriptions.some(feedStored => feedStored.feed === feed && feedStored.medium === medium);
        }
    };
}
;
