const PATH = '/pushNotifications';

export class PushNotificationsService {
	constructor(wacRequest) {
		this.wacRequest = wacRequest;
	}

	notifyToken(deviceId, tokenType, token) {
		return this.wacRequest.post(PATH, {}, {deviceId, tokenType, token});
	}

	destroyToken(deviceId) {
		return this.wacRequest.delete(PATH, {}, {deviceId});
	}
}
