import {EventEmitter2} from 'eventemitter2';
import qs from 'query-string';
import generate from 'nanoid/generate';
import {JID} from 'stanza';
import {bindMethods} from '../../utils';
import Logs from '../../Logs';

const log = Logs.instance.getLogger('SippoJS/WacStack/ChatService');

const SERVER_CHAT_INVITE_REASONS = {
	CONFERENCE: "You've been added to the conference's chat room",
	SUPPORT: 'supportChat',
};

export const CHAT_INVITE_REASONS = {
	CONFERENCE: 'conference',
	SUPPORT: 'support',
};

const castServerReason = (reason) => {
	switch (reason) {
		case SERVER_CHAT_INVITE_REASONS.CONFERENCE:
			return CHAT_INVITE_REASONS.CONFERENCE;
		case SERVER_CHAT_INVITE_REASONS.SUPPORT:
			return CHAT_INVITE_REASONS.SUPPORT;
		default:
			return reason;
	}
};

export class ChatService {
	constructor(wacRequest, xmppClient, pushUri) {
		this.wacRequest = wacRequest;
		this.xmppClient = xmppClient;
		this.pushUri = pushUri;
		this.emitter = new EventEmitter2({wildcard: true});
		this.emitter.setMaxListeners(0);
		bindMethods(this, [
			'onIndividualChatChanges',
			'onMarkerReceived',
			'onMarkerDisplayed',
			'onMucInvite',
			'onMucLeave',
			'onMucTopic',
			'onMucOther',
			'onChat',
			'onChatState',
			'onPresence',
			'onMessage',
			'onMessageError',
			'onMessageSent',
		]);
	}

	start() {
		this.wacRequest.emitter.on('xmpp/individualChatList/*', this.onIndividualChatChanges);
		this.xmppClient.emitter.on('marker:received', this.onMarkerReceived);
		this.xmppClient.emitter.on('marker:displayed', this.onMarkerDisplayed);
		this.xmppClient.emitter.on('muc:invite', this.onMucInvite);
		this.xmppClient.emitter.on('muc:leave', this.onMucLeave);
		this.xmppClient.emitter.on('muc:topic', this.onMucTopic);
		this.xmppClient.emitter.on('muc:other', this.onMucOther);
		this.xmppClient.emitter.on('chat', this.onChat);
		this.xmppClient.emitter.on('groupchat', this.onChat);
		this.xmppClient.emitter.on('chat:state', this.onChatState);
		this.xmppClient.emitter.on('presence', this.onPresence);
		this.xmppClient.emitter.on('message', this.onMessage);
		this.xmppClient.emitter.on('message:sent', this.onMessageSent);
		this.xmppClient.emitter.on('message:error', this.onMessageError);
		return this.xmppClient.start();
	}

	stop() {
		this.wacRequest.emitter.off('xmpp/individualChatList/*', this.onIndividualChatChanges);
		this.xmppClient.emitter.off('marker:received', this.onMarkerReceived);
		this.xmppClient.emitter.off('marker:displayed', this.onMarkerDisplayed);
		this.xmppClient.emitter.off('muc:invite', this.onMucInvite);
		this.xmppClient.emitter.off('muc:leave', this.onMucLeave);
		this.xmppClient.emitter.off('muc:topic', this.onMucTopic);
		this.xmppClient.emitter.off('muc:other', this.onMucOther);
		this.xmppClient.emitter.off('chat', this.onChat);
		this.xmppClient.emitter.off('groupchat', this.onChat);
		this.xmppClient.emitter.off('chat:state', this.onChatState);
		this.xmppClient.emitter.off('presence', this.onPresence);
		this.xmppClient.emitter.off('message', this.onMessage);
		this.xmppClient.emitter.off('message:sent', this.onMessageSent);
		this.xmppClient.emitter.off('message:error', this.onMessageError);
		return this.xmppClient.stop();
	}

	suspend() {
		return this.xmppClient.suspend();
	}

	resume() {
		return this.xmppClient.resume();
	}

	onIndividualChatChanges(event) {
		const participant = event.path.split('/').pop();
		switch (event.method) {
			case 'put':
				this.emitter.emit('individualChatAdded', participant);
				break;
			case 'delete':
				this.emitter.emit('individualChatDeleted', participant);
				break;
			// no default
		}
	}

	onMarkerReceived(event) {
		this.emitter.emit(['received', JID.toBare(event.from)], {
			chat: JID.toBare(event.from),
			from: event.type === 'groupchat' ? JID.getResource(event.from) : JID.toBare(event.from),
			id: event.marker.id,
		});
	}

	onMarkerDisplayed(event) {
		this.emitter.emit(['displayed', JID.toBare(event.from)], {
			chat: JID.toBare(event.from),
			from: event.type === 'groupchat' ? JID.getResource(event.from) : JID.toBare(event.from),
			id: event.marker.id,
		});
	}

	onChat(event) {
		const from = event.type === 'groupchat' ? JID.getResource(event.from) : JID.toBare(event.from);
		const timestamp = event.delay ? new Date(event.delay.timestamp).getTime() : Date.now();
		this.emitter.emit('chat', {type: event.type, id: JID.toBare(event.from)});
		this.emitter.emit(['message', JID.toBare(event.from)], {
			chat: JID.toBare(event.from),
			type: event.type,
			id: event.id,
			from,
			to: JID.toBare(event.to),
			timestamp,
			body: event.body,
			links: event.links,
		});
	}

	onMucInvite(event) {
		this.emitter.emit('mucInvite', {
			chatId: JID.toBare(event.room),
			conferenceChat: event.reason === SERVER_CHAT_INVITE_REASONS.CONFERENCE,
			reason: castServerReason(event.reason),
		});
	}

	onMucLeave(event) {
		this.emitter.emit('mucEnd', {chatId: JID.toBare(event.from)});
	}

	onMucTopic(event) {
		this.emitter.emit(['mucConfig', JID.toBare(event.from)], {
			subject: event.topic,
		});
	}

	async onMucOther(event) {
		if (event.muc.jid) {
			if (JID.getLocal(event.muc.jid) === 'admin' || JID.equal(event.muc.jid, event.to)) {
				return;
			}
			this.emitter.emit(['mucPresence', event.from], {
				jid: JID.toBare(event.muc.jid),
				affiliation: event.muc.affiliation,
				role: event.muc.role,
			});
		} else if (event.muc.statusCodes && event.muc.statusCodes.includes('104')) {
			const info = await this.getRoomInfo(event.from);
			this.emitter.emit(['mucConfig', event.from], info);
		}
	}

	onChatState(event) {
		const from = event.type === 'groupchat' ? JID.getResource(event.from) : JID.toBare(event.from);
		this.emitter.emit(['state', JID.toBare(event.from)], {
			from,
			to: JID.toBare(event.to),
			state: event.chatState,
		});
	}

	onPresence(event) {
		if (event.muc && event.muc.jid) {
			if (JID.getLocal(event.muc.jid) === 'admin') {
				return;
			}
			if (JID.equalBare(event.muc.jid, event.to)) {
				if (event.muc.affiliation === 'none') {
					this.emitter.emit('mucEnd', {chatId: JID.toBare(event.from)});
				}
				return;
			}
			this.emitter.emit(['mucPresence', JID.toBare(event.from)], {
				jid: JID.toBare(event.muc.jid),
				affiliation: event.muc.affiliation,
				role: event.muc.role,
			});
		}
	}

	onMessageSent(event) {
		if (event.type !== 'chat' && event.type !== 'groupchat') {
			return;
		}
		const datum = {
			chat: JID.toBare(event.to),
			type: event.type,
			id: event.id,
			to: JID.toBare(event.to),
			timestamp: event.delay ? new Date(event.delay.timestamp).getTime() : Date.now(),
			body: event.body,
			links: event.links,
		};
		if (event.marker) {
			if (event.marker.type === 'received') {
				datum.received = event.marker.id;
			} else if (event.marker.type === 'displayed') {
				datum.displayed = event.marker.id;
			}
		}
		this.emitter.emit(['message:sent', JID.toBare(event.to)], datum);
	}

	onMessage(event) {
		if (event.body && event.id) {
			localStorage.setItem('sippo:lastXmppTimestamp', Date.now());
		}
	}

	onMessageError(event) {
		log.error(event);
	}

	async getIndividualChats() {
		const res = await this.wacRequest.get('xmpp/individualChatList/');
		return res.chats;
	}

	addIndividualChat(jid) {
		return this.wacRequest.put(`xmpp/individualChatList/${jid}`);
	}

	deleteIndividualChat(jid) {
		return this.wacRequest.delete(`xmpp/individualChatList/${jid}`);
	}

	async getRoomInfo(jid) {
		const res = await this.xmppClient.getDiscoInfo(jid);
		const fields = res.extensions[0].fields;
		let ret = {};
		for (const field of fields) {
			if (field.name === 'muc#roomconfig_roomname') {
				ret.name = field.value || '';
			} else if (field.name === 'muc#roominfo_description') {
				ret.subject = field.value || '';
			}
		}
		return ret;
	}

	async getBookmarks() {
		const bookmarks = await this.xmppClient.getBookmarks();
		return bookmarks.map(group => group.jid);
	}

	addBookmark(jid) {
		return this.xmppClient.addBookmark({jid});
	}

	removeBookmark(jid) {
		return this.xmppClient.removeBookmark(jid);
	}

	async createRoom(creator, name = '', subject = '') {
		const alphabet = '0123456789_abcdefghijklmnopqrstuvwxyz-';
		const jid = `${generate(alphabet, 28)}@conference.${JID.getDomain(creator)}`;
		await this.xmppClient.joinRoom(jid, creator);
		const form = await this.xmppClient.getRoomConfig(jid);
		const fields = form.fields.map((field) => {
			if (field.name === 'FORM_TYPE') {
				return {name: field.name, value: field.value};
			}
			if (field.name === 'muc#roomconfig_roomname') {
				return {name: field.name, value: name};
			}
			if (field.name === 'muc#roomconfig_roomdesc') {
				return {name: field.name, value: subject};
			}
			if (field.name === 'muc#roomconfig_persistentroom') {
				return {name: field.name, value: '1'};
			}
			if (field.name === 'muc#roomconfig_publicroom') {
				return {name: field.name, value: '0'};
			}
			if (field.name === 'muc#roomconfig_membersonly') {
				return {name: field.name, value: '1'};
			}
			if (field.name === '{http://prosody.im/protocol/muc}roomconfig_allowmemberinvites') {
				return {name: field.name, value: '1'};
			}
			if (field.name === 'muc#roomconfig_allowinvites') {
				return {name: field.name, value: '1'};
			}
			if (field.name === 'muc#roomconfig_changesubject') {
				return {name: field.name, value: '1'};
			}
			if (field.name === 'muc#roomconfig_moderatedroom') {
				return {name: field.name, value: '0'};
			}
			if (field.name === 'muc#roomconfig_whois') {
				return {name: field.name, value: 'anyone'};
			}
			if (field.name === 'muc#roomconfig_enablearchiving') {
				return {name: field.name, value: '1'};
			}
			// From XEP-0045. If the client wishes to receive no history, it MUST set the 'maxchars' attribute to a value of "0" (zero).
			// Even the client set maxchars to zero, the history is received. Until xmpp fix the bug, the room will not send the history.
			if (field.name === 'muc#roomconfig_historylength') {
				return {name: field.name, value: '0'};
			}
		}).filter(field => field);
		await this.xmppClient.configureRoom(jid, {fields});
		return jid;
	}

	async setRoomName(jid, name) {
		try {
			const form = await this.xmppClient.getRoomConfig(jid);
			const fields = form.fields;
			for (const field of fields) {
				if (field.name === 'muc#roomconfig_roomname') {
					field.value = name;
					break;
				}
			}
			await this.xmppClient.configureRoom(jid, {fields});
		} catch (error) {
			throw new Error(error.error.condition);
		}
	}

	setRoomSubject(jid, subject) {
		return this.xmppClient.setSubject(jid, subject);
	}

	async getRoomMembers(jid) {
		const res = await Promise.all([
			this.xmppClient.getRoomMembers(jid, 'owner'),
			this.xmppClient.getRoomMembers(jid, 'admin'),
			this.xmppClient.getRoomMembers(jid, 'member'),
		]);
		// Array.prototype.flatMap is missing in some cellphones (eg: Huawei P20)
		const flatMap = (f, xs) => xs.reduce((acc, x) => acc.concat(f(x)), []);
		return flatMap(d => d.muc.users || [], res)
			.filter(d => JID.getLocal(d.jid) !== 'admin')
			.map(d => ({
				affiliation: d.affiliation,
				jid: JID.toBare(d.jid),
			}));
	}

	joinRoom(jid, nick) {
		return this.xmppClient.joinRoom(jid, nick, {muc: {type: 'join', history: {maxCharacters: 0}}});
	}

	invite(room, to) {
		to = Array.isArray(to) ? to : [to];
		return this.xmppClient.invite(room, [{to}]);
	}

	expel(room, to) {
		return this.xmppClient.setRoomAffiliation(room, to, 'none');
	}

	async setParticipantRole(room, to, role) {
		try {
			await this.xmppClient.setRoomAffiliation(room, to, role);
		} catch (error) {
			throw new Error(error.error.condition);
		}
	}

	leave(room) {
		return this.xmppClient.deleteAccount(room);
	}

	async destroy(jid, reason = 'Room deleted by owner') {
		try {
			await this.xmppClient.destroyRoom(jid, {reason});
		} catch (error) {
			throw new Error(error.error.condition);
		}
	}

	setState(to, type, chatState) {
		return this.xmppClient.sendMessage({to, type, chatState});
	}

	async fetchMessages(jid, type, lastForwardedMessage, max = 50) {
		const params = {
			to: type === 'groupchat' ? jid : undefined,
			with: type === 'chat' ? jid : undefined,
			paging: {max, before: lastForwardedMessage ? lastForwardedMessage : true},
		};
		const res = await this.xmppClient.searchHistory(params);
		const results = this.processMamMessages(res.results.reverse());
		return {
			results,
			complete: res.complete,
			paging: res.paging,
		};
	}

	async sync(jid, type, start) {
		const params = {
			to: type === 'groupchat' ? jid : undefined,
			with: type === 'chat' ? jid : undefined,
		};
		let res = await this.xmppClient.searchHistory({...params, start: new Date(start)});
		let results = res.results;
		while (!res.complete) {
			const paging = {after: res.paging.last};
			res = await this.xmppClient.searchHistory({...params, paging});
			results = results.concat(res.results);
		}
		return this.processMamMessages(res.results);
	}

	async syncMucInvitations(to) {
		const lastXmppTimestamp = parseInt(localStorage.getItem('sippo:lastXmppTimestamp'));
		if (lastXmppTimestamp > 0) {
			const params = {to, start: new Date(lastXmppTimestamp)};
			let res = await this.xmppClient.searchHistory(params);
			let results = res.results;
			while (!res.complete) {
				const paging = {after: res.paging.last};
				res = await this.xmppClient.searchHistory({...params, paging});
				results = results.concat(res.results);
			}
			for (const {item} of results) {
				const {muc} = item.message;
				if (muc && muc.action === 'invite' && muc.invite.some(({reason}) => reason !== SERVER_CHAT_INVITE_REASONS.CONFERENCE)) {
					this.emitter.emit('mucInvite', {
						chatId: item.message.from,
					});
				}
			}
			return res;
		}
		// FIXME Last received message information is not available available so MUC invitations could have been lost.
		// FIXME as we can't know since what point of past time we need to request all messages
	}

	sendMessage(to, type, body) {
		return this.xmppClient.sendMessage({to, type, body, marker: {type: 'markable'}});
	}

	sendFile(to, type, url, description) {
		return this.xmppClient.sendMessage({to, type, marker: {type: 'markable'}, links: [{url, description}]});
	}

	markReceived(to, id, type) {
		return this.xmppClient.markReceived(to, id, type);
	}

	markDisplayed(to, id, type) {
		return this.xmppClient.markDisplayed(to, id, type);
	}

	async enableNotifications(config) {
		const registerUri = `${this.pushUri}/v1/register`;
		const headers = {'Content-Type': 'application/x-www-form-urlencoded'};
		const body = qs.stringify({
			node: config.node,
			type: config.type,
			token: config.token,
		});
		const res = await window.fetch(registerUri, {method: 'POST', headers, body});
		const data = await res.text();
		const parts = data.split('\n');
		if (parts[0] !== 'OK') {
			throw new Error('ERROR response from register: ' + data);
		}
		if (parts[1] !== config.node) {
			throw new Error('Wrong node received from register: ' + parts[1]);
		}
		const secret = parts[2];
		const fields = [{name: 'secret', value: secret}];
		return this.xmppClient.enableNotifications(config.jid, config.node, fields);
	}

	async disableNotifications(config) {
		const unregisterUri = `${this.pushUri}/v1/unregister`;
		const headers = {'Content-Type': 'application/x-www-form-urlencoded'};
		const body = qs.stringify({
			node: config.node,
			type: config.type,
			token: config.token,
		});
		const res = await window.fetch(unregisterUri, {method: 'POST', headers, body});
		const data = await res.text();
		const parts = data.split('\n');
		if (parts[0] !== 'OK') {
			throw new Error('ERROR response from register: ' + data);
		}
		if (parts[1] !== config.node) {
			throw new Error('Wrong node received from register: ' + parts[1]);
		}
		return this.xmppClient.disableNotifications(config.jid, config.node);
	}

	/** @private */
	processMamMessages(results) {
		return results.filter(({item}) => {
			const marker = item.message.marker;
			const body = item.message.body;
			const links = item.message.links;
			return body || links || marker && (marker.type === 'displayed' || marker.type === 'received');
		}).map(({item}) => {
			const message = item.message;
			return {
				id: message.id,
				from: message.type === 'groupchat' ? JID.getResource(message.from) : JID.toBare(message.from),
				to: message.to,
				timestamp: new Date(item.delay.timestamp).getTime(),
				body: message.body,
				links: message.links,
				displayed: message.marker && message.marker.type === 'displayed' ? message.marker.id : undefined,
				received: message.marker && message.marker.type === 'received' ? message.marker.id : undefined,
			};
		});
	}

	createSupportChat(id, context) {
		return this.wacRequest.post('/xmpp/supportChat', {id}, {context});
	}
}
