import {EventInterval, Scheduler} from '../EventInterval';
import {Session} from './Session';

type SessionUpdater = (id: string) => Promise<Session>;

export class SessionsThread {
	private interval: EventInterval;
	private retained!: Session;
	private updateFn: SessionUpdater;
	private updatedFn: () => void;
	private onError: () => void;

	public constructor(
		updateFn: SessionUpdater,
		updatedFn: () => void,
		onError: () => void,
		scheduler: Scheduler,
	) {
		this.updateFn = updateFn;
		this.updatedFn = updatedFn;
		this.onError = onError;
		this.interval = new EventInterval(
			this.action.bind(this),
			30000,
			{
				setInterval: scheduler.setInterval,
				clearInterval: scheduler.clearInterval,
			},
		);
		this.interval.emitter.on('error', this.onError);
	}

	public retain(session: Session): void {
		this.retained = session;
		this.interval.run();
	}

	private async action(): Promise<void> {
		this.retained = await this.updateFn(this.retained.id);
		this.updatedFn();
	}

	public release(): void {
		this.interval.stop();
	}
}
