interface Constraints {
	audio?: MediaTrackConstraints | boolean;
	video?: MediaTrackConstraints | boolean;
	screen?: MediaTrackConstraints | boolean;
}

/**
 * Stop a {MediaStream} by calling 'stop' method on every track.
 *
 * @param {MediaStream} stream stream to stop
 * @return {void}
 */
export function stopStream(stream: MediaStream): void {
	stream.getTracks().forEach(track => track.stop());
}

function promiseWithTimeout<T>(promise: Promise<T>, timeout: number): Promise<T> {
	const timeoutPromise: Promise<any> = new Promise((resolve, reject) => {
		setTimeout(() => {
			reject(`${promise} timed out after ${timeout} ms`);
		}, timeout);
	});

	// Returns a race between our timeout and the passed in promise
	return Promise.race([promise, timeoutPromise]);
}

function getUserMedia(constraints: MediaStreamConstraints): Promise<MediaStream> {
	return promiseWithTimeout(navigator.mediaDevices.getUserMedia(constraints), 60000);
}

async function getDisplayMedia(constraints: MediaStreamConstraints): Promise<MediaStream> {
	// @ts-ignore
	if (window.desktopCapturer) {
		// @ts-ignore
		const [source] = await window.desktopCapturer.getSources({types: ['screen']});
		return getUserMedia({video: {mandatory: {
			chromeMediaSource: 'desktop',
			chromeMediaSourceId: source.id,
		}} as unknown as MediaTrackConstraints});

	}
	// @ts-ignore https://github.com/microsoft/TypeScript/issues/33232
	if (navigator.mediaDevices && typeof navigator.mediaDevices.getDisplayMedia === 'function') {
		// @ts-ignore https://github.com/microsoft/TypeScript/issues/33232
		return promiseWithTimeout(navigator.mediaDevices.getDisplayMedia(constraints), 10000);
	}
	throw Error('Browser does not support screensharing');
}


/**
 * Method that returns a {MediaStream} based on constraints provided
 * This method is needed for screen sharing when an unique stream that
 * contains audio, video and screen tracks must be obtained. Current implementation
 * has some limitations:
 *  - Require two video tracks (camera and screen) is not supported
 *  - Screen sharing is only supported on Chrome >= 72, Firefox >= 66 and Edge >= 17
 *
 * @param {Constraints} constraints audio camera and screen constraints
 * @return {Promise<MediaStream>} media stream with desired tracks
 */
export async function getUserMediaWithMediatypes(constraints: Constraints = {}): Promise<MediaStream> {
	const audio = constraints.audio || false;
	const video = constraints.video || false;
	const screen = constraints.screen || false;
	if (!(audio || video || screen)) {
		throw Error('At least one of audio, video or screen must be requested');
	}
	if (video && screen) {
		throw Error('video and screen cannot be both true at the same time');
	}

	if (!screen) {
		return getUserMedia({audio, video});
	}
	if (!audio) {
		return getDisplayMedia({video: screen});
	}
	const audioStream = await getUserMedia({audio});
	const screenStream = await getDisplayMedia({video: screen});
	return new MediaStream([...screenStream.getTracks(), ...audioStream.getTracks()]);
}
