import * as log from 'js-logger';
import { ConferenceMediaFeed } from '../conference-media/ConferenceMediaFeed';
import { RTCPeerConnectionAdapter } from '../RTCPeerConnectionAdapter';
import { checkRTCDependency } from '../utils';
const _room = Symbol('room');
const _medium = Symbol('medium');
const _pc = Symbol('pc');
/**
 * Media subscriber.
 * Used to manage remote media sent from Janus server.
 *
 * @extends ConferenceMediaFeed
 */
export class Subscriber extends ConferenceMediaFeed {
    /**
     * Create a new Subscriber.
     * @protected
     * @param {string} feed associated user name.
     * @param {string} medium remote media type.
     */
    constructor(feed, room, medium = 'sfuvideo', deps = {}) {
        super(feed);
        /**
         * Janus wrapper room
         * @private
         * @type {JanusRoom}
         */
        this[_room] = room;
        /**
         * Remote medium type
         * @private
         * @type {string}
         */
        this[_medium] = medium;
        this.deps = deps;
        checkRTCDependency(deps, 'RTCPeerConnection');
        checkRTCDependency(deps, 'RTCSessionDescription');
        checkRTCDependency(deps, 'MediaStream');
    }
    /**
     * Check if this Subscriber has the same feed and medium
     * properties.
     * @param {string} feed subscribed user name.
     * @param {string} medium remote medium type.
     * @return {boolean} true if feed and medium properties matches.
     */
    provides(feed, medium) {
        return this.feed === feed && this[_medium] === medium;
    }
    /**
     * Check if this is an audio subscription
     * @type {boolean}
     */
    get hasAudio() {
        return this[_medium] === 'sipcall';
    }
    /**
     * Indicate if {Subscriber} has been subscribed.
     * @type {boolean}
     */
    get subscribed() {
        return !!this[_pc] && this[_pc].connectionState === 'connected';
    }
    setRemoteStreamWithMedium(stream) {
        return this.setRemoteStream(stream, this[_medium]);
    }
    /**
     * Start subscription.
     * This method creates RTCPeerConnection and interchanges
     * messages with Janus wrapper server to establish multimedia conectivity.
     * @return {Promise} resolved on success
     */
    subscribe() {
        /* sip call media is received in local publisher as remote stream */
        if (this.hasAudio) {
            this.setRemoteStreamWithMedium(new this.deps.MediaStream());
            return Promise.resolve();
        }
        return this[_room].api.media.subscribe(this[_medium], this.feed).then((response) => {
            const pcConfig = {
                iceServers: this[_room].ice,
            };
            const pcConstraints = {
                'optional': [
                    { 'DtlsSrtpKeyAgreement': true },
                ],
            };
            this[_pc] = new RTCPeerConnectionAdapter(pcConfig, pcConstraints, this.deps.RTCPeerConnection, this.deps);
            this[_pc].onicecandidate = (evt) => {
                this[_room].api.media.trickle(this[_medium], evt.candidate, this.feed);
            };
            this[_pc].remoteStreamHandlers(this.setRemoteStreamWithMedium.bind(this));
            this[_pc].onconnectionstatechange = () => {
                if (this[_pc] && this[_pc].connectionState === 'closed') {
                    this[_pc] = null;
                }
            };
            return this[_pc].setRemoteDescription(new this.deps.RTCSessionDescription(response.jsep));
        }).then(() => {
            return this[_pc].createAnswer();
        }).then((answer) => {
            return this[_pc].setLocalDescription(answer).then(() => {
                return this[_room].api.media.answer(this[_medium], this.feed, answer.sdp);
            });
        }).catch((error) => {
            log.error('subscribe: got error', error);
            this.unsubscribe();
            throw error;
        });
    }
    /**
     * Stop subscription
     * @return {Promise} resolved on success
     */
    unsubscribe() {
        if (this[_medium] === 'sipcall') {
            this.setRemoteStreamWithMedium(null);
            return Promise.resolve();
        }
        if (this[_pc]) {
            this[_pc].close();
            this[_pc] = null;
        }
        this.setRemoteStreamWithMedium(null);
        return this[_room].api.media.unsubscribe(this[_medium], this.feed);
    }
}
