const priv = {
	context: Symbol('context'),
	timer: Symbol('timer'),
	retain: Symbol('retain'),
	running: Symbol('running'),
};

/**
 * Class that implements a Runnable thread
 *
 * When 'start' method is called '_thread' method will be called
 * periodically
 *
 * @example
 * let foo = 'bar';
 * let thread = new Thread(foo);
 * thread._thread = function() {
 *   console.error('this is', this);
 * }
 * thread.start(1000);
 *
 * > this is [String: 'bar']
 * > this is [String: 'bar']
 * > this is [String: 'bar']
 * > this is [String: 'bar']
 *
 * thread.stop()
 */
export class Thread {

	/**
	 * Class constructor
	 *
	 * @param {Object} context context used in _thread method
	 */
	constructor(context) {
		this[priv.timer] = null;
		this[priv.context] = context;
		this[priv.retain] = null;
		this[priv.running] = false;
	}

	/**
	 * Start thread execution with given period
	 *
	 * @param {Number} period time in ms between consecutive '_thread' method calls
	 */
	start(period) {
		if (!this[priv.running]) {
			return;
		}
		this._timer = setTimeout(() => {
			this._timer = null;
			let thread = this._thread.bind(this[priv.context]);
			thread().then((expires) => {
				this.start(expires);
			});
		}, period / 2);
	}

	/**
	 * Stop thread execution
	 *
	 * @return {Boolean} true if thread stoped, false if already stoped
	 */
	stop() {
		this[priv.running] = false;
		if (this._timer) {
			clearTimeout(this._timer);
			this._timer = null;
		}
	}

	/**
	 * Check if thread is running
	 *
	 * @return {Boolean} true if thread is running, false otherwise
	 */
	isRunning() {
		return this[priv.running];
	}

	/**
	 * Retained value
	 * @protected
	 * @type {Object}
	 */
	get retained() {
		return this[priv.retain];
	}

	/**
	 * Store an object to be retained periodically
	 * The expiration time is the one defined by expireField.
	 * When the retained element has such field with a value < 0, element should
	 * be updated inmediately. Values > 0 indicate the relative time in ms when
	 * element will expire. 0 or absence indicates that no updates are required.
	 * @param {Object} element object to retain
	 * @param {String} expireField field of element that indicates the expiration
	 * time in ms.
	 */
	retain(element, expireField) {
		this.release();
		this[priv.retain] = element;
		this[priv.running] = true;
		let expires = element[expireField] || 0;
		if (expires !== 0) {
			this.start(Math.max(expires, 0));
		}
	}

	/**
	 * Remove an object from being retained periodically
	 */
	release() {
		this[priv.retain] = null;
		this.stop();
	}

	/**
	 * @protected
	 * @return {Promise<Number>} next relative expiration time
	 */
	_thread() {
	}

}
