export class LoginAPI {
    constructor(client) {
        this.client = client;
    }
    /**
     * Authenticate at the wrapper and join a room
     * @param {string} username user name to register
     * @param {string} room room identifier
     * @param {string} token token assigned to user in room
     * @return {Promise} resolved on success
     */
    login(username, room, token) {
        return this.client.send('login', {
            username: username,
            token: token,
            room: room,
        });
    }
    /**
     * Leave a room at the wrapper
     * @return {Promise} resolved on success
     */
    logout() {
        return this.client.send('logout', {});
    }
}
