/**
 * Enum containing the possible status of chat message. Possible values:
 * - ERROR
 * - SENDING
 * - SENT
 * - RECEIVED
 * - DISPLAYED
 * - ACKNOWLEDGED
 * @type {Enum}
 */
export const ChatMessageStatus = {
	'ERROR': 'error',
	'SENDING': 'sending',
	'SENT': 'sent',
	'RECEIVED': 'received',
	'DISPLAYED': 'displayed',
	'ACKNOWLEDGED': 'acknowledged',
};
