import {bindMethods} from '../../../utils';
import {default as Logs} from '../../../Logs';
import {
	SignalingClient,
	CONNECTION_CLOSED as CLIENT_CONNECTION_CLOSED,
	CONNECTED as CLIENT_CONNECTED,
	RECONNECTING as CLIENT_RECONNECTING,
	RECONNECTED as CLIENT_RECONNECTED,
} from './signaling/Client';
import {EventEmitter2} from 'eventemitter2';

const log = Logs.instance.getLogger('SippoJS/Janus/Session');

export const SESSION_CONNECTED = 'connected';

export const RECONNECTING = 'reconnecting';

export const RECONNECTED = 'reconnected';

export const SESSION_CLOSED = 'closed';

export class Session extends EventEmitter2 {
	constructor(address, userName, token = ';', applicationToken = '', dependencies = {}) {
		super();
		this.dependencies = dependencies;

		this.signaling = null;

		this.address = address;

		this.username = userName;

		this.token = token;

		this.applicationToken = applicationToken;

		this.session = null;

		bindMethods(this, ['onSessionClosed']);
	}

	get registered() {
		return !!this.session;
	}

	get peerId() {
		return this.session.id;
	}

	async register({deviceId} = {}) {
		let signaling = new SignalingClient(this.address, this.dependencies);
		try {
			log.debug(`register ${this.username}`);
			this.deviceId = deviceId;
			if (this.registered) {
				throw new Error('already registered');
			}
			this.bindSignalingEvents(signaling);
			await signaling.init();
			await this.login(signaling, {deviceId: this.deviceId});
			this.onSessionCreated(signaling);
		} catch (error) {
			log.error('register failed with error', error);
			if (signaling && signaling.ready) {
				signaling.uninit();
			}
			throw new Error('registration failed');
		}
	}

	async unregister() {
		try {
			if (!this.registered) {
				throw new Error('not registered');
			}
			await this.signaling.register.logout();
			await this.signaling.uninit();
		} catch (error) {
			log.error('unregister failed with error', error);
			throw new Error('unregister failed');
		}
	}

	async suspend() {
		this.unbindSignalingEvents();
		await this.signaling.register.logout();
		await this.signaling.uninit();
		log.debug('session suspended');
	}

	async resume() {
		await this.signaling.init();
		await this.login(this.signaling, {deviceId: this.deviceId});
		this.bindSignalingEvents(this.signaling);
		log.debug('session resumed');
	}

	async login(signaling, {deviceId} = {}) {
		const registry = await signaling.register.login(
			this.username,
			`${this.token}${this.applicationToken}`,
			{
				deviceId,
			},
		);
		this.session = registry;
	}

	onSessionCreated(signaling) {
		log.debug('session created');
		this.signaling = signaling;
		this.emit(SESSION_CONNECTED);
	}

	onSessionClosed() {
		log.debug('session closed');
		this.unbindSignalingEvents();
		this.signaling = null;
		this.session = null;
		this.emit(SESSION_CLOSED);
	}

	bindSignalingEvents(signaling) {
		signaling.on(CLIENT_CONNECTED, () => {
			signaling.removeAllListeners(CLIENT_CONNECTED);
			signaling.on(CLIENT_RECONNECTING, this.onReconnecting.bind(this));
			signaling.on(CLIENT_RECONNECTED, this.onReconnected.bind(this));
		});
		signaling.on(CLIENT_CONNECTION_CLOSED, this.onSessionClosed);
	}

	onReconnecting() {
		this.emit(RECONNECTING);
	}

	async onReconnected() {
		try {
			await this.login(this.signaling, {deviceId: this.deviceId});
			this.emit(RECONNECTED, this);
		} catch (err) {
			this.onSessionClosed();
		}
	}

	unbindSignalingEvents() {
		if (this.signaling) {
			this.signaling.removeAllListeners(CLIENT_CONNECTED);
			this.signaling.removeAllListeners(CLIENT_RECONNECTING);
			this.signaling.removeAllListeners(CLIENT_RECONNECTED);
			this.signaling.removeAllListeners(CLIENT_CONNECTION_CLOSED);
		}
	}
}
