import {EventEmitter2} from 'eventemitter2';

import {bindMethods} from '../../utils';

const PATH = 'usersGroup';

function getEventData(data) {
	return {
		id: data.groupId,
		name: data.name,
		participants: data.participants,
		owner: `${data.owner.username}@${data.owner.domain}`,
	};
}

/** @private */
export class GroupService {
	constructor(wacRequest) {
		this.emitter = new EventEmitter2();
		this.wacRequest = wacRequest;
		bindMethods(this, ['onEvent', 'onParticipantEvent']);
		this.bindEventHandlers();
	}

	/** @private */
	bindEventHandlers() {
		this.wacRequest.emitter.on(PATH, this.onEvent);
		this.wacRequest.emitter.on(`${PATH}/participant`, this.onParticipantEvent);
	}

	/** @private */
	onEvent(event) {
		switch (event.method) {
			case 'put':
			case 'post':
				return this.emitter.emit('put', getEventData(event.body));
			case 'delete':
				return this.emitter.emit('delete', {id: event.body.groupId});
			// no default
		}
	}

	/** @private */
	onParticipantEvent(event) {
		switch (event.method) {
			case 'put':
				return this.emitter.emit('put', getEventData(event.body));
			case 'delete':
				return this.emitter.emit('put', getEventData(event.body));
			// no default
		}
	}

	async fetch() {
		const res = await this.wacRequest.get(PATH, {participating: true});
		return res.groupList.map(getEventData);
	}

	async create(name, participants) {
		const group = await this.wacRequest.post(PATH, {}, {name});
		const id = group.groupId;
		await Promise.all(participants.map(targetUserId =>
			this.wacRequest.post(`${PATH}/${id}/participants`, {}, {targetUserId})),
		);
		return {id, name, participants};
	}

	update(id, name, participants) {
		return this.wacRequest.put(`${PATH}/${id}`, {}, {id, name, participants});
	}

	delete(id) {
		return this.wacRequest.delete(`${PATH}/${id}`);
	}
}
