export class Settings {
	constructor(wacRequest) {
		this.wacRequest = wacRequest;
		this.pathname = '/usersettings';

		this.getSettings = this.getSettings.bind(this);
		this.setSetting = this.setSetting.bind(this);
	}

	getSettings() {
		return this._getAllSettings().then((settings) => {
			return this._parseSettings(settings);
		});
	}

	setSetting(key, value) {
		return this._getAllSettings().then((settings) => {
			let setting = settings.find(item => item.key === key);
			if (setting) {
				return this._updateSettings(setting.id, key, value);
			} else {
				return this._createSettings('wac', key, value);
			}
		});
	}

	_getAllSettings() {
		return this.wacRequest.get(this.pathname);
	}

	_updateSettings(id, key, value) {
		const fullPath = `${this.pathname}/${id}`;
		const body = {
			[key]: value,
		};
		return this.wacRequest.put(fullPath, {}, body);
	}

	_createSettings(source, key, value) {
		const body = {
			source,
			[key]: value,
		};
		return this.wacRequest.post(this.pathname, {}, body);
	}

	_parseSettings(settings) {
		if (!Array.isArray(settings)) {
			settings = [settings];
		}
		const body = {
			settings,
		};
		return this.wacRequest.post('/usersettingsparser', {}, body);
	}
}
