
import nanoid from 'nanoid';
import {ServerAPI} from './ServerAPI';

export class RoomsAPI extends ServerAPI {

	/**
	 * Get any room from server where user participates and return
	 * a list of Room instances
	 * @throws {ErrorResponse} if server returns an error
	 * @return {Promise<Array<object>>} resolved with server response.
	 */
	getRooms() {
		return this.request('getRooms', nanoid(), {})
			.then(response => response.payload);
	}

	/**
	 * Request a room creation on server and returns a Room instance
	 * @throws {ErrorResponse} if server returns an error
	 * @param {boolean} [recording=false] indicate if room should be recorded
	 * @return {Promise<Room>} resolved on success.
	 */
	createRoom(recording) {
		return this.request('createRoom', nanoid(), {
			payload: {
				record: recording,
			},
		}).then(response => response.payload);
	}

	/**
	 * Add participants to a room
	 * @throws {ErrorResponse} if server returns an error
	 * @param {string} id room identifier
	 * @param {Array<string>} participants list of user names to add
	 * to the room
	 * @return {Promise<object>} resolved on success with server response.
	 */
	addRoomParticipants(id, participants) {
		return this.request('update', nanoid(), {
			payload: {
				room: id,
				participants: {
					add: participants,
				},
			},
		}).then(response => response.payload);
	}

	/**
	 * Removes participants from a room
	 * @throws {ErrorResponse} if server returns an error
	 * @param {string} id room identifier
	 * @param {Array<string>} participants list of user names to remove
	 * from the room
	 * @return {Promise<object>} resolved on success with server response.
	 */
	removeRoomParticipants(id, participants) {
		return this.request('update', nanoid(), {
			payload: {
				room: id,
				participants: {
					remove: participants,
				},
			},
		}).then(response => response.payload);
	}

	/**
	 * Starts recording a room
	 * @throws {ErrorResponse} if server returns an error
	 * @param {string} id room identifier
	 * @return {Promise<object>} resolved on success with server response.
	 */
	startRecordingRoom(id) {
		return this.request('update', nanoid(), {
			payload: {
				room: id,
				record: false,
			},
		}).then(response => response.payload);
	}

	/**
	 * Stops recording a room
	 * @throws {ErrorResponse} if server returns an error
	 * @param {string} id room identifier
	 * @return {Promise<object>} resolved on success with server response.
	 */
	stopRecordingRoom(id) {
		return this.request('update', nanoid(), {
			payload: {
				room: id,
				record: false,
			},
		}).then(response => response.payload);
	}

	/**
	 * Close a room
	 * @throws {ErrorResponse} if server returns an error
	 * @return {Promise<object>} resolved on success with server response.
	 */
	leaveRoom(id) {
		return this.request('leaveRoom', nanoid(), {
			payload: {
				room: id,
			},
		}, 60);
	}

	sendDtmf(payload) {
		return this.request('dtmf', nanoid(), {
			payload,
		});
	}

	joinRoom(uri) {
		return this.request('joinRoom', nanoid(), {
			payload: {
				uri,
			},
		}).then(response => response.payload);
	}
}
