import { isEqual, pick } from 'lodash-es';
import { ConferenceMediaList } from '../conference-media/ConferenceMediaList';
import { Publisher } from './Publisher';
const _room = Symbol('room');
const _mediatypes = Symbol('mediatypes');
/**
 * Class that manage multiple local media publishers.
 * The main responsability is create and publish required Publisher
 * to satisfy desired mediatypes and unpublish those that are not
 * needed anymore.
 *
 * To a complete reference of 'mediatypes' structure see
 * getUserMediaWithMediatypes documentation.
 *
 * @extends ConferenceMediaList
 */
export class Publishers extends ConferenceMediaList {
    /**
     * Creates a new Publishers instance
     */
    constructor(room, deps) {
        super();
        /**
         * Janus room
         * @private
         */
        this[_room] = room;
        /**
         * Current mediatypes.
         * Used internally to restore previous mediatypes when an update fails.
         * Initialized to only audio to try requesting only audio by default if
         * initial request can not be satisfied.
         * @private
         * @type {object}
         */
        this[_mediatypes] = {
            audio: true,
        };
        this.deps = deps;
    }
    /**
     * Current mediatypes.
     * @type {object}
     */
    get mediatypes() {
        return this[_mediatypes];
    }
    /**
     * Update published mediatypes by creating and destroying managed
     * Publisher instances.
     * If an error is detected when Publisher list is updated previous state
     * is recovered.
     * @param {object} mediatypes new mediatypes to publish.
     * @param {boolean} [restoreOnErrors=true] try to restore previous publishers on error
     * @return {Promise} resolved on success
     */
    // @TODO: when an error occurs, it's needed to restore the publish
    update(mediatypes = {}, restoreOnErrors = true) {
        return Promise.resolve().then(() => {
            if (mediatypes.video && mediatypes.screen) {
                throw new Error('screen and camera can not be enabled at same time');
            }
            const remove = this._getUselessPublishers(mediatypes);
            return Promise.all(remove.map((publisher) => this._removeItem(publisher)));
        }).then(() => {
            return this.getPublishersAndStartToPublish(mediatypes);
        }).then(() => {
            this[_mediatypes] = mediatypes;
        }).catch((error) => {
            // Prevent loops
            if (mediatypes === this[_mediatypes] || !restoreOnErrors) {
                throw error;
            }
            // Restore previous constraints
            return this.update(this[_mediatypes]).then(() => {
                throw error;
            });
        });
    }
    getPublishersAndStartToPublish(mediatypes) {
        const add = this._getRequiredPublishers(mediatypes);
        return Promise.all(add.map(p => this._addItem(p)));
    }
    unpublishAll() {
        return Promise.all(this.items.map(publisher => this._removeItem(publisher)));
    }
    republishAllMediums() {
        return this.getPublishersAndStartToPublish(this.mediatypes);
    }
    /**
     * Return a list of those Publisher that are not needed to satisfy especified
     * constraints.
     * @private
     * @param {object} constraints
     * @return {Array<Publisher>} list of publisher not needed to update published
     * media to especified constraints.
     */
    _getUselessPublishers(constraints) {
        return this.items.filter((publisher) => {
            return !!Object.keys(publisher.constraints).find((name) => {
                return !isEqual(constraints[name], publisher.constraints[name]);
            });
        });
    }
    /**
     * Create a list of Publisher that are needed to satisfy especified constraints.
     * @private
     * @param {object} constraints
     * @return {Array<Publisher>} list of publisher needed to update published
     * media to especified constraints.
     */
    _getRequiredPublishers(constraints) {
        const add = [];
        for (const name in constraints) {
            const constraint = constraints[name];
            if (!constraint) {
                continue;
            }
            const isPublished = this.items.find((publisher) => {
                return isEqual(publisher.constraints[name], constraint);
            });
            if (!isPublished) {
                add.push(pick(constraints, name));
            }
        }
        return add.map((constraint) => this.createPublisher(constraint));
    }
    createPublisher(constraint) {
        const PublisherIF = this.deps.Publisher || Publisher;
        return new PublisherIF(null, this[_room], constraint, this.deps);
    }
    /**
     * @override
     */
    _addItem(publisher) {
        super._addItem(publisher);
        return publisher.publish().catch((error) => {
            return this._removeItem(publisher).then(() => {
                throw error;
            }).catch(() => {
                throw error;
            });
        });
    }
    /**
     * @override
     */
    _removeItem(publisher) {
        return publisher.unpublish().then(() => {
            super._removeItem(publisher);
        }).catch((error) => {
            super._removeItem(publisher);
            throw error;
        });
    }
}
