import bowser from 'bowser';
import {Set} from 'immutable';

export class CapabilitiesManager {
	constructor() {
		this.gatewayCapabilities = [];
		this.wacCapabilities = [];
		this.forbiddenCapabilities = [];
		this.regenerateCapabilities();
		this.onCapabilitiesUpdated = this.onCapabilitiesUpdated.bind(this);
	}

	start(capabilitiesService) {
		this.capabilitiesService = capabilitiesService;
		this.capabilitiesService.emitter.on('change', this.onCapabilitiesUpdated);
		return this.onCapabilitiesUpdated();
	}

	setGatewayCapabilities(capabilities) {
		this.gatewayCapabilities = capabilities;
		this.regenerateCapabilities();
	}

	getCapabilities() {
		return this.capabilities;
	}

	hasWacCapability(capability) {
		return this.wacCapabilities.includes(capability);
	}

	hasGatewayCapability(capability) {
		return this.gatewayCapabilities.includes(capability);
	}

	/** @private */
	async onCapabilitiesUpdated() {
		this.wacCapabilities = await this.capabilitiesService.getCapabilities();
		this.forbiddenCapabilities = await this.capabilitiesService.getForbiddenCapabilities();
		this.regenerateCapabilities();
	}

	/** @private */
	regenerateCapabilities() {
		this.capabilities = Set.of().withMutations((capabilities) => {
			capabilities.union(this.wacCapabilities);
			capabilities.union(this.gatewayCapabilities);
			capabilities.subtract(this.forbiddenCapabilities);
			if (!window.MediaRecorder) {
				capabilities.delete('local-recording');
			}
			if (window.location.protocol !== 'https:' || bowser.android || bowser.ios) {
				capabilities.delete('screen-sharing');
			}
		});
	}
}
