import {EventEmitter2} from 'eventemitter2';
import {bindMethods} from '../../utils';

const PATH = 'datapipe';

/** @private */
export class DatapipeService {
	constructor(wacRequest) {
		this.wacRequest = wacRequest;
		this.emitter = new EventEmitter2();
		bindMethods(this, ['onEvent', 'onInstanceEvent']);
		this.bindEventHandlers();
	}

	/** @private */
	bindEventHandlers() {
		this.wacRequest.emitter.on(PATH, this.onEvent);
		this.wacRequest.emitter.on([PATH, '*'], this.onInstanceEvent);
	}

	/** @private */
	onEvent(event) {
		switch (event.method) {
			case 'post':
				return this.emitter.emit('invite', event.body);
			// no default
		}
	}

	/** @private */
	onInstanceEvent(event) {
		switch (event.method) {
			case 'post':
				return this.emitter.emit('data', event.body);
			case 'put':
				return this.emitter.emit('update', event.body);
			case 'del':
				return this.emitter.emit('delete', event.body);
			// no default
		}
	}

	get() {
		return this.wacRequest.get(PATH);
	}

	create(label, participants) {
		return this.wacRequest.post(PATH, {}, {label, participants});
	}

	accept(id, session) {
		return this.wacRequest.put(`${PATH}/${id}`, {}, {session});
	}

	delete(id) {
		return this.wacRequest.delete(`${PATH}/${id}`);
	}

	send(id, data) {
		return this.wacRequest.post(`${PATH}/${id}`, {}, {data});
	}
}
