import {Enum} from '../utils';

/**
 * Enum containing the possible end reasons of a conference. Possible values:
 * - UNSPECIFIED
 * - NORMAL_CLEARING
 * - MEDIA_ACCESS_ERROR
 * - SERVER_ERROR
 * @type {Enum}
 */
export const ConferenceEndReason = new Enum([
	'UNSPECIFIED',
	'NORMAL_CLEARING',
	'MEDIA_ACCESS_ERROR',
	'SERVER_ERROR',
]);
