import { STREAM_ADDED, STREAM_REMOVED } from '../conference-media/ConferenceMediaFeed';
import { ConferenceMediaList } from '../conference-media/ConferenceMediaList';
import { StreamChangeReason } from '../Constants';
const withReason = (data, reason) => Object.assign({}, data, { reason });
export function HoldableConferenceMediaList(heldParticipants) {
    return new ConferenceMediaList({
        onStreamAdded(conferenceMediaList, stream) {
            const addReason = heldParticipants.isOnHold(stream.feed)
                ? StreamChangeReason.STREAM_CHANGE_UNHOLD
                : StreamChangeReason.STREAM_CHANGE_NEW;
            const data = withReason(stream, addReason);
            conferenceMediaList.emit(STREAM_ADDED, data);
        },
        onStreamRemoved(conferenceMediaList, stream) {
            const addReason = heldParticipants.isOnHold(stream.feed)
                ? StreamChangeReason.STREAM_CHANGE_HOLD
                : StreamChangeReason.STREAM_CHANGE_GONE;
            const data = withReason(stream, addReason);
            conferenceMediaList.emit(STREAM_REMOVED, data);
        }
    });
}
;
