
import {EventEmitter2} from 'eventemitter2';

const _listeners = Symbol('listeners');

/**
 * Like an EventEmitter, instances of this class allow us
 * register and unregister listeners for events, but it allows listening
 * for those messages that match a pattern.
 *
 * @example
 *   let x = new Emitter();
 *   x.addContentListener({foo: 'bar'}, (data) => {
 *     console.log('received data', data);
 *   });
 *   x.emit({foo: 'bar', hello: 'world'})
 *
 *   > received data {foo: 'bar', hello: 'world'}
 *
 * @extends EventEmitter2
 */
export class Emitter extends EventEmitter2 {

	/**
	 * Create a new Emitter and initialize internal properties
	 */
	constructor() {
		super();
		this[_listeners] = [];
	}

	/**
	 * Add an event listener that will be called when an event that matches
	 * provided filter is received
	 * @param {object} filter filter that should match emited events to call
	 * provided function
	 * @param {function} fn callback to invoke on each new match.
	 */
	addContentListener(filter, fn) {
		this[_listeners].push({
			filter: filter,
			fn: fn,
		});
	}

	/**
	 * Stop receiving events on a previously added listener.
	 * @param {function} fn callback to remove from listeners list.
	 */
	removeContentListener(fn) {
		this[_listeners] = this[_listeners].filter(x => x.fn !== fn);
	}

	/**
	 * Emit an event
	 * @param {string} event event name.
	 * @param {object} data event data.
	 */
	emit(event, data) {
		super.emit(event, data);
		if (!data) {
			return;
		}
		this[_listeners].forEach((listener) => {
			if (!this._match(data, listener.filter)) {
				return;
			}
			listener.fn(data);
		});
	}

	/**
	 * Check if given data matches provided filter.
	 * @private
	 * @param {object} item tested element.
	 * @param {object} filter tested filter.
	 * @return {boolean} true if item matches filter.
	 */
	_match(item, filter) {
		let isDifferent = Object.keys(filter).find((key) => {
			if (typeof filter[key] === 'object') {
				return !this._match(item[key], filter[key]);
			}
			return filter[key] !== item[key];
		});
		return !isDifferent;
	}
}
