import {EventEmitter2} from 'eventemitter2';
import nanoid from 'nanoid';
import {
	JanusRoom,
	ConnectionEvents,
	ParticipantEvents,
	ConferenceMediaFeed,
	ServerAddress,
} from '@quobis/wrapper-stack';

import {bindMethods} from '../../../utils';
import {ErrorResponse} from './signaling/errors';
import {
	ParticipantEvents as ConferenceParticipantEvents,
	StreamChangeEvents as ConferenceStreamChangeEvents,
} from '../ConferenceEvents';
import Logs from '../../../Logs';
import {getUserMediaWithMediatypes} from '../Media';

const log = Logs.instance.getLogger('SippoJS/Janus/Conference');

const {
	STREAM_ADDED,
	STREAM_REMOVED,
} = ConferenceMediaFeed;


const asLocalStream = ({feed, stream}) =>
	({stream, participant: feed});

const asRemoteStream = ({feed, stream, reason}) =>
	({stream, reason, participant: feed});

/**
 * Updated conference event.
 * Fired when a conference where user participates is updated.
 * @event UPDATED_CONFERENCE_EVENT
 * @type {object}
 */
export const UPDATED_CONFERENCE_EVENT = 'conference-updated';

/**
 * Destroyed conference event.
 * Fired when user leaves a conference.
 * @event DESTROYED_CONFERENCE_EVENT
 * @type {object}
 */
export const DESTROYED_CONFERENCE_EVENT = 'conference-destroyed';

/**
 * Expel conference event.
 * Fired when conference is detroyed by other user.
 * @event EXPEL_CONFERENCE_EVENT
 * @type {object}
 */
export const EXPEL_CONFERENCE_EVENT = 'expelled';

/**
 * Local streams changed event.
 * Fired when local streams changes.
 * @event LOCAL_STREAM_CHANGED
 * @type {object}
 * @property {Array<MediaStream>} streams current local streams.
 */
export const LOCAL_STREAM_CHANGED = ConferenceStreamChangeEvents.LOCAL_STREAM_CHANGED;

/**
 * New local stream event.
 * Fired when a new local stream arrives
 * @event LOCAL_STREAM_ADDED
 * @type {object}
 * @property {string} participant gatewayUsername of the stream owner
 * @property {MediaStream} stream new local stream
 */
export const LOCAL_STREAM_ADDED = ConferenceStreamChangeEvents.LOCAL_STREAM_ADDED;

/**
 * Local stream gone event
 * Fired when a local stream stops being available
 * @event LOCAL_STREAM_REMOVED
 * @type {object}
 * @property {string} participant gatewayUsername of the stream owner
 * @property {MediaStream} stream removed local stream
 */
export const LOCAL_STREAM_REMOVED = ConferenceStreamChangeEvents.LOCAL_STREAM_REMOVED;

/**
 * Remote streams changes event.
 * Fired when streams associated with a remote peer changes.
 * @event REMOTE_STREAM_CHANGED
 * @type {object}
 * @property {string} participant user name of remote peer.
 * @property {Array<MediaStream>} streams current local streams.
 */
export const REMOTE_STREAM_CHANGED = ConferenceStreamChangeEvents.REMOTE_STREAM_CHANGED;

/**
 * Remote streams added event
 * Fired when a new remote stream arrives
 * @event REMOTE_STREAM_ADDED
 * @type {object}
 * @property {string} participant user name of remote peer.
 * @property {MediaStream} streams new remote stream
 * @property {string} [reason='NEW'|'UNHOLD'] reason of stream addition
 */
export const REMOTE_STREAM_ADDED = ConferenceStreamChangeEvents.REMOTE_STREAM_ADDED;

/**
 * Remote streams remove event
 * Fired when a new remote stops being available
 * @event REMOTE_STREAM_REMOVED
 * @type {object}
 * @property {string} participant user name of remote peer.
 * @property {MediaStream} streams removed stream
 * @property {string} [reason='GONE'|'HOLD'] reason of stream removal
 */
export const REMOTE_STREAM_REMOVED = ConferenceStreamChangeEvents.REMOTE_STREAM_REMOVED;

/**
 * Fired when the user has to invite to other participant
 * to implement an unattended call transfer
 * @event TRANSFERING_CALL
 * @type {object}
 * @property {string} to username to send an invitation to
 * @property {string} callTransferID identifier of
 * an unattended call transfer.
 * @property {string} roomId identifier of a room
 * @property {string} mediatypes identifier of a room
 */
export const TRANSFERING_CALL = 'transferingCall';

const _ice = Symbol('ice');
const _id = Symbol('id');
const _invite = Symbol('invite');
const _room = Symbol('room');
const _ownwer = Symbol('owner');
const _participants = Symbol('participants');
const _record = Symbol('recording');
const _left = Symbol('left');
const _pendingInvites = Symbol('pendingInvites');
const _server = Symbol('server');
const _session = Symbol('session');
const _token = Symbol('token');
const _uninitedRoomPromise = Symbol('uninitedRoom');

/**
 * @fires UPDATED_CONFERENCE_EVENT
 * @fires DESTROYED_CONFERENCE_EVENT
 * @fires EXPEL_CONFERENCE_EVENT
 * @fires LOCAL_STREAM_CHANGED
 * @fires REMOTE_STREAM_CHANGED
 * @fires TRANSFERING_CALL
 * @extends EventEmitter2
 */
export class Conference extends EventEmitter2 {

	/**
	 * Creates a new instance and initialize with provided
	 * data
	 */
	constructor(dependencies = {}) {
		super();

		/**
		 * Server client
		 * @private
		 * @type {Session}
		 */
		this[_session] = dependencies.session || null;

		/**
		 * Janus client
		 * @private
		 * @type {JanusRoom}
		 */
		this[_room] = null;
		/**
		 * Room identifier
		 * @private
		 * @type {string}
		 */
		this[_id] = '';

		/**
		 * Invitation when user doesn't joined the conference.
		 * Contains next properties:
		 *  - id: invitation id,
		 *  - from: inviter username.
		 *  - mediatypes: invitation offered mediatypes
		 * @private
		 * @type {object}
		 */
		this[_invite] = null;

		/**
		 * Room owner username
		 * @private
		 * @type {string}
		 */
		this[_ownwer] = '';

		/**
		 * User identifiers that participate on a conference.
		 * @private
		 * @type {Array<string>}
		 */
		this[_participants] = [];

		/**
		 * Indicates if conference is being recorded
		 * @private
		 * @type {boolean}
		 */
		this[_record] = false;

		/**
		 * Conference server
		 * @private
		 * @type {string}
		 */
		this[_server] = '';

		/**
		 * Conference access token
		 * @private
		 * @type {string}
		 */
		this[_token] = '';

		/**
		 * Conference ice servers
		 * @private
		 * @type {Array<object>}
		 */
		this[_ice] = [];

		/**
		 * Contains a list of pending invitations (still not accepted/rejected
		 * by remote peer) transaction identifiers. Used to cancel a previous invitation
		 * and determine if an user has been invited.
		 * @private
		 * @type {object}
		 */
		this[_pendingInvites] = {};

		/**
		 * True if leave method was called
		 * @private
		 * @type {object}
		 */
		this[_left] = false;

		this.parallelSipExtension = null;
		this.sipUri = null;

		this[_uninitedRoomPromise] = null;

		bindMethods(this, [
			'onRoomUpdated',
			'onRoomDestroyed',
			'onInviteCancel',
			'onStreamAdded',
			'onStreamRemoved',
			'onTransferingCallToPeer',
			'onParticipantAdded',
			'onParticipantRemoved',
			'onParticipantHold',
			'onParticipantUnhold',
		]);

		this.dependencies = dependencies;
	}

	/**
	 * Conference identifier
	 * @type {string}
	 */
	get id() {
		return this[_id];
	}

	/**
	 * Conference creator username
	 * @type {string}
	 */
	get owner() {
		return this[_ownwer];
	}

	/**
	 * Indicate if invited
	 * @type {boolean}
	 */
	get invited() {
		return !!this[_invite];
	}

	/**
	 * Returns an object with the invitation (with the properties "id" an "from")
	 * Needed to know who is inviting us in anonymous rooms
	 */
	get invite() {
		return this[_invite];
	}

	/**
	 * Indicate if joined
	 * @type {boolean}
	 */
	get joined() {
		return !!this[_room];
	}

	/**
	 * Server session.
	 * @type {Session}
	 */
	set session(session) {
		if (this[_session] && this[_session].signaling) {
			this[_session].signaling.removeContentListener(this.onRoomUpdated);
			this[_session].signaling.removeContentListener(this.onRoomDestroyed);
			this[_session].signaling.removeContentListener(this.onInviteCancel);
			this[_session].signaling.removeContentListener(this.onTransferingCallToPeer);
			this._uninitRoom();
		}
		this[_session] = session;
		if (this[_session]) {
			this[_session].signaling.addContentListener({
				request: 'room-update',
				payload: {room: this.id},
			}, this.onRoomUpdated);
			this[_session].signaling.addContentListener({
				request: 'room-destroyed',
				payload: {room: this.id},
			}, this.onRoomDestroyed);
			this[_session].signaling.addContentListener({
				cancel: 'invitation',
			}, this.onInviteCancel);
			this[_session].signaling.addContentListener({
				request: 'transfering-call-to-peer',
				payload: {roomId: this.id},
			}, this.onTransferingCallToPeer);
		}
	}

	/**
	 * Indicates if conference has been created by local user
	 * @type {boolean}
	 */
	isOwner() {
		return this[_session] && this.owner === this[_session].username;
	}

	/**
	 * Indicates if the conference has owner
	 * @type {boolean}
	 */
	hasOwner() {
		return this.owner.length !== 0;
	}

	/**
	 * Conference participants, expressed as a list of
	 * current user identifiers
	 * @type {Array<string>}
	 */
	get participants() {
		return this[_participants];
	}

	/**
	 * Current local streams.
	 * @type {Array<ConferenceMediaStream>}
	 */
	get localStreams() {
		if (!this.joined) {
			throw new Error('not joined');
		}
		return this[_room].media.localStreams;
	}

	/**
	 * Current remote streams.
	 * @type {Array<ConferenceMediaStream>}
	 */
	get remoteStreams() {
		if (!this.joined) {
			throw new Error('not joined');
		}
		return this[_room].media.remoteStreams;
	}

	/**
	 * Current conference streams.
	 * @type {Array<ConferenceMediaStream>}
	 */
	get streams() {
		return this.localStreams.concat(this.remoteStreams);
	}

	/**
	 * Local hold
	 * Stop publishing and subscribing to the media in the conference
	 * @return {Promise} resolved on success
	 */
	hold() {
		return this[_room].hold();
	}

	/**
	* Stop local hold
	 * Republishes all previously published media, subscribes
	 * to previously subscribed media and notifies new status to
	 * other participants.
s 	 * @return {Promise} resolved on success
	 */
	unhold() {
		return this[_room].unhold();
	}

	/**
	 * Check if conference is on hold state.
	 * @type {boolean}
	 */
	get isOnHold() {
		return this[_room].isOnHold();
	}

	/**
	 * Suspend conference:
	 *  - cancel any pending invitation sent.
	 *  - decline invitation if we was invited to this conference.
	 * @return {Promise} resolved on success.
	 */
	suspend() {
		return Promise.resolve().then(() => {
			Object.keys(this[_pendingInvites]).forEach(participant => this.cancelInvitation(participant));
			if (this.invited) {
				this.onRoomDestroyed();
			}
		});
	}

	/**
	 * Resume conference
	 * @return {Promise} resolved on success.
	 */
	resume() {
		return Promise.resolve();
	}

	/**
	 * Join this conference and start sending local media defined by mediatypes
	 * parameter.
	 * @throws {Error} when already joined.
	 * @param {object} mediatypes local media used after joining the conference.
	 * @return {Promise} resolved on success.
	 */
	join(mediatypes) {
		if (this.joined) {
			return Promise.reject(new Error('already joined'));
		}
		return Promise.resolve().then(() => {
			if (this.invited) {
				this[_session].signaling.invites.accept(this[_invite].id, this[_invite].from);
				this[_invite] = null;
			}
			return this._initRoom();
		}).then(() => {
			return this.update(mediatypes);
		});
	}

	/**
	 * Add participant to this conference.
	 * Only conference owner can do this.
	 * @throws {Error} if participant is already present on conference or a pending
	 * invitation exists.
	 * @param {string} participant user name to add.
	 * @param {object} mediatypes the mediatypes requested to the new participant.
	 * @param {string} [requestId] the ID of the request that generated this invitation.
	 * @param {object} [context] arbitrary context that can be sent to invites
	 * @return {Promise<boolean>} resolved with a boolean value that indicates if
	 * remote accepts the invitation.
	 */
	async inviteParticipant(participant, mediatypes, requestId, context) {
		log.debug('inviteParticipant', participant);
		if (this[_pendingInvites][participant]) {
			log.debug('inviteParticipant/pendintInvites');
			throw new Error(`participant ${participant} has a pending invitation`);
		}
		const id = nanoid();
		return new Promise(async (resolve, reject) => {
			try {
				this[_pendingInvites][participant] = {id, resolve, reject};
				log.debug('inviteParticipant/Promise<>/pendingInvites saved, id=', id);
				log.debug(id, this[_session].username, participant, this.id, mediatypes, requestId);
				const response = await this[_session].signaling.invites.invite(
					id,
					this[_session].username,
					participant,
					this.id,
					mediatypes,
					requestId,
					context,
				);
				log.debug('inviteParticipant/Promise<>/invite request sent');
				resolve({
					code: 200,
					whoAccepted: response ? response.whoAccepted : undefined,
				});
			} catch (error) {
				log.error('inviteParticipant/Promise<>/error invite request', error);
				if (error instanceof ErrorResponse) {
					resolve({code: error.message});
				} else {
					reject({code: error});
				}
			} finally {
				delete this[_pendingInvites][participant];
			}
		});
	}

	/**
	 * Cancel an invite
	 * @param {string} participant user name to decline.
	 */
	cancelInvitation(participant) {
		try {
			this.cancelPendingInvite(participant);
		} catch (error) {
			log.error(error);
		}
	}

	cancelPendingInvite(participant) {
		let pending = this[_pendingInvites][participant];
		if (!pending) {
			throw new Error(`participant ${participant} has no pending invitation`);
		}
		delete this[_pendingInvites][participant];
		this[_session].signaling.invites.cancelInvitation(pending.id, this[_session].username);
		pending.reject(new Error('invite canceled'));
	}

	/**
	 * Removes a participant from this conference.
	 * Only conference owner can do this.
	 * @param {string} participant user name to remove.
	 * @return {Promise} resolved when remote user leaves conference.
	 */
	expelParticipant(participant) {
		return this[_session].signaling.rooms.removeRoomParticipants(this.id, [participant])
			.then(response => this._updateWithServerData(response));
	}

	/**
	 * Leaves this conference or rejects a previous invitation.
	 * @param {number} errorCode the rejection cause when an invitation is received
	 * and the conference has not been joined.
	 * @throws {Error} when already joined or a previous invitation was not received.
	 * @return {Promise<Conference>} resolved on success with self reference.
	 */
	async leave(errorCode) {
		// TODO(antonio) remove this hack to maintain async call
		await Promise.resolve();
		this[_left] = true;
		this._declineInviteIfNeeded(errorCode);
		if (this.joined || this.isOwner()) {
			await this._tryToLeaveSignalingRoom();
		}
		return this;
	}

	_declineInviteIfNeeded(errorCode) {
		if (this.invited) {
			this._sendDeclineMessage(errorCode);
			this[_invite] = null;
		}
	}

	_sendDeclineMessage(errorCode) {
		this[_session].signaling.invites.decline(
			this[_invite].id,
			this[_invite].from,
			errorCode,
		);
	}

	async _tryToLeaveSignalingRoom() {
		try {
			await this[_session].signaling.rooms.leaveRoom(this.id);
		} catch (ignored) {
			await this._uninitRoom();
		}
	}

	/**
	 * Update local media types.
	 * @param {object} mediatypes required mediatypes.
	 * @return {Promise} resolved on success.
	 */
	update(mediatypes = {}) {
		if (!this.joined) {
			return Promise.reject(new Error('not joined'));
		}
		if (this._mediaUpdateInProgress) {
			return Promise.reject(new Error('media update already in progress'));
		}
		this._mediaUpdateInProgress = true;
		this[_room].api.media.setParallelSipExtension(this.parallelSipExtension);
		return this[_room].update(mediatypes).finally(() => {
			this._mediaUpdateInProgress = false;
		});
	}

	/**
	 * True if conference is being recorded
	 * @type {boolean}
	 */
	get recording() {
		return this[_record];
	}

	/**
	 * Starts recording this conference
	 * @return {Promise<Conference>} resolved on success with self reference.
	 */
	startRecording() {
		return this[_session].signaling.rooms.startRecordingRoom(this.id)
			.then(response => this._updateWithServerData(response));
	}

	/**
	 * Stops conference recording
	 * @return {Promise<Conference>} resolved on success with self reference.
	 */
	stopRecording() {
		return this[_session].signaling.rooms.stopRecordingRoom(this.id)
			.then(response => this._updateWithServerData(response));
	}

	/**
	 * Create an unattended call transfer towards a user in the system
	 * @param {string} participant user name to call.
	 * @param {object} mediatypes the mediatypes requested to the new participant.
	 * @return {Promise}
	 */
	unattendedCallTransfer(participant, mediatypes) {
		let id = nanoid();
		return this[_session].signaling.callTransfer.unattendedCallTransfer(
			id,
			participant,
			this.id,
			mediatypes,
		);
	}


	/**
	 * Accept inviting to other participant to make an unattended call transfer
	 * @param {string} callTransferID identifier of a call transfer
	 * @return {Promise}
	 */
	acceptCallTransfer(callTransferID) {
		let id = nanoid();
		return this[_session].signaling.callTransfer.acceptCallTransfer(
			id,
			callTransferID,
		);
	}


	createDependencies() {
		const defaultDependencies = {
			RTCPeerConnection,
			RTCSessionDescription,
			getUserMedia: getUserMediaWithMediatypes,
			window,
			MediaStream,
		};
		return Object.assign(defaultDependencies, this.dependencies);
	}


	/**
	 * Initialize Janus client after joining a conference.
	 * @private
	 * @return {Promise} resolved on success
	 */
	_initRoom() {
		this[_room] = this.createJanusRoom();
		this[_room].media.on(STREAM_ADDED, this.onStreamAdded);
		this[_room].media.on(STREAM_REMOVED, this.onStreamRemoved);
		this[_room].emitter.on(ConnectionEvents.CONNECTION_CLOSED, this.onRoomDestroyed);
		this[_room].emitter.on(ParticipantEvents.PARTICIPANT_ADDED, this.onParticipantAdded);
		this[_room].emitter.on(ParticipantEvents.PARTICIPANT_REMOVED, this.onParticipantRemoved);
		this[_room].emitter.on(ParticipantEvents.PARTICIPANT_HOLD, this.onParticipantHold);
		this[_room].emitter.on(ParticipantEvents.PARTICIPANT_UNHOLD, this.onParticipantUnhold);
		return this[_room].init().catch((error) => {
			return this._uninitRoom().then(() => {
				throw error;
			});
		});
	}

	createJanusRoom() {
		const server = this[_server].wss || this[_server].ws
			? ServerAddress.fromWebsocket(this[_server].wss || this[_server].ws)
			: ServerAddress.fromSocketIo(this[_server].https || this[_server].http);
		const {room: injectedRoom} = this.dependencies;
		return injectedRoom || new JanusRoom({
			server,
			username: this[_session].username,
			room: this[_id],
			token: this[_token],
			ice: this[_ice],
			deps: this.createDependencies(),
		});
	}

	/**
	 * Deinitialize Janus client after leaving a conference.
	 * @private
	 * @return {Promise} resolved on success
	 */
	_uninitRoom() {
		if (this[_uninitedRoomPromise]) {
			return this[_uninitedRoomPromise];
		}
		this[_uninitedRoomPromise] = this._uninitRoomIfExists()
			.finally(() => this._resetUninitedRoomPromise());
		return this[_uninitedRoomPromise];
	}

	_uninitRoomIfExists() {
		if (this[_room]) {
			this._removeRoomListeners();
			return this[_room].uninit()
				.finally(() => {
					this._removeRoomMediaListeners();
					this._resetCurrentRoom();
				});
		}
		return Promise.resolve();
	}

	_removeRoomListeners() {
		const {emitter} = this[_room];
		emitter.off(ConnectionEvents.CONNECTION_CLOSED, this.onRoomDestroyed);
		emitter.off(ParticipantEvents.PARTICIPANT_ADDED, this.onParticipantAdded);
		emitter.off(ParticipantEvents.PARTICIPANT_REMOVED, this.onParticipantRemoved);
		emitter.off(ParticipantEvents.PARTICIPANT_HOLD, this.onParticipantHold);
		emitter.off(ParticipantEvents.PARTICIPANT_UNHOLD, this.onParticipantUnhold);
	}

	_removeRoomMediaListeners() {
		const {media} = this[_room];
		media.off(STREAM_ADDED, this.onStreamAdded);
		media.off(STREAM_REMOVED, this.onStreamRemoved);
	}

	_resetCurrentRoom() {
		this[_room] = null;
	}

	_resetUninitedRoomPromise() {
		this[_uninitedRoomPromise] = null;
	}

	/**
	 * Updates instance properties with data returned by a
	 * signaling server
	 * @private
	 * @param {object} data server data
	 * @return {Conference} self reference.
	 */
	_updateWithServerData(data) {
		if (data.room) {
			this[_id] = data.room;
		}
		if (data.owner) {
			this[_ownwer] = data.owner;
		}
		if (data.participants) {
			this[_participants] = data.participants;
		}
		if (data.record) {
			this[_record] = data.record;
		}
		if (data.token) {
			this[_token] = data.token;
		}
		if (data.server) {
			this[_server] = data.server;
		}
		if (data.ice) {
			this[_ice] = data.ice;
		}
		if (data.invite) {
			this[_invite] = data.invite;
		}
		if (typeof data.left !== 'undefined') {
			this[_left] = data.left;
		}
		if (data.parallelSipExtension) {
			this.parallelSipExtension = data.parallelSipExtension;
		}
		if (data.sipUri) {
			this.sipUri = data.sipUri;
		}
		return this;
	}

	sendDtmf(value) {
		return this[_session].signaling.rooms.sendDtmf({
			digit: value,
			sipUri: this.parallelSipExtension || this.sipUri,
		});
	}

	/**
	 *
	 */
	getParticipants() {
		return this[_room].getParticipants();
	}

	/**
	 * Callback called when a room update event is received.
	 * @private
	 */
	onRoomUpdated(data) {
		this._updateWithServerData(data.payload);
		this.emit(UPDATED_CONFERENCE_EVENT, this);
	}

	/**
	 * Callback called when a destroyed room event is received.
	 * @private
	 */
	onRoomDestroyed() {
		if (this[_session] && (this.joined || this.isOwner())) {
			this[_session].signaling.rooms.leaveRoom(this.id)
				.catch((e) => {
					if (typeof e.message === 'string' && !e.message.match(/Session closed/) || e.message !== 403) {
						throw e;
					}
				});
		}
		this.emit(DESTROYED_CONFERENCE_EVENT);
		if (!this[_left]) {
			this.emit(EXPEL_CONFERENCE_EVENT);
		}
	}

	/**
	 * Callback called when an invitation is canceled
	 * @private
	 */
	onInviteCancel(evt) {
		if (!!this[_invite] && this[_invite].id === evt.requestID) {
			this[_invite] = null;
			this.onRoomDestroyed();
		}
	}

	/**
	 * Callback called when a ConferenceMediaStream is added.
	 * @private
	 * @param {ConferenceMediaStream} stream
	 */
	onStreamAdded(stream) {
		const event = stream.local
			? {name: LOCAL_STREAM_ADDED, data: asLocalStream(stream)}
			: {name: REMOTE_STREAM_ADDED, data: asRemoteStream(stream)};
		this.onStreamChanged(stream);
		this.emit(event.name, event.data);
	}

	/**
	 * Callback called when a ConferenceMediaStream is removed.
	 * @private
	 * @param {ConferenceMediaStream} stream
	 */
	onStreamRemoved(stream) {
		const event = stream.local
			? {name: LOCAL_STREAM_REMOVED, data: asLocalStream(stream)}
			: {name: REMOTE_STREAM_REMOVED, data: asRemoteStream(stream)};
		this.onStreamChanged(stream);
		this.emit(event.name, event.data);
	}

	/**
	 * Callback called when a ConferenceMediaStream has changed.
	 * @private
	 * @param {ConferenceMediaStream} stream
	 */
	onStreamChanged(stream) {
		if (stream.local) {
			let streams = this.joined ? this.localStreams.map(item => item.stream) : [];
			this.emit(LOCAL_STREAM_CHANGED, {
				streams: streams,
			});
		} else {
			let streams = this.remoteStreams
				.filter(item => item.feed === stream.feed)
				.map(item => item.stream);
			if (streams.length > 1) {
				streams = streams.filter(s => !!s.getTracks().length);
			}
			this.emit(REMOTE_STREAM_CHANGED, {
				participant: stream.feed,
				streams: streams,
				reason: stream.reason,
			});
		}
	}

	/**
	 * Callback called when an invitation is canceled
	 * @private
	 */
	onTransferingCallToPeer(data) {
		this.transferingCall = true;
		this.emit(TRANSFERING_CALL, data);
	}

	/**
	 * Callback called when a new participant joins the room
	 * @private
	 */
	onParticipantAdded(data) {
		this.emit(ConferenceParticipantEvents.PARTICIPANT_ADDED, data);
	}

	/**
	 * Callaback called when a participant leaves the room
	 * @private
	 */
	onParticipantRemoved(data) {
		this.emit(ConferenceParticipantEvents.PARTICIPANT_REMOVED, data);
	}

	/**
	 * Callback called when a remote participant does a hold
	 * @private
	 */
	onParticipantHold(data) {
		this.emit(ConferenceParticipantEvents.PARTICIPANT_HOLD, data);
	}

	/**
	 * Callback called when a remote participant stops a hold
	 * @private
	 */
	onParticipantUnhold(data) {
		this.emit(ConferenceParticipantEvents.PARTICIPANT_UNHOLD, data);
	}
}
