import EventEmitter from 'eventemitter3';

type Action = () => Promise<void>;

interface IntervalEvents {
	error: [Error];
}

export type IntervalId = ReturnType<typeof setInterval>;

export interface Scheduler {
	setInterval(handler: () => void, timeout: number): IntervalId;
	clearInterval(handlerId: IntervalId): void;
}

export class EventInterval {
	public emitter: EventEmitter<IntervalEvents>;
	private handler?: IntervalId;
	private action: Action;
	private interval: number;
	private scheduler: Scheduler;

	public constructor(
		action: Action,
		interval: number,
		scheduler: Scheduler,
	) {
		this.action = action;
		this.interval = interval;
		this.scheduler = scheduler;
		this.emitter = new EventEmitter();
	}
	public run(): void {
		this.handler = this.scheduler.setInterval(async () => {
			try {
				await this.action();
			} catch (e) {
				this.emitter.emit('error', e);
			}
		}, this.interval);
	}

	private clear(): void {
		if (this.handler) {
			this.scheduler.clearInterval(this.handler);
		}
	}

	public stop(): void {
		this.clear();
	}
}
