export class Login {
	constructor(sapiClient) {
		this.pathname = '/login';
		this.sapiClient = sapiClient;
	}

	async doLoginToken(provider, token, deviceId) {
		return this.sapiClient.login(provider, token, deviceId);
	}

	async recoverSession(token) {
		return this.sapiClient.recover(token);
	}
}
