import {Enum} from '../utils';

/**
 * Enum containing the possible status of a call. Possible values:
 * - UNCONNECTED
 * - TRYING
 * - QUEUED
 * - CONNECTING
 * - CONNECTED
 * - HOLD
 * - REMOTE_HOLD
 * - RECONNECTING
 * - DISCONNECTED
 * @type {Enum}
 */
export const CallStatus = new Enum([
	'UNCONNECTED',
	'TRYING',
	'QUEUED',
	'CONNECTING',
	'CONNECTED',
	'HOLD',
	'REMOTE_HOLD',
	'RECONNECTING',
	'DISCONNECTED',
]);
