import {EventEmitter2} from 'eventemitter2';
import {bindMethods} from '../../utils';
import Logs from '../../Logs';

const log = Logs.instance.getLogger('SippoJS/WacStack/MeetingService');
const PATH = '/meetings/';

export class MeetingService {
	constructor(wacRequest) {
		this.wacRequest = wacRequest;
		this.emitter = new EventEmitter2();
		this.emitter.setMaxListeners(0);
		bindMethods(this, ['onEvent', 'onInstanceEvent']);
		this.bindEventHandlers();
	}

	/** @private */
	bindEventHandlers() {
		this.wacRequest.emitter.on('meetings', this.onEvent);
		this.wacRequest.emitter.on('meetings/*', this.onInstanceEvent);
	}

	/** @private */
	onEvent(event) {
		switch (event.method) {
			default:
				log.warn('Unhandled WAC event: ', event);
		}
	}

	/** @private */
	onInstanceEvent(event) {
		switch (event.method) {
			case 'put':
				this.emitter.emit('update', event.body);
				break;
			default:
				log.warn('Unhandled WAC event: ', event);
		}
	}

	get({language, phone, name, validUntil} = {}) {
		return this.wacRequest.get(PATH, {language, phone, name, validUntil});
	}

	create({name, participants, language, validSince, validUntil} = {}) {
		return this.wacRequest.post(PATH, {}, {name, participants, language, validSince, validUntil});
	}

	update(id, {name, participants, language, validSince, validUntil} = {}) {
		return this.wacRequest.put(PATH + id, {sendInvites: true}, {
			name, participants, language, validSince, validUntil,
		});
	}

	delete(id) {
		return this.wacRequest.delete(PATH + id);
	}
}
