import {difference} from 'lodash-es';
import {EventEmitter2 as EventEmitter} from 'eventemitter2';
import {WapiState} from '@quobis/wapi-client';

import {bindMethods} from '../../utils';
import Logs from '../../Logs';
import {LoginError, WacInternalError, AlreadyLoggedError} from '../errors';

import {WacRequest} from './WacRequest';
import {SapiClient} from './SapiClient';
import {XmppClient} from './XmppClient';
import {CallAttemptService} from './CallAttemptService';
import {CallService} from './CallService';
import {CapabilitiesService} from './CapabilitiesService';
import {ChatService} from './ChatService';
import {ConferenceLogService} from './ConferenceLogService';
import {DatapipeService} from './DatapipeService';
import {FileSharingService} from './FileSharingService';
import {GroupService} from './GroupService';
import {MeetingService} from './MeetingService';
import {PhonebookService} from './PhonebookService';
import {PushNotificationsService} from './PushNotificationsService';
import {Resolver} from './Resolver';

import {CallStats as CallStatsService} from './call-stats/CallStats';
import {Contact as ContactService} from './contact/Contact';
import {Credentials as CredentialsService} from './credentials/Credentials';
import {Sessions as SessionsService} from './sessions/Sessions';
import {Login as LoginService} from './login/Login';
import {Presence as PresenceService} from './presence/Presence';
import {Recording as RecordingService} from './recording/Recording';
import {RemoteLog as RemoteLogService} from './remote-log/RemoteLog';
import {Settings as SettingsService} from './settings/Settings';
import {User as UserService} from './user/User';

const log = Logs.instance.getLogger('SippoJS/WacStack');
const events = Logs.instance.getLogger('SippoJS/WacStack events');

export class WacStack extends EventEmitter {
	constructor(wacUri, wacRequest, sapiClient) {
		super();
		// TODO: Specify a correct number here
		this.setMaxListeners(0);
		this.WACVersion = '';
		this.wacUri = wacUri;
		this.urls = {};
		this._reconnecting = false;
		this.wacRequest = wacRequest || new WacRequest();
		this.sapiClient = sapiClient || new SapiClient();
		bindMethods(this, [
			'onCapabilitiesChange',
			'onClose',
			'onConnection',
			'onWapiStateChange',
		]);
	}

	// FIXME: kept for not breaking WacStack API, but this needs some love
	generateWacProxyActions() {
		// Actions which will be managed by the services
		this.actionProxy = {
			// CONTACT
			'getContacts': this.contactService.getContacts,
			'addContact': this.contactService.addContact,
			'updateContact': this.contactService.updateContact,
			'removeContact': this.contactService.removeContact,
			'searchContact': this.contactService.searchContact,
			'invalidateContacts': this.contactService.invalidateContacts,
			'insertMany': this.contactService.insertMany,
			'getContactsWacUser': this.contactService.getContactsWacUser,
			// SETTINGS
			'getSettings': this.settingsService.getSettings,
			'setSetting': this.settingsService.setSetting,
			// STATS/CALL
			'sendStats': this.callStatsService.sendStats,
			// SESSION
			'getSessions': this.sessionsService.getSessions,
			'retainSession': this.sessionsService.retain,
			'releaseSession': this.sessionsService.release,
			// CREDENTIALS
			'getCredentials': this.credentialsService.getCredentials,
			// USER
			'getUser': this.userService.getUser,
			'updateUser': this.userService.updateUser,
			'validatePhone': this.userService.validatePhone,
		};
		for (const action in this.actionProxy) {
			this[action] = this.actionProxy[action];
		}
	}

	onWapiStateChange(state) {
		log.debug(`Wapi connection transitioned to ${state}`);
		switch (state) {
			case WapiState.CONNECTED:
				this.onConnection();
				break;
			case WapiState.DISCONNECTED:
				// FIXME we need more information from underlying wapi client about the reason
				this.onClose('io server disconnect');
				break;
			default:
				log.debug('No handler for this wapi state transition');
		}
	}

	async start() {
		const res = await fetch(`${this.wacUri}/.well-known/host-meta.json`);
		const data = await res.json();
		for (const element of data.links) {
			this.urls[element.rel] = element.href;
		}
		this.wacRequest.urls = this.urls;
		this.sapiClient.urls = this.urls;
		this.generateWacProxyActions();
	}

	async stop() {
		if (this.getCurrentCredential()) {
			this.releaseCredential();
		}
		if (this.getCurrentSession()) {
			this.releaseSession();
		}
		if (this.wacRequest) {
			this.wacRequest.uninit();
		}
		if (this.wacRequest.wapi.getState() === WapiState.DISCONNECTED) {
			return this;
		}
		this.wacRequest.wapi.emitter.off('state', this.onWapiStateChange);
		const xmpp = this._chatService ? this._chatService.stop() : Promise.resolve();
		await Promise.all([this.wacRequest.wapi.disconnect(), xmpp]);
	}

	loginToken(provider, token, deviceId) {
		return this.processLogin(this.loginService.doLoginToken(provider, token, deviceId));
	}

	async processLogin(loginPromise) {
		try {
			const wapiToken = await loginPromise;
			this._wapiToken = wapiToken;
			this._username = wapiToken.session.userObj.username;
			this._domain = wapiToken.session.userObj.domain;
			this.wacRequest.token = wapiToken.access_token;
			this.wacRequest.init();
			this.wacRequest.wapi.emitter.on('state', this.onWapiStateChange);
			await this.wacRequest.wapi.connect();
			this.capabilitiesService = new CapabilitiesService(this.wacRequest);
			this.capabilitiesService.emitter.on('change', this.onCapabilitiesChange);
			await this.onCapabilitiesChange();
			return wapiToken;
		} catch (error) {
			switch (error.message) {
				case 403:
					throw new LoginError();
				case 409:
					throw new AlreadyLoggedError();
				case 500:
					throw new WacInternalError();
				default:
					throw error;
			}
		}

	}

	recoverSession(token) {
		return this.processLogin(this.loginService.recoverSession(token));
	}

	getCurrentSession() {
		return this.sessionsService && this.sessionsService.retained;
	}

	getCurrentCredential() {
		return this.credentialsService && this.credentialsService.retained;
	}

	retainCredential(credential) {
		this.credentialsService.retain(credential);
		let session = this.sessionsService.retained;
		if (session.registered !== credential.id) {
			session.registered = credential.id;
			session.expires = -1;
			this.sessionsService.retain(session);
		}
	}

	releaseCredential() {
		let session = this.sessionsService.retained;
		session.registered = '';
		session.expires = -1;
		this.sessionsService.retain(session);
		this.credentialsService.release();
	}

	async suspendSession() {
		const xmpp = this._chatService ? this._chatService.suspend() : Promise.resolve();
		const session = this._sessionsService ? this._sessionsService.suspend() : Promise.resolve();
		await Promise.all([session, xmpp]);
	}

	async resumeSession() {
		const xmpp = this._chatService ? this._chatService.resume() : Promise.resolve();
		const session = this._sessionsService ? this._sessionsService.resume() : Promise.resolve();
		await Promise.all([session, xmpp]);
	}

	getVersion() {
		return [
			{name: 'WAPI', version: 'unknown'},
			{name: 'WAC', version: 'unknown'},
		];
	}

	/**
	 * Update our presence state
	 * @param [Presence] presence object
	 * @return {Promise<Object>} Submited presence or an error object.
	 */
	updatePresence(address, presence) {
		return this.presenceService.update(address, presence);
	}

	isReconnecting() {
		return this._reconnecting;
	}

	async onCapabilitiesChange() {
		const capabilities = await this.capabilitiesService.getCapabilities();
		const forbiddenCapabilities = await this.capabilitiesService.getForbiddenCapabilities();
		if (difference(capabilities, forbiddenCapabilities).includes('log')) {
			this.remoteLogService.enable();
		} else {
			this.remoteLogService.disable();
		}
	}

	onConnection() {
		if (this._reconnecting) {
			this.emit('reconnected');
			this._reconnecting = false;
			let session = this.sessionsService.retained;
			if (session) {
				session.expires = -1;
				this.sessionsService.retain(session);
			}
		}
	}

	onClose(reason) {
		log.warn('WAC API closed: ' + reason);
		if (reason === 'io server disconnect') {
			this.emit('disconnected');
		} else {
			this._reconnecting = true;
			this.emit('reconnecting');
			switch (reason) {
				case 'transport close':
					this.emit('qs-transport-close');
					break;
				case 'ping timeout':
					this.emit('qs-connection-lost');
					break;
				default:
					this.emit('qs-unknown-close');
					break;
			}
		}
	}

	trace(method, args) {
		return log.debug('trace: ' + method, args);
	}

	emit(...args) {
		events.debug(args);
		super.emit.apply(this, args);
	}

	get callAttemptService() {
		if (!this._callAttemptService) {
			this._callAttemptService = new CallAttemptService(this.wacRequest);
		}
		return this._callAttemptService;
	}

	get callService() {
		if (!this._callService) {
			this._callService = new CallService(this.wacRequest);
		}
		return this._callService;
	}

	get callStatsService() {
		if (!this._callStatsService) {
			this._callStatsService = new CallStatsService(this.wacRequest);
		}
		return this._callStatsService;
	}

	get chatService() {
		if (!this._chatService) {
			const xmppUri = this.urls['urn:xmpp:alt-connections:websocket'];
			const xmppClient = new XmppClient(xmppUri, this._wapiToken.access_token, `${this._username}@${this._domain}`.toLowerCase());
			const xmppPushUri = this.urls['urn:quobis:xmpp:push'];
			this._chatService = new ChatService(this.wacRequest, xmppClient, xmppPushUri);
		}
		return this._chatService;
	}

	get conferenceLogService() {
		if (!this._conferenceLog) {
			this._conferenceLog = new ConferenceLogService(this.wacRequest);
		}
		return this._conferenceLog;
	}

	get contactService() {
		if (!this._contactService) {
			this._contactService = new ContactService(this.wacRequest);
			this._contactService.emitter.on('qs-contact-add', evt => this.emit('qs-contact-add', evt));
			this._contactService.emitter.on('qs-contact-add-many', evt => this.emit('qs-contact-add-many', evt));
			this._contactService.emitter.on('qs-contact-update', evt => this.emit('qs-contact-update', evt));
			this._contactService.emitter.on('qs-contact-remove', evt => this.emit('qs-contact-remove', evt));

		}
		return this._contactService;
	}

	get credentialsService() {
		if (!this._credentialsService) {
			this._credentialsService = new CredentialsService(this.wacRequest);
		}
		return this._credentialsService;
	}

	get datapipeService() {
		if (!this._datapipeService) {
			this._datapipeService = new DatapipeService(this.wacRequest);
		}
		return this._datapipeService;
	}

	get groupService() {
		if (!this._groupService) {
			this._groupService = new GroupService(this.wacRequest);
		}
		return this._groupService;
	}

	get fileSharingService() {
		if (!this._fileSharingService) {
			const fileUploadUri = this.urls['urn:quobis:fileupload:websocket'];
			this._fileSharingService = new FileSharingService(this.wacRequest, fileUploadUri, this._wapiToken);
		}
		return this._fileSharingService;
	}

	get loginService() {
		if (!this._loginService) {
			this._loginService = new LoginService(this.sapiClient);
		}
		return this._loginService;
	}

	get meetingService() {
		if (!this._meetingService) {
			this._meetingService = new MeetingService(this.wacRequest);
		}
		return this._meetingService;
	}

	get phonebookService() {
		if (!this._phonebookService) {
			this._phonebookService = new PhonebookService(this.wacRequest);
		}
		return this._phonebookService;
	}

	get pushNotificationsService() {
		if (!this._pushNotifications) {
			this._pushNotifications = new PushNotificationsService(this.wacRequest);
		}
		return this._pushNotifications;
	}

	get resolver() {
		if (!this._resolver) {
			this._resolver = new Resolver(this.wacRequest);
		}
		return this._resolver;
	}

	get presenceService() {
		if (!this._presenceService) {
			this._presenceService = new PresenceService(this.wacRequest);
		}
		return this._presenceService;
	}

	get recordingService() {
		if (!this._recordingService) {
			this._recordingService = new RecordingService(this.wacRequest);
		}
		return this._recordingService;
	}

	get remoteLogService() {
		if (!this._remoteLogService) {
			this._remoteLogService = new RemoteLogService(this.wacRequest);
		}
		return this._remoteLogService;
	}

	get sessionsService() {
		if (!this._sessionsService) {
			this._sessionsService = new SessionsService(this.wacRequest);
			this._sessionsService.emitter.on('disconnected', evt => this.emit('disconnected', evt));
		}
		return this._sessionsService;
	}

	get settingsService() {
		if (!this._settingsService) {
			this._settingsService = new SettingsService(this.wacRequest);
		}
		return this._settingsService;
	}

	get userService() {
		if (!this._userService) {
			this._userService = new UserService(this.wacRequest);
		}
		return this._userService;
	}
}
