import {bindMethods} from '../../../utils';
import {Conferences} from './Conferences';
import EventClass from '../EventClass';
import {
	Session,
	SESSION_CLOSED,
	RECONNECTING as SESSION_RECONNECTING,
	RECONNECTED as SESSION_RECONNECTED,
} from './Session';

const CAPABILITIES = [
	'audio',
	'video',
	'audio-call',
	'video-call',
	'audio-video-call',
	'media-update',
	'multicall',
	'conferences',
	'conference-hold',
];

class AlreadyRegisteredError extends Error {
	constructor(message) {
		super(message);
		this.name = 'Already registered';
	}
}

export class Janus extends EventClass {
	constructor(dependencies = {}) {
		super();
		this.dependencies = dependencies;

		/**
		 * Conference management
		 * @private
		 * @type {Conferences}
		 */
		this.conferences = new Conferences(dependencies);

		/**
		 * Signaling server {Session}
		 * @private
		 * @type {Session}
		 */
		this.session = null;

		bindMethods(this, ['onSessionClosed', 'onSessionReconnected', 'onSessionReconnecting']);
	}

	get conference() {
		return this.conferences;
	}

	getSession() {
		return this.session;
	}

	start() {
		this.trigger('qs-ready');
		return Promise.resolve();
	}

	capabilities() {
		return CAPABILITIES;
	}

	async register(credential, {deviceId}) {
		try {
			if (this.session) {
				throw new AlreadyRegisteredError('already registered or registering');
			}
			this.session = new Session(
				credential.authserver,
				credential.username,
				credential.token,
				credential.applicationToken,
				this.dependencies,
			);
			await this.session.register({deviceId});
			await this.onSessionCreated();
			this.trigger('qs-register-success');
		} catch (error) {
			if (!(error instanceof AlreadyRegisteredError)) {
				this.session = null;
			}
			this.trigger('qs-register-error');
			throw error;
		}
	}

	async unregister() {
		try {
			if (!this.session || !this.session.registered) {
				throw new Error('not registered');
			}
			await this.session.unregister();
			await this.onSessionClosed();
			this.trigger('qs-unregister-success');
		} catch (error) {
			this.trigger('qs-unregister-error');
			throw error;
		}
	}

	suspendSession() {
		return this.conferences.suspend().then(() => this.session.suspend());
	}

	resumeSession() {
		return this.session.resume().then(() => this.conferences.resume());
	}

	version() {
		return [];
	}

	/**
	 * Method called internally when session was created
	 * @private
	 */
	onSessionCreated() {
		this.session.on(SESSION_CLOSED, this.onSessionClosed);
		this.session.on(SESSION_RECONNECTING, this.onSessionReconnecting);
		this.session.on(SESSION_RECONNECTED, this.onSessionReconnected);
		return this.conferences.init(this.session).then(() => {
			this.trigger('qs-registered');
		});
	}

	/**
	 * Method called internally when session is reconnecting
	 * @private
	 */
	onSessionReconnecting() {
		this.trigger('qs-reconnecting');
	}

	/**
	 * Method called internally when session is reconnected
	 * @private
	 */
	onSessionReconnected() {
		this.trigger('qs-reconnected');
	}

	/**
	 * Method called internally when session is no longer available
	 * @private
	 */
	onSessionClosed() {
		this.conferences.uninit();
		if (this.session) {
			this.session.off(SESSION_CLOSED, this.onSessionClosed);
			this.session.off(SESSION_RECONNECTING, this.onSessionReconnecting);
			this.session.off(SESSION_RECONNECTED, this.onSessionReconnected);
			this.session = null;
			this.trigger('qs-unregistered');
		}
	}
}
