import io from 'socket.io-client';
import SocketIoFileClient from '@quobis/socket.io-file-client';
import {EventEmitter2} from 'eventemitter2';

import {bindMethods} from '../../utils/bindMethods';

const PATH = '/filesharing/';

/** @protected */
export class FileSharingService {
	constructor(wacRequest, fileUploadUrl, token) {
		this.wacRequest = wacRequest;
		this.fileUploadUrl = fileUploadUrl;
		this.token = token;
		this.socketsPromise = null;

		this.emitter = new EventEmitter2();

		bindMethods(this, [
			'onFile',
			'onDisconnect',
			'onStart',
			'onStream',
			'onComplete',
			'onAbort',
			'onError',
		]);
	}

	async createSocket() {
		let url = new URL(this.fileUploadUrl);
		const socket = new io(`${url.protocol}//${url.hostname}:${url.port}`, {path: url.pathname});
		socket.once('connect', () => {
			socket.on('disconnect', this.onDisconnect);
			socket.emit('authentication', {[this.token.token_type]: this.token.access_token});
		});
		await new Promise(resolve => socket.once('authenticated', resolve));
		const fileUploader = new SocketIoFileClient(socket);
		await new Promise(resolve => fileUploader.on('ready', resolve));
		fileUploader.off('ready');
		socket.on('file', this.onFile);
		fileUploader.on('start', this.onStart);
		fileUploader.on('stream', this.onStream);
		fileUploader.on('complete', this.onComplete);
		fileUploader.on('abort', this.onAbort);
		fileUploader.on('error', this.onError);
		return [socket, fileUploader];
	}

	getSockets() {
		if (!this.socketsPromise) {
			this.socketsPromise = this.createSocket();
		}
		return this.socketsPromise;
	}

	async onDisconnect() {
		const [socket, fileUploader] = await this.socketsPromise;
		this.socketsPromise = null;
		socket.off('disconnect', this.onDisconnect);
		socket.off('file', this.onFile);
		fileUploader.off('start', this.onStart);
		fileUploader.off('stream', this.onStream);
		fileUploader.off('complete', this.onComplete);
		fileUploader.off('abort', this.onAbort);
		fileUploader.off('error', this.onError);
	}

	onFile(event) {
		this.emitter.emit('file', event);
	}

	onStart(event) {
		this.emitter.emit('start', event);
	}

	onStream(event) {
		this.emitter.emit('stream', event);
	}

	onComplete(event) {
		this.emitter.emit('complete', event);
	}

	onAbort(event) {
		this.emitter.emit('abort', event);
	}

	onError(event) {
		this.emitter.emit('error', event);
	}

	async upload(file) {
		const [, uploader] = await this.getSockets();
		const [id] = uploader.upload([file]);
		return id;
	}

	async abort(id) {
		const [, uploader] = await this.getSockets();
		uploader.abort(id);
	}

	createSharedFile(key) {
		return this.wacRequest.post(PATH, {}, {key}).then(res => res.publicUrl);
	}
}
