
import {ServerAPI} from './ServerAPI';

export class CallTransferAPI extends ServerAPI {

	/**
	 * Try to perform an unattended call transfer
	 * @throws {ErrorResponse} when server returns an error.
	 * @param {string} username who must be inserted in conference
	 * @param {string} roomId room identifier where invited user should join.
	 * @return {Promise} resolved with server response on success.
	 */
	unattendedCallTransfer(id, username, roomId, mediatypes) {
		return this.request('unattendedCallTransfer', id, {
			to: username,
			roomId: roomId,
			mediatypes: mediatypes,
		});
	}

	/**
	 * Accept performing the call transfer
	 * @throws {ErrorResponse} when server returns an error.
	 * @param {string} callTransferID call transfer identifier
	 * @return {Promise} resolved with server response on success.
	 */
	acceptCallTransfer(id, callTransferID) {
		return this.request('acceptCallTransfer', id, {
			callTransferID,
		});
	}
}
