import {EventEmitter2} from 'eventemitter2';
import {bindMethods} from '../../utils';
import Logs from '../../Logs';

const log = Logs.instance.getLogger('SippoJS/WacStack/CallAttemptService');
const PATH = '/callAttempts/';

export class CallAttemptService {
	constructor(wacRequest) {
		this.wacRequest = wacRequest;
		this.emitter = new EventEmitter2();
		this.emitter.setMaxListeners(0);
		bindMethods(this, ['onEvent', 'onInstanceEvent']);
		this.bindEventHandlers();
	}

	/** @private */
	bindEventHandlers() {
		this.wacRequest.emitter.on('callAttempts', this.onEvent);
		this.wacRequest.emitter.on('callAttempts/*', this.onInstanceEvent);
	}

	/** @private */
	onEvent(event) {
		switch (event.method) {
			case 'post':
				this.emitter.emit('created', event.body);
				break;
			default:
				log.warn('Unhandled WAC event: ', event);
		}
	}

	/** @private */
	onInstanceEvent(event) {
		switch (event.method) {
			case 'delete':
				this.emitter.emit('rejected', event.body);
				break;
			default:
				log.warn('Unhandled WAC event: ', event);
		}
	}

	accept(id) {
		return this.wacRequest.put(PATH + id, {}, {accepted: true});
	}

	reject(id) {
		return this.wacRequest.put(PATH + id, {}, {accepted: false});
	}

}
