import { EventEmitter2 } from 'eventemitter2';
import { without } from 'lodash-es';
import { bindMethods } from '../utils';
import { STREAM_ADDED, STREAM_REMOVED } from './ConferenceMediaFeed';
import { DefaultConferenceStreamHandler } from './DefaultConferenceStreamHandler';
const _list = Symbol('list');
/**
 * Collection of ConferenceMediaFeed items.
 *
 * Captures and forwards any ConferenceMediaFeed event and provides getters
 * to obtain local and remote streams.
 *
 * @fires STREAM_ADDED
 * @fires STREAM_REMOVED
 *
 * @extends EventEmitter2
 */
export class ConferenceMediaList extends EventEmitter2 {
    /**
     * Creates a new ConferenceMediaList instance
     */
    constructor(streamEventHandler) {
        super();
        /**
         * Media list.
         * @private
         * @type {Array<ConferenceMediaFeed|ConferenceMediaList>}
         */
        this[_list] = [];
        this.streamEventHandler = streamEventHandler;
        const createStreamEventHandler = () => {
            if (streamEventHandler) {
                return streamEventHandler;
            }
            return DefaultConferenceStreamHandler();
        };
        this.streamEventHandler = createStreamEventHandler();
        bindMethods(this, [
            'onStreamAdded',
            'onStreamRemoved',
        ]);
    }
    /**
     * Access managed ConferenceMediaFeed elements.
     * @return {Array<ConferenceMediaFeed|ConferenceMediaList>}
     */
    get items() {
        return this[_list];
    }
    /**
     * Get local streams.
     * @type {Array<ConferenceMediaStream>}
     */
    get localStreams() {
        let streams = [];
        this[_list].forEach((item) => {
            if (item.localStream) {
                streams.push(item.localStream);
            }
            if (item.localStreams) {
                streams = streams.concat(item.localStreams);
            }
        });
        return streams;
    }
    /**
     * Get remote streams.
     * @type {Array<ConferenceMediaStream>}
     */
    get remoteStreams() {
        let streams = [];
        this[_list].forEach((item) => {
            if (item.remoteStream) {
                streams.push(item.remoteStream);
            }
            if (item.remoteStreams) {
                streams = streams.concat(item.remoteStreams);
            }
        });
        return streams;
    }
    /**
     * Add a ConferenceMediaFeed to the list of managed ConferenceMediaFeed.
     * This method also initialize ConferenceMediaFeed event listeners.
     * @private
     * @param {ConferenceMediaFeed|ConferenceMediaList} item elemen to add.
     * @return {Promise} resolved on success
     */
    _addItem(item) {
        if (!this[_list].includes(item)) {
            item.on(STREAM_ADDED, this.onStreamAdded);
            item.on(STREAM_REMOVED, this.onStreamRemoved);
            this[_list].push(item);
        }
        return Promise.resolve();
    }
    /**
     * Removes a ConferenceMediaFeed from the list of managed ConferenceMediaFeed.
     * This method also deinitialize ConferenceMediaFeed event listeners.
     * @private
     * @param {ConferenceMediaFeed|ConferenceMediaList} item elemen to add.
     * @return {Promise} resolved on success
     */
    _removeItem(item) {
        if (this[_list].includes(item)) {
            item.off(STREAM_ADDED, this.onStreamAdded);
            item.off(STREAM_REMOVED, this.onStreamRemoved);
            this[_list] = without(this[_list], item);
        }
        return Promise.resolve();
    }
    /**
     * Callback called when a ConferenceMediaFeed notifies a new stream.
     * @private
     * @param {ConferenceMediaStream} stream notified stream
     */
    onStreamAdded(stream) {
        this.streamEventHandler.onStreamAdded(this, stream);
    }
    /**
     * Callback called when a ConferenceMediaFeed notifies that a stream is
     * not available anymore.
     * @private
     * @param {ConferenceMediaStream} stream notified stream
     */
    onStreamRemoved(stream) {
        this.streamEventHandler.onStreamRemoved(this, stream);
    }
}
