/**
 * Enum containing the possible states of chat participant. Possible values:
 * - ACTIVE
 * - INACTIVE
 * - GONE
 * - COMPOSING
 * - PAUSED
 * @type {Enum}
 */
export const ChatParticipantState = {
	ACTIVE: 'active',
	INACTIVE: 'inactive',
	GONE: 'gone',
	COMPOSING: 'composing',
	PAUSED: 'paused',
};
