import * as log from 'js-logger';
import { JANUS_EVENTS } from './JanusEvents';
export function BaseJanusFeedEventHandler() {
    const onJanusFeed = (room, data) => {
        switch (data.event) {
            case JANUS_EVENTS.JANUS_FEED_NEW:
                return onNewFeed(room, data);
            case JANUS_EVENTS.JANUS_FEED_GONE:
                return onFeedGone(room, data);
            default:
                return Promise.resolve();
        }
    };
    const onNewFeed = (room, data) => {
        return room.getSubscribers().subscribe(data.feed.feed, data.feed.medium)
            .catch(() => { });
    };
    const onFeedGone = (room, data) => {
        const subscribers = room.getSubscribers();
        return subscribers.unsubscribe(data.feed.feed, data.feed.medium)
            .catch(() => { })
            .then(() => {
            // @TODO: should maintain this unsubscription
            if (data.feed.medium === 'sipcall' && isSubscribedToVideo(subscribers, data.feed.feed)) {
                return subscribers.unsubscribe(data.feed.feed, 'sfuvideo').catch((error) => {
                    log.error(`Unable to unsubscribe from feed ${data.feed.feed} with error: ${error}`);
                });
            }
        });
    };
    const isSubscribedToVideo = (subscribers, feed) => !!subscribers.get(feed, 'sfuvideo');
    return {
        onJanusFeed,
    };
}
;
