import EventEmitter from 'eventemitter3';
import {bindMethods} from '../../utils';
import Logs from '../../Logs';

const log = Logs.instance.getLogger('SippoJS/WacStack/Resolver');
const PATH = 'resolver';

export class Resolver {
	constructor(wacRequest) {
		this.wacRequest = wacRequest;
		this.emitter = new EventEmitter();
		bindMethods(this, ['onEvent']);
		this.bindEventHandlers();
	}

	/** @private */
	bindEventHandlers() {
		this.wacRequest.emitter.on([PATH, '**'], this.onEvent);
	}

	/** @private */
	onEvent(event) {
		switch (event.method) {
			case 'put':
				this.emitter.emit('updated', event.body);
				break;
			default:
				log.warn('Unhandled event: ', event);
		}
	}

	getResolvedUsers() {
		return this.wacRequest.get(`/${PATH}/user/`);
	}

	resolveUsers(addresses) {
		return this.wacRequest.post(`/${PATH}/user/`, {}, addresses);
	}

	updateResolvedUsers(addresses) {
		return this.wacRequest.put(`/${PATH}/user/`, {}, addresses);
	}

	getUserByGatewayUsername(username) {
		return this.wacRequest.post(`/${PATH}/userByGatewayUsername/`, {}, {username: username});
	}

	getGatewayUsernameByUser(userId) {
		return this.wacRequest.post(`/${PATH}/gatewayUsernameByUser/`, {}, {id: userId});
	}
}
