export function HeldParticipantsState() {
    let participants = [];
    const isOnHold = (user) => participants.includes(user);
    return {
        isOnHold,
        addParticipant(user) {
            if (isOnHold(user)) {
                return;
            }
            participants.push(user);
        },
        removeParticipant(user) {
            const isDifferentUser = u1 => u2 => u1 !== u2;
            participants = participants.filter(isDifferentUser(user));
        },
        participants() {
            return participants;
        }
    };
}
