import * as qs from 'query-string';

export class Recording {
	constructor(wacRequest) {
		this.wacRequest = wacRequest;
		this.pathname = '/recordings';

		this.getRecordings = this.getRecordings.bind(this);
		this.startRecording = this.startRecording.bind(this);
		this.stopRecording = this.stopRecording.bind(this);
	}

	getRecordings(filter) {
		let params = '';
		if (!!Object.keys(filter).length && typeof filter === 'object') {
			params = qs.stringify(filter);
		}
		return this.wacRequest.get(this.pathame, params);
	}

	startRecording(id, filename, source) {
		const fullPath = `${this.pathname}/start/${id}`;
		let body = {};
		if (filename) {
			body.filename = filename;
		}
		if (source) {
			body.source = source;
		}
		return this.wacRequest.put(`${fullPath}`, {}, body);
	}

	stopRecording(id) {
		const fullPath = `${this.pathname}/stop/${id}`;
		return this.wacRequest.put(`${fullPath}`, {}, {});
	}
}
