import { HOLD_MESSAGE_TYPES } from '../api/HoldAPI';
import { ParticipantEvents, ParticipantStatus } from '../Constants';
import { createRoomInterface } from '../RoomInterface';
export function HoldableRoom(room, holdState) {
    const publishers = room.getPublishers();
    const subscribers = room.getSubscribers();
    const { hold: apiHold } = room.api;
    const { heldParticipants, savedSubscriptions } = holdState;
    apiHold.emitter.on(HOLD_MESSAGE_TYPES.HOLD, (eventData) => {
        heldParticipants.addParticipant(eventData.user);
        room.emitter.emit(ParticipantEvents.PARTICIPANT_HOLD, eventData.user);
    });
    apiHold.emitter.on(HOLD_MESSAGE_TYPES.UNHOLD, (eventData) => {
        heldParticipants.removeParticipant(eventData.user);
        room.emitter.emit(ParticipantEvents.PARTICIPANT_UNHOLD, eventData.user);
    });
    const isOnHold = () => holdState.isHolding();
    const hold = async () => {
        if (holdState.isHolding()) {
            throw new Error('Can not hold, already held');
        }
        if (publishers.items.length == 0) {
            throw new Error('Can not hold, not publishing');
        }
        await apiHold.hold();
        await publishers.unpublishAll();
        await subscribers.unsubscribeAll();
        holdState.hold();
    };
    const unhold = async () => {
        if (!holdState.isHolding()) {
            throw new Error('Can not unhold, not holding');
        }
        return publishers.republishAllMediums()
            .then(() => subscribers.subscribeMany(savedSubscriptions.getSavedSubscriptions()))
            .then(() => apiHold.unhold())
            .then(() => holdState.unhold());
    };
    const uninit = async () => {
        if (holdState.isHolding()) {
            await apiHold.unhold();
            holdState.unhold();
        }
        return room.uninit();
    };
    const getParticipants = async () => {
        const participants = await room.getParticipants();
        const participantsOnHold = participants.filter(isParticipantInHold);
        participantsOnHold.forEach(({ user }) => heldParticipants.addParticipant(user));
        return participants;
    };
    const isParticipantInHold = ({ status }) => status === ParticipantStatus.PARTICIPANT_ON_HOLD;
    return Object.assign(createRoomInterface(room), {
        hold,
        unhold,
        uninit,
        isOnHold,
        getParticipants,
    });
}
;
