import { EventEmitter2 } from 'eventemitter2';
import { ConferenceMediaStream } from './ConferenceMediaStream';
/**
 * Stream added event.
 * Fired when a new stream is available.
 * @event STREAM_ADDED
 * @type {ConferenceMediaStream}
 */
export const STREAM_ADDED = 'streamAdded';
/**
 * Stream removed event.
 * Fired when a stream becomes unavailable.
 * @event STREAM_REMOVED
 * @type {ConferenceMediaStream}
 */
export const STREAM_REMOVED = 'streamRemoved';
const _feed = Symbol('feed');
const _localStream = Symbol('localStream');
const _remoteStream = Symbol('remoteStream');
/**
 * Class that manages local and remote streams associated
 * with a Conference Media feed.
 *
 * @fires STREAM_ADDED
 * @fires STREAM_REMOVED
 *
 * @extends EventEmitter2
 */
export class ConferenceMediaFeed extends EventEmitter2 {
    /**
     * Creates a new ConferenceMediaFeed instance.
     * @param {string} feed associated user name.
     */
    constructor(feed) {
        super();
        /**
         * Subscribed user name
         * @private
         * @type {string}
         */
        this[_feed] = feed;
        /**
         * Local user media
         * @private
         * @type {ConferenceMediaStream}
         */
        this[_localStream] = null;
        /**
         * Remote user media
         * @private
         * @type {ConferenceMediaStream}
         */
        this[_remoteStream] = null;
    }
    /**
     * User name associated with this media.
     * @type {string}
     */
    get feed() {
        return this[_feed];
    }
    /**
     * Get published stream.
     * @type {ConferenceMediaStream}
     */
    get localStream() {
        return this[_localStream];
    }
    /**
     * Get remote stream (if any).
     * @type {ConferenceMediaStream}
     */
    get remoteStream() {
        return this[_remoteStream];
    }
    /**
     * Updates local stream and fires STREAM_REMOVED and/or
     * STREAM_ADDED events
     * @protected
     * @param {MediaStream} stream new local stream
     */
    setLocalStream(stream, medium) {
        if (this[_localStream]) {
            const oldStream = this[_localStream];
            this[_localStream] = null;
            this.emit(STREAM_REMOVED, oldStream);
        }
        if (stream) {
            const janusMedia = new ConferenceMediaStream(this.feed, true, stream, medium);
            this[_localStream] = janusMedia;
            this.emit(STREAM_ADDED, janusMedia);
        }
    }
    /**
     * Updates remote stream and fires STREAM_ADDED and/or
     * STREAM_REMOVED events
     * @protected
     * @param {MediaStream} stream new remote stream
     */
    setRemoteStream(stream, medium) {
        if (this[_remoteStream]) {
            const oldStream = this[_remoteStream];
            this[_remoteStream] = null;
            this.emit(STREAM_REMOVED, oldStream);
        }
        if (stream) {
            const janusMedia = new ConferenceMediaStream(this.feed, false, stream, medium);
            this[_remoteStream] = janusMedia;
            this.emit(STREAM_ADDED, janusMedia);
        }
    }
}
