import { STREAM_ADDED, STREAM_REMOVED, } from './ConferenceMediaFeed';
export function DefaultConferenceStreamHandler() {
    return {
        onStreamAdded(conferenceMediaList, stream) {
            conferenceMediaList.emit(STREAM_ADDED, stream);
        },
        onStreamRemoved(conferenceMediaList, stream) {
            conferenceMediaList.emit(STREAM_REMOVED, stream);
        }
    };
}
;
