import {EventEmitter2} from 'eventemitter2';
import queryString from 'query-string';
import {pickBy} from 'lodash-es';
import {bindMethods} from '../../utils';
import Logs from '../../Logs';
import {WapiClient, WapiState} from '@quobis/wapi-client';

const log = Logs.instance.getLogger('SippoJS/WacStack/WacRequest');

export class WacRequest {
	constructor() {
		/** @type {EventEmitter} */
		this.emitter = new EventEmitter2({
			wildcard: true,
			delimiter: '/',
		});

		this.wapi = new WapiClient();

		bindMethods(this, ['onWapiEvent']);

		this._urls = {};
	}

	init() {
		this.wapi.emitter.on('event', this.onWapiEvent);
	}

	uninit() {
		this.wapi.emitter.off('event', this.onWapiEvent);
	}

	/** @private */
	onWapiEvent(msg) {
		let eventName = msg.path.substring(1);
		this.emitter.emit(eventName, {
			method: msg.method,
			path: msg.path,
			body: msg.body,
		});
	}

	/**
	 * Assigns the well-known URLs to this WacRequest instance
	 *
	 * @param {Object} urls The different transports' URLs exposed by sippo-server
	 * @param {string} urls.urn:quobis:sippo:websocket The URL for the WAPI v3 endpoint
     */
	set urls(urls) {
		this._urls = urls;
	}

	/**
	 * Configures the token to use in the wapi client
	 * @param {string} token
	 */
	set token(token) {
		this.wapi.configure(this._urls['urn:quobis:sippo:websocket'], token);
	}

	async request(options = {}) {
		if (this.wapi.getState() !== WapiState.CONNECTED) {
			log.debug('Dropping request while disconnected');
			return;
		}
		const method = options.method || 'GET';
		const qs = queryString.stringify(options.qs);
		const url = options.url + (qs ? '?' + qs : '');
		const body = Array.isArray(options.body)
			? options.body
			: pickBy(options.body, v => typeof v !== 'undefined');
		log.debug(`Request ${method} ${url}`, body);
		const res = await this.wapi.send(method, url, body);
		if (res.status >= 300) {
			log.error('Unexpected response', options, res);
			throw new Error(res.status);
		}
		log.debug(`Response ${method} ${url}`, res.status, res.body);
		return res.body;
	}

	get(url, qs = {}) {
		return this.request({
			method: 'GET',
			url: url,
			qs: qs,
		});
	}

	post(url, qs = {}, body = {}) {
		return this.request({
			method: 'POST',
			url: url,
			qs: qs,
			body: body,
		});
	}

	put(url, qs = {}, body = {}) {
		return this.request({
			method: 'PUT',
			url: url,
			qs: qs,
			body: body,
		});
	}

	delete(url, qs = {}) {
		return this.request({
			method: 'DELETE',
			url: url,
			qs: qs,
		});
	}
}
