import {EventEmitter2} from 'eventemitter2';
import {SessionsThread} from './SessionsThread';

export class Sessions {

	constructor(wacRequest, deps = {}) {
		const scheduler = {
			clearInterval: deps.clearInterval ? deps.clearInterval : handler => clearInterval(handler),
			setInterval: deps.setInterval ? deps.setInterval : (fn, timeout) => setInterval(fn, timeout),
		};
		this.pathname = '/sessions';
		this.wacRequest = wacRequest;

		this.emitter = new EventEmitter2();
		this.emitter.setMaxListeners(0);

		this.getSessions = this.getSessions.bind(this);
		this.retain = this.retain.bind(this);
		this.release = this.release.bind(this);
		this._thread = new SessionsThread(
			this.update.bind(this),
			this.onThreadCompleted.bind(this),
			this.onThreadError.bind(this),
			scheduler,
		);
	}

	getSessions(query) {
		return this.wacRequest.get(this.pathname, query);
	}

	update(id, credential) {
		const body = credential ? {
			expires: credential.expires,
			registered: credential,
		} : {};
		const fullPath = `${this.pathname}/${id}`;
		return this.wacRequest.put(fullPath, {}, body);
	}

	destroy(id) {
		const fullPath = `${this.pathname}/${id}`;
		return this.wacRequest.delete(fullPath);
	}

	retain(session) {
		return this._thread.retain(session, 'expires');
	}

	release() {
		if (!this.retained) {
			return;
		}
		let session = this.retained;
		this._thread.release();
		this.destroy(session.id);
	}

	get retained() {
		return this._thread.retained;
	}

	async suspend() {
		this._thread.release();
		await this.wacRequest.wapi.suspend();
	}

	async resume() {
		await this.wacRequest.wapi.resume();
		this._thread.retain(this.retained);
	}

	onThreadError() {
		this.release();
		this.emitter.emit('disconnected');
	}

	onThreadCompleted() {
		this.emitter.emit('updated');
	}
}
