import {Thread} from '../Thread';

export class CredentialsThread extends Thread {

	_thread() {
		return this.update(this._thread.retained.id, this._thread.retained.lease).then((credential) => {
			this._thread.retained.lease = credential.lease;
			return credential.lease;
		}).catch((error) => {
			this.onThreadError(error);
		});
	}
}
