import {EventEmitter2} from 'eventemitter2';

import {bindMethods} from '../../utils';

const PATH = '/conferences';

/** @protected */
export class ConferenceLogService {
	constructor(wacRequest) {
		this.wacRequest = wacRequest;
		bindMethods(this, ['onEvent']);
		this.emitter = new EventEmitter2({wildcard: true});
		this.bindEventHandlers();
	}

	/** @private */
	bindEventHandlers() {
		this.wacRequest.emitter.on('conferences', this.onEvent);
	}

	/** @private */
	onEvent(event) {
		this.emitter.emit('create', event.body);
	}

	fetch(limit, lastEntryStartTimestamp) {
		return this.wacRequest.get(PATH, {
			limit,
			createdAt: lastEntryStartTimestamp,
		});
	}
}
