const apply = th => args => fun => fun.apply(th, args);

export default class EventClass {
	constructor() {
		this._callbacks = {};
	}

	on(evtNames, callback) {
		evtNames.split(' ').map((evtName) => {
			if (!this._callbacks[evtName]) {
				this._callbacks[evtName] = [];
			}
			if (!this._callbacks[evtName].includes(callback)) {
				this._callbacks[evtName].push(callback);
			}
		});
		return this;
	}

	off(evtNames, callback) {
		if (callback) {
			evtNames.split(' ').forEach((evtName) => {
				if (this._callbacks[evtName]) {
					this._callbacks[evtName] = this._callbacks[evtName].filter(cb => cb !== callback);
				}
			});
		} else if (evtNames) {
			evtNames.split(' ').forEach((evtName) => {
				this._callbacks[evtName] = [];
			});
		} else {
			this._callbacks = {};
		}
		return this;
	}

	once(evtNames, callback) {
		var callbackMod = (...args) => {
			callback.apply(this, args);
			this.off(evtNames, callbackMod);
		};
		this.on(evtNames, callbackMod);
		return this;
	}

	trigger(evtName, ...data) {
		if (this._callbacks[evtName] != null) {
			this._callbacks[evtName].forEach(apply(this)(data));
		}
		return this;
	}
}
