const RoomInterface = room => ({
    getPublishers() {
        return room.getPublishers();
    },
    getParticipants() {
        return room.getParticipants();
    },
    getSubscribers() {
        return room.getSubscribers();
    },
    init() {
        return room.init();
    },
    uninit() {
        return room.uninit();
    },
    update(mediatypes = {}) {
        return room.update(mediatypes);
    },
    get media() {
        return room.media;
    },
    get mediatypes() {
        return room.mediatypes;
    },
    get ice() {
        return room.ice;
    },
    get emitter() {
        return room.emitter;
    },
    get api() {
        return room.api;
    }
});
export const createRoomInterface = room => RoomInterface(room);
