const create = (data) => {
	let {id, name = '', source = '', favorite = false} = data;
	return {
		getId: () => id,
		getName: () => name,
		getSource: () => source,
		isFavorite: () => favorite,
		setName: (value) => {
			name = value;
		},
		setFavorite: (value) => {
			favorite = value;
		},
		setSource: (value) => {
			source = value;
		},
		setId: (value) => {
			id = value;
		},
	};
};

/** @private */
export const BaseContact = {
	create,
};
