import {Enum} from '../utils/Enum';

/**
 * Enum containing the possible status of a file upload. Possible values:
 * - LOADING
 * - SENDING
 * - COMPLETED
 * - ERROR
 * - ABORTED
 * @type {Enum}
 */
export const FileUploadStatus = new Enum([
	'LOADING',
	'SENDING',
	'COMPLETED',
	'ERROR',
	'ABORTED',
]);
