import { HoldableConferenceMediaList, HoldableRoom, HoldState } from './hold';
import { BaseJanusFeedEventHandler, HoldableJanusFeedEventHandler } from './janus-feeds';
import { Room } from './Room';
export function JanusHoldableRoom(args) {
    const holdState = HoldState();
    const conferenceMediaList = HoldableConferenceMediaList(holdState.heldParticipants);
    const baseJanusFeedHandler = BaseJanusFeedEventHandler();
    const janusFeedHandler = HoldableJanusFeedEventHandler(holdState, baseJanusFeedHandler);
    const room = new Room(Object.assign({}, args, {
        deps: Object.assign({}, args.deps, {
            conferenceMediaList,
            janusFeedHandler,
        }),
    }));
    const holdeableRoom = HoldableRoom(room, holdState);
    return holdeableRoom;
}
