import {EventEmitter2} from 'eventemitter2';
import {bindMethods} from '../../utils';
import {union} from 'lodash-es';

const PATH = '/capabilities';

/** @private */
export class CapabilitiesService {
	constructor(wacRequest) {
		this.wacRequest = wacRequest;
		this.emitter = new EventEmitter2();
		bindMethods(this, ['onEvent']);
		this.bindEventHandlers();
	}

	/** @private */
	bindEventHandlers() {
		this.wacRequest.emitter.on(PATH, this.onEvent);
	}

	/** @private */
	async onEvent(event) {
		if (event.method === 'put') {
			await this.process(event.body);
			this.emitter.emit('change');
		}
	}

	/** @private */
	async populate() {
		const res = await this.wacRequest.get(PATH);
		this.process(res);
	}

	/** @private */
	async process(data) {
		const domain = data.domain;
		const user = data.user.filter(item => item.startsWith('+')).map(item => item.slice(1));
		this.capabilities = union(domain, user);
		this.forbiddenCapabilities = data.user.filter(item => item.startsWith('-')).map(item => item.slice(1));
	}

	async getCapabilities() {
		if (!this.capabilities) {
			await this.populate();
		}
		return this.capabilities;
	}

	async getForbiddenCapabilities() {
		if (!this.forbiddenCapabilities) {
			await this.populate();
		}
		return this.forbiddenCapabilities;
	}
}
