import {CredentialsThread} from './CredentialsThread';

export class Credentials {

	constructor(wacRequest) {
		this.wacRequest = wacRequest;

		this._retain = {};
		this._thread = new CredentialsThread(this);
		this.pathname = '/credentials';

		this.getCredentials = this.getCredentials.bind(this);
	}

	getCredentials(query) {
		return this.wacRequest.get(this.pathname, query);
	}

	update(id, lease) {
		const body = {
			lease,
		};
		const fullPath = `${this.pathname}/${id}`;
		return this.wacRequest.put(fullPath, {}, body);
	}

	retain(credential) {
		return this._thread.retain(credential, 'lease');
	}

	release() {
		return this._thread.release();
	}

	get retained() {
		return this._thread.retained;
	}

	onThreadError() {
		this.release();
	}

}
