import { EventEmitter2 as EventEmitter } from 'eventemitter2';
export const HOLD_MESSAGE_TYPES = {
    HOLD: 'hold',
    UNHOLD: 'unhold'
};
export function HoldAPI(client) {
    const emitter = new EventEmitter();
    client.emitter.on(HOLD_MESSAGE_TYPES.HOLD, (eventData) => emitter.emit(HOLD_MESSAGE_TYPES.HOLD, eventData));
    client.emitter.on(HOLD_MESSAGE_TYPES.UNHOLD, (eventData) => emitter.emit(HOLD_MESSAGE_TYPES.UNHOLD, eventData));
    const sendWithEmptyPayload = (messageType) => () => client.send(messageType, {});
    const hold = sendWithEmptyPayload(HOLD_MESSAGE_TYPES.HOLD);
    const unhold = sendWithEmptyPayload(HOLD_MESSAGE_TYPES.UNHOLD);
    return {
        hold,
        unhold,
        emitter,
    };
}
