import URL from 'lite-url';
import * as socketIOClient from 'socket.io-client';
/**
 * Try to create a socket.io connection with given address.
 * @throws {Error} when an error during connection is detected.
 * @param {string} address socket.io server address.
 * @return {Promise<io.Socket>} resolved with connected socket.io client.
 */
export const create = (address, { socketIO = socketIOClient } = {}) => {
    return new Promise((resolve, reject) => {
        const url = new URL(address);
        // @todo autoaprobar los certificados por parametro
        const socket = socketIO.connect(url.origin, {
            path: url.pathname,
            'force new connection': true,
            rejectUnauthorized: process.env.NODE_TLS_REJECT_UNAUTHORIZED !== '0'
        });
        let onSuccess;
        let onError;
        const done = () => {
            socket.off('connect', onSuccess);
            socket.off('disconnect', onError);
            socket.off('connect_failed', onError);
        };
        onSuccess = () => {
            done();
            resolve(socket);
        };
        onError = () => {
            done();
            reject(new Error('got error while connecting'));
        };
        socket.on('connect', onSuccess);
        socket.on('disconnect', onError);
        socket.on('connect_failed', onError);
    });
};
