import ReconnectingWebSocket from 'reconnecting-websocket';
import { Subject } from 'rxjs';
export class WebSocketConnection {
    constructor(socket, messages) {
        this.socket = socket;
        this.messages = messages;
    }
    static fromSocket(socket, messages) {
        return new WebSocketConnection(socket, messages);
    }
    static async connect(address, socketFactory) {
        const createSocket = socketFactory ||
            ((addr) => new ReconnectingWebSocket(addr, WebSocketConnection.PROTOCOL));
        const socket = createSocket(address);
        return new Promise((res, rej) => {
            const messages = new Subject();
            socket.onopen = () => res(WebSocketConnection.fromSocket(socket, messages));
            socket.onerror = () => rej(Error('Unable to connect'));
            socket.onmessage = (e) => {
                messages.next(JSON.parse(e.data));
            };
        });
    }
    send(payload) {
        this.socket.send(JSON.stringify(payload));
    }
    getMessages() {
        return this.messages.asObservable();
    }
    disconnect() {
        this.socket.close();
    }
}
WebSocketConnection.PROTOCOL = 'janus-protocol';
