import {EventEmitter2} from 'eventemitter2';
import {bindMethods} from '../../utils';
import Logs from '../../Logs';

const log = Logs.instance.getLogger('SippoJS/WacStack/CallService');
const PATH = '/calls/';

export class CallService {
	constructor(wacRequest) {
		this.wacRequest = wacRequest;
		this.emitter = new EventEmitter2();
		this.emitter.setMaxListeners(0);
		bindMethods(this, ['onEvent', 'onInstanceEvent']);
		this.bindEventHandlers();
	}

	/** @private */
	bindEventHandlers() {
		this.wacRequest.emitter.on('calls', this.onEvent);
		this.wacRequest.emitter.on('calls/*', this.onInstanceEvent);
	}

	/** @private */
	onEvent(event) {
		switch (event.method) {
			case 'post':
				this.emitter.emit('created', event.body);
				break;
			default:
				log.warn('Unhandled WAC event: ', event);
		}
	}

	/** @private */
	onInstanceEvent(event) {
		switch (event.method) {
			case 'put':
				this.emitter.emit('updated', event.body);
				break;
			default:
				log.warn('Unhandled WAC event: ', event);
		}
	}

	get({after, before, limit, sortBy, caller, callee, target, queueposition} = {}) {
		return this.wacRequest.get(PATH, {
			after, before, limit, sortBy, caller, callee, target, queueposition,
		});
	}

	create({caller, callee, context, anonymousSessionId} = {}) {
		return this.wacRequest.post(PATH, {}, {
			caller, callee, context, anonymousSessionId,
		});
	}

	update(id, {caller, callee, context, anonymousSessionId, state} = {}) {
		return this.wacRequest.put(PATH + id, {}, {
			caller, callee, context, anonymousSessionId, state,
		});
	}

}
