import { JANUS_EVENTS } from './JanusEvents';
export function HoldableJanusFeedEventHandler(holdState, baseFeed) {
    const onJanusFeed = (room, feedEvent) => {
        processFeed(feedEvent);
        return holdState.isHolding()
            ? Promise.resolve()
            : baseFeed.onJanusFeed(room, feedEvent);
    };
    const processFeed = (feedEvent) => {
        const { event } = feedEvent;
        switch (event) {
            case JANUS_EVENTS.JANUS_FEED_NEW:
                holdState.savedSubscriptions.saveSubscription(feedEvent.feed);
                break;
            case JANUS_EVENTS.JANUS_FEED_GONE:
                holdState.savedSubscriptions.removeSubscription(feedEvent.feed);
                break;
        }
    };
    return {
        onJanusFeed,
    };
}
